import { Services } from "../Global";


export namespace Http {

    export function getStringAsync(url: string): Promise<string> {
        return Services.httpClient.requestAsync<string>({
            url: url,
            method: "GET",
        });
    }

    /****************************************/

    export function postStringAsync(url: string, data: any): Promise<string> {
        return Services.httpClient.requestAsync<string>({
            url: url,
            method: "POST",
            data: data,
        });
    }

    /****************************************/

    export function getJsonAsync<T>(url: string): Promise<T> {

        return Services.httpClient.requestAsync<T>({
            url: url,
            method: "GET",
            responseType: "application/json",
        });
    }

    /****************************************/

    export function postJsonAsync<T>(url: string, data: any): Promise<T> {

        return Services.httpClient.requestAsync<T>({
            url: url,
            method: "POST",
            responseType: "application/json",
            data: data,
        });
    }

    /****************************************/

    export function postBinaryAsync<T>(url: string, data: Blob | File | ArrayBuffer, onProgress?: (ev: ProgressEvent) => void): Promise<T> {

        return Services.httpClient.requestAsync<T>({
            url: url,
            method: "POST",
            responseType: "application/json",
            dataType: "application/octet-stream",
            data: data,
            onProgress: onProgress
        });
    }
}