import { IDictionary } from "../Abstraction/IDictionary";

const idMap: IDictionary<number> = {};

export function generateId(base: string): string {

    if (!base)
        base = "item";

    if (idMap[base] === undefined)
        idMap[base] = 0;

    idMap[base]++;

    if (idMap[base] == 1)
        return base;

    return base + idMap[base];
}