import { OperationType } from "../../Core/Abstraction/IOperation";
import { linq } from "../../Core/Linq";
import { CatalogTemplate } from "../../Templating/Abstraction/ITemplateProvider";
import { Bindable } from "../../Templating/Properties";
import { ActionPriority } from "../Abstraction/IAction";
import { IItemAction } from "../Abstraction/IItemAction";
import { ActionView } from "./ActionView";
import { ContextMenu } from "./ContextMenu";
import { IPanelConfig, Panel } from "./Panel";
import { View } from "./View";
import "Templates/ItemView.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface IItemViewConfig<TItem> extends IPanelConfig {

    item?: Bindable<TItem>;

    actions?: IItemAction<TItem>[];

    itemTemplate?: CatalogTemplate<ItemView<TItem>>;

    canOpen?: Bindable<boolean>;

    open?(): void;
}

/****************************************/

export class ItemView<TItem> extends Panel {

    protected _otherAction: ActionView;

    constructor(config: IItemViewConfig<TItem>) {
        super({ template: "ItemView", ...config });

        this.bindConfig("item", config);

        this.bindConfig("canOpen", config);

        if (config) {

            if (config.open)
                this.openWork = config.open;

            if (config.actions)
                config.actions.forEach(a => this.addAction(a));

            if (config.itemTemplate)
                this.content.add(new View({ template: t => t.template(config.itemTemplate, this) }));
        }

        this.computed("hasActions",
            m => linq(m.contextActions).any(a => a.visible));

        this.create();
    }

    /****************************************/

    addAction(action: IItemAction<TItem>) {

        const view = ActionView.fromItemAction(action, () => this.item);

        if (action.priority == ActionPriority.Primary) {
            if (this.mainActions.length >= 3) {

                if (!this._otherAction) {

                    this._otherAction = ActionView.fromAction({
                        name: "other",
                        icon: "fas fa-ellipsis-h",
                        operation: OperationType.Local,
                        executeAsync: () => Promise.resolve()
                    });
                    this._otherAction.subActions.add(this.mainActions.pop());
                    this.mainActions.push(this._otherAction);
                }

                this._otherAction.subActions.add(view);
            }
            else
                this.mainActions.push(view);
        }
        else {
            this.contextActions.push(view);
            this.prop("hasActions").notifyChanged();
        }
    }

    /****************************************/

    create() {

    }

    /****************************************/

    open() {
        if (this.canOpen)
            this.openWork();
    }

    /****************************************/

    protected openWork() {

    }

    /****************************************/

    showMenu() {

        let menu = new ContextMenu();
        this.contextActions.forEach(a => menu.actions.add(a));
        menu.showAsync(window.event.srcElement as HTMLElement);
    }

    /****************************************/

    item: TItem = null;

    contextActions: ActionView[] = [];

    mainActions: ActionView[] = [];

    status: string = null;

    canOpen: boolean = false;

    hasActions: boolean = null;
}


registerType(ItemView, "ItemView");