import { registerType } from "../../Core/Utils/ObjectUtils";
import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { Bindable } from "../../Templating/Properties";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";
import { BaseEditor, IBaseEditorConfig } from "./BaseEditor";

export type TextEditorTrackMode = "onchange" | "onlostfocus";

export interface IBaseTextEditorConfig<TValue> extends IBaseEditorConfig<TValue> {

    trackMode?: Bindable<TextEditorTrackMode>;
}

/********************************************/

export abstract class BaseTextEditor<TValue> extends BaseEditor<TValue, string> implements IHTMLAttach {

    protected _element: HTMLElement;

    constructor(config?: IBaseTextEditorConfig<TValue>) {

        super(config);

        this.bindConfig("trackMode", config);

        this.prop("hasFocus").subscribe(a => this.onFocusChanged(a));
    }

    /********************************************/

    attach(element: HTMLElement) {
        this._element = element;
    }

    /********************************************/

    async activateAsync() {

        if (this._element.nodeName == "INPUT" || this._element.nodeName == "TEXTAREA") {
            while (true) {

                const input = <HTMLTextAreaElement>this._element;
                input.selectionStart = 0;
                input.selectionEnd = input.value?.length;
                this._element.focus();

                if (this.hasFocus)
                    break;

                await delayAsync(100);
            }
        }
    }

    /********************************************/

    protected onFocusChanged(value: boolean) {

        if (!value && this.trackMode == "onlostfocus")
            this.notifyEditValueChangedAsync();
    }

    /********************************************/

    protected notifyEditValueChangedAsync() {

        if (this.hasFocus && this.trackMode == "onlostfocus") {

            this._needValidation = true;

            this.isDirty = true;

            return Promise.resolve();
        }
        return super.notifyEditValueChangedAsync();
    }

    /********************************************/

    trackMode: TextEditorTrackMode = "onlostfocus";
}

registerType(BaseTextEditor, "BaseTextEditor");