import { LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { IHasLabel } from "../Abstraction/IHasLabel";
import { BaseEditor, IBaseEditorConfig } from "./BaseEditor";

export interface IBooleanEditorConfig extends IBaseEditorConfig<boolean> {

    trueLabel?: Bindable<LocalString>;

    falseLabel?: Bindable<LocalString>;

    label?: Bindable<LocalString>;
}

/********************************************/

export class BooleanEditor extends BaseEditor<boolean> implements IHasLabel {

    constructor(config?: IBooleanEditorConfig) {

        super({ template: "CheckBox", ...config });

        this.bindConfigString("trueLabel", config);

        this.bindConfigString("falseLabel", config);

        this.bindConfigString("label", config, StringUsage.Action);

        this.createEditValueProp();
    }

    /********************************************/

    toggle() {

        if (window.event)
            window.event.stopPropagation();
        this.editValue = !this.editValue;
    }

    /********************************************/

    trueLabel: string = null;

    falseLabel: string = null;

    label: string = null;
}

registerType(BooleanEditor, "BooleanEditor");