import { INamedContent } from "../../Core/Abstraction/INamedContent";
import { OperationType } from "../../Core/Abstraction/IOperation";
import { Format } from "../../Core/Helpers/Format";
import { linq } from "../../Core/Linq";
import { DynamicString, LocalString, StringUsage, WordNumber } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { ActionPriority } from "../Abstraction/IAction";
import { IAsyncEditor } from "../Abstraction/IAsyncEditor";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { IView } from "../Abstraction/IView";
import { ActionView } from "../Components/ActionView";
import { BottomSheet } from "../Components/BottomSheet";
import { ItemsView } from "../Components/ItemsView";
import { RemovableItemView } from "../Components/RemovableItemView";
import { SelectableItemView } from "../Components/SelectableItemView";
import { TextView } from "../Components/TextView";
import { ActivityEditor } from "../Editors/ActivityEditor";
import { isAsyncEditor } from "../Helpers/TypeCheck";
import { EditableItemsSource } from "../Items/EditableItemsSource";
import { ISearchActivityConfig, SearchActivity } from "./SearchActivity";

export interface ISelectMultipleItemsActivityConfig<TItem, TValue, TFilter, TEdit> extends ISearchActivityConfig<TItem, SelectableItemView<TItem, IView>> {

    itemsSource?: EditableItemsSource<TItem, TValue, TFilter, TEdit>;

    filters?: INamedContent<TFilter>[];

    createItemEditor?: () => IItemEditor<TItem, TEdit> | IAsyncEditor<TItem, TEdit>;

    createItemContentView?: (item: TItem) => IView;

    createItemListView?: (item: TItem) => IView;

    canAdd?: boolean;

    addLabel?: LocalString;

    emptyLabel?: LocalString;

    selectedItems?: TItem[];

    showEmptyItem?: boolean;
}

/****************************************/

export class SelectMultipleItemsActivity<TItem, TValue, TFilter, TEdit = TItem> extends SearchActivity<TItem, SelectableItemView<TItem, IView>, TItem[]>   {

    protected _bottomSheet: BottomSheet;
    protected _emptyItem: SelectableItemView<TItem, IView>;
    protected _lastItems: TItem[];


    constructor(config?: ISelectMultipleItemsActivityConfig<TItem, TValue, TFilter, TEdit>) {

        super(config);

        this.bindConfig("canAdd", config);

        this.bindConfigString("emptyLabel", config, StringUsage.Title);

        this.bindConfig("showEmptyItem", config);

        this.bindConfig("itemsSource", config);

        this.bindConfigString("addLabel", config);

        if (config) {

            if (config.createItemView)
                this.createItemContentView = config.createItemContentView;

            if (config.createItemListView)
                this.createItemListView = config.createItemListView;

            if (config.createItemEditor)
                this.createItemEditor = config.createItemEditor;

            if (config.filters)
                this.filters.addRange(config.filters);

            if (config.selectedItems)
                this.setSelectedItems(config.selectedItems);

            if (config.searchMode == "client") {
                this.searchWorkAsync = async query => {

                   if (!this._lastItems)
                        this._lastItems = await this.itemsSource.getItemsAsync(this.getFilter(undefined));

                    if (query) {
                        query = query.toLowerCase();
                        return linq(this._listView.items).where(a => {
                            const text = this.itemsSource.getItemText(a).toLowerCase();
                            return text && text.indexOf(query) != -1;
                        }).toArray();
                    }
                    else
                        return this._lastItems;
                };
            }
        }

        if (!config?.tooltip)
            this.tooltip = DynamicString.get("select-item")({ params: [this.itemsSource.displayName], number: WordNumber.Plural, usage: StringUsage.Tooltip });

        if (!config?.addLabel)
            this.addLabel = Format.action("new-item", this.itemsSource.displayName);
    }

    /****************************************/

    setSelectedItems(items: TItem[]) {

        this.selectedItems.clear();

        if (items) {
            items.forEach(a =>
                this.addSelection(a));
        }
    }

    /****************************************/

    protected async createAsync() {

        await super.createAsync();

        this._actionBar.addAction({
            name: "confirm",
            icon: "fas fa-check",
            displayName: "confirm",
            executeAsync: () => this.confirmAsync(),
            priority: ActionPriority.Primary
        });

        this._bottomSheet = new BottomSheet({
            headHeight: 50,
            content: new ItemsView({
                styles: ["padding", "horizontal-wrap"],
                content: this.selectedItems
            })
        });

        this.view.addView(this._bottomSheet);

        this.updateView();
    }

    /****************************************/

    async confirmAsync() {

        await this.closeAsync(linq(this.selectedItems).select(a => a.item).toArray());
    }

    /****************************************/

    protected configureListView() {

        const header: IView[] = [];

        if (this.canAdd)
            header.push(ActionView.fromAction({
                name: "add-item",
                icon: "fas fa-plus",
                operation: OperationType.Local,
                executeAsync: () => this.addItemAsync(),
                displayName: this.addLabel,
            }));

        if (this.showEmptyItem) {

            this._emptyItem = new SelectableItemView({
                content: new TextView({ content: this.emptyLabel }),
                item: null,
            });

            this._emptyItem.toggle = () =>
                this.closeAsync([]);

            header.push(this._emptyItem);
        }

        const result = <any>{
            header: header,
            selectionMode: "multiple"
        };

        if (this.pageSize > 0)
            result.itemsSource = this.itemsSource;

        return result;
    }

    /****************************************/

    protected searchWorkAsync(query: string) {

        return this.itemsSource.getItemsAsync(this.getFilter(query));
    }

    /****************************************/

    protected getFilter(query: string, offset?: number, pageSize?: number) {

        let filter = this.itemsSource.getFilter(query, offset, pageSize);
        if (this.filters.count == 1)
            filter = { ...filter, ...this.filters.get(0).content };
        return filter;
    }

    /****************************************/

    protected createItemListView(item: TItem): IView {
        return new TextView({ content: this.itemsSource.getItemText(item) });
    }

    /****************************************/

    protected createItemContentView(item: TItem): IView {
        return new TextView({ content: this.itemsSource.getItemText(item) });
    }

    /****************************************/

    protected createItemEditor(): IItemEditor<TItem, TEdit> | IAsyncEditor<TItem, TEdit> {

        throw "Not Supported";
    }

    /****************************************/

    protected createItemView(item: TItem) {

        const view = new SelectableItemView({
            content: this.createItemListView(item),
            item: item
        });

        view.prop("isSelected").subscribe(value => this.updateSelection(view, item, value));

        const exItem = this.findItem(item);

        if (exItem != null)
            view.isSelected = true;

        return view;
    }

    /****************************************/

    protected updateSelection(selView: SelectableItemView<TItem, IView>, item: TItem, isSelected: boolean) {

        if (!isSelected)
            this.removeSelection(item);
        else
            this.addSelection(item, selView);

        this.updateView();
    }

    /****************************************/

    protected updateView() {

        if (this._bottomSheet) {
            if (this.selectedItems.count > 0)
                this._bottomSheet.open();
            else
                this._bottomSheet.close();
        }
    }

    /****************************************/

    removeSelection(item: TItem) {
        const exItem = this.findItem(item);
        if (exItem)
            this.selectedItems.remove(exItem);
    }

    /****************************************/

    addSelection(item: TItem, selView?: SelectableItemView<TItem, IView>) {

        const exItem = this.findItem(item);

        if (exItem)
            return;

        const itemView = new RemovableItemView({
            content: this.createItemContentView(item),
            removeAsync: () => {

                this.selectedItems.remove(itemView);

                const selItem = this.findSelectable(item);
                if (selItem)
                    selItem.isSelected = false;

                return Promise.resolve();
            },
            item: item
        });

        this.selectedItems.add(itemView);

        return itemView;
    }

    /****************************************/

    protected findItem(item: TItem) {

        return linq(this.selectedItems).first(a => this.itemsSource.itemComparer(a.item, item));
    }

    /****************************************/

    protected findSelectable(item: TItem) {

        return linq(this._listView.content).first(a => this.itemsSource.itemComparer(a.item, item));
    }

    /****************************************/

    async addItemAsync() {

        const editor = this.createItemEditor();

        let editValue: TEdit;

        if (isAsyncEditor(editor))

            editValue = await editor.editAsync(this.itemsSource.newItem());

        else {
            const activity = new ActivityEditor<TItem, TEdit>({
                editor: editor,
                title: this.addLabel
            });

            editValue = await activity.editAsync(this.itemsSource.newItem());
        }

        if (!editValue)
            return;

        const newItem = await this.itemsSource.addItemAsync(editValue);
        if (!newItem)
            return;

        const itemView = this.createItemView(newItem);

        itemView.isSelected = true;

        this._listView.content.insert(0, itemView);
    }

    /****************************************/

    canAdd: boolean = false;

    showEmptyItem: boolean = false;

    emptyLabel: string = "[None]";

    itemsSource: EditableItemsSource<TItem, TValue, TFilter, TEdit> = null;

    addLabel: string = "Add New";

    filters:  IObservableList< INamedContent<TFilter>> = observableListOf();

    readonly selectedItems: IObservableList<RemovableItemView<TItem>> = observableListOf();
}


registerType(SelectMultipleItemsActivity, "SelectMultipleItemsActivity");