import { IActivable } from "../Abstraction/IActivable";
import { IAsyncEditor } from "../Abstraction/IAsyncEditor";
import { IAsyncLoad } from "../Abstraction/IAsyncLoad";
import { IEditable } from "../Abstraction/IEditable";
import { IHTMLContainer } from "../Abstraction/IHTMLContainer";
import { ISelectable } from "../Abstraction/ISelectable";
import { ISingleItemSelector } from "../Abstraction/ISingleItemSelector";
import { IValidable } from "../Abstraction/IValidator";
import { IView } from "../Abstraction/IView";

export function isAsyncEditor(value: any): value is IAsyncEditor<any, any> {

    return value && typeof value == "object" && "editAsync" in value && typeof value["editAsync"] == "function";
}

/****************************************/

export function isView(value: any): value is IView {

    return value && typeof value == "object" && "parentView" in value && "template" in value;
}

/****************************************/

export function isActivable(value: any): value is IActivable {

    return value && typeof value == "object" && typeof value["activateAsync"] == "function" && value["activateAsync"].length == 0;
}

/****************************************/

export function isSelectable(value: any): value is ISelectable {

    return value && typeof value == "object" && "isSelected" in value && "select" in value && typeof value["select"] == "function";
}

/****************************************/

export function isSingleItemSelector(value: any): value is ISingleItemSelector<any> {

    return value && typeof value == "object" && "selectedItem" in value;
}

/****************************************/

export function isAsyncLoad(value: any): value is IAsyncLoad {

    return value && typeof value == "object" && typeof value["loadAsync"] == "function" && value["loadAsync"].length == 0;
}

/****************************************/

export function isValidable(value: any): value is IValidable {

    return value && typeof value == "object" && typeof value["validateAsync"] == "function" && value["validateAsync"].length < 2;
}

/****************************************/

export function isHTMLContainer(value: any): value is IHTMLContainer {

    return value && typeof value == "object" && "nodes" in value;
}

/****************************************/

export function isEditable(value: any): value is IEditable<any> {

    return value && typeof value == "object" && "createEditor" in value && typeof value.createEditor == "function";
}