import { getOrCreateProperty, getTypeName, hasProp } from "../Core/Utils/ObjectUtils";
import { IBindable } from "./Abstraction/IBindable";
import { IObservableProperty } from "./Abstraction/IObservableProperty";
import { IProperty } from "./Abstraction/IProperty";
import { ComputedProperty } from "./ComputedProperty";
import { ObservableProperty } from "./ObservableProperty";

export type Bindable<TValue> = IProperty<TValue> | TValue;

/****************************************/

export interface IValueConverter<TSrc, TDest> {

    convertTo(src: TSrc): TDest;

    convertFrom(dest: TDest): TSrc;
}

/****************************************/

export function newProp<TValue>(name: string, value?: TValue, onChanged?: (value: TValue, oldValue: TValue) => void) : IObservableProperty < TValue > {

    const result = new ObservableProperty<TValue>({
        value: value
    }, name);

    if (onChanged)
        result.subscribe(onChanged);
    return result;
}

/****************************************/

export namespace Properties {

    export function converter<TObj extends object, TKey extends keyof TObj, TValue extends TObj[TKey], TProp>(obj: TObj, propName: TKey, convert: (value: TValue) => TProp, convertBack?: (value: TProp) => TValue): IProperty<TProp> {

        let conValue;

        return {

            get: () => {
                if (conValue == undefined)
                    conValue = convert(obj[propName] as TValue);
                return conValue;
            },

            set: value => {
                conValue = value;
                if (convertBack)
                    obj[propName] = convertBack(value);
            },

            name: <string>propName
        };
    }

    /****************************************/

    export function create<TObj extends IBindable, TKey extends (keyof TObj) & string, TValue extends TObj[TKey]>(obj: TObj, propName: TKey, property?: IObservableProperty<TValue>): IObservableProperty<TValue> {

        let desc = Object.getOwnPropertyDescriptor(obj, propName);
        if (!desc) {
            console.warn("'", propName, "' not defined in ", getTypeName(obj));
            desc = {};
        }

        if (!property)
            property = new ObservableProperty(desc, <string>propName);

        const props = getOrCreateProperty<any>(obj, "@props", {});
        props[propName as any] = property;

        Object.defineProperty(obj, propName, {

            get: () => property.get(),

            set: (newValue) => property.set(newValue)
        });

        return property;
    }

    /****************************************/

    export function createAll<TObj>(obj: TObj): TObj {

        const props = Object.getOwnPropertyNames(obj);

        props.forEach(propName => {

            if (propName[0] == "_" || propName[0] == "$")
                return;

            const propDesc = Object.getOwnPropertyDescriptor(obj, propName);

            if (propDesc && !propDesc.writable)
                return;

            create(obj, <any>propName);
        });

        return obj;
    }

    /****************************************/

    export function get<TObj extends IBindable, TKey extends keyof TObj, TValue extends TObj[TKey]>(obj: TObj, propName: TKey): IProperty<TValue> {

        if (hasProp(obj, "@props")) {
            const prop = obj["@props"][propName];
            if (prop)
                return prop;
        }
        return undefined;
    }

    /****************************************/

    export function getOrCreate<TObj extends IBindable, TKey extends keyof TObj & string, TValue extends TObj[TKey]>(obj: TObj, propName: TKey, property?: IObservableProperty<TValue>): IObservableProperty<TValue> {

        const prop = get(obj, propName);
        if (prop)
            return <IObservableProperty<TValue>>prop;
        return create(obj, propName, property);
    }

    /****************************************/

    export function isProperty(obj: any): obj is IProperty<any> {

        return (obj && typeof obj == "object" && typeof obj["get"] == "function" && obj["get"].length == 0 && typeof obj["set"] == "function" && obj["set"].length == 1);
    }

    /****************************************/

    export function isObservableProperty(obj: any): obj is IObservableProperty<any> {
        //TODO improve this check
        return obj instanceof ObservableProperty || obj instanceof ComputedProperty;
    }

    /****************************************/

    export function bind<TDest, TSrc = TDest>(dest: IObservableProperty<TDest>, src: Bindable<TSrc>, converter: IValueConverter<TSrc, TDest>) {

        let value: TSrc;

        if (isProperty(src)) {

            if (isObservableProperty(src)) {
                src.subscribe(value => {
                    if (converter)
                        dest.set(converter.convertTo(value as TSrc));
                    else
                        dest.set(value);
                });
            }

            dest.subscribe(value => {
                if (converter)
                    (src as IProperty<TSrc>).set(converter.convertFrom(value as TDest));
                else
                    (src as IProperty<TSrc>).set(value as unknown as TSrc);
            });

            value = src.get();
        }
        else
            value = <TSrc>src;

        if (converter)
            dest.set(converter.convertTo(value));
        else
            dest.set(value as unknown as TDest);

        return undefined;
    }
}