import { OperationType } from "../../Core/Abstraction/IOperation";
import { Services } from "../../Core/Global";
import { forEachAsync } from "../../Core/Utils/ArrayUtils";
import { IItemsContainer, IItemsLoader } from "../Abstraction/IItemsLoader";

export class FullItemsLoader<TItem = any> implements IItemsLoader<TItem, any>  {

    async loadItemsAsync(container: IItemsContainer<TItem, any>, chunkSize: number): Promise<void> {

        container.status = "loading";
        const operation = Services.operation.begin({ message: "Loading items", type: OperationType.Local });
        try {

            const items = await container.itemsSource.getItemsAsync(container.filter);

            container.clear();

            if (items)
                await forEachAsync(items, chunkSize, a => container.addItem(a));

        }
        finally {
            operation.end();
            container.status = "loaded";
        }
    }

    static readonly instance = new FullItemsLoader();
}