import { LocalString } from "../Types/DynamicString";

export enum OperationType {
    Global,
    Local
}

/****************************************/

export interface IOperationProgress {

    message?: LocalString;

    totCount?: number;

    current?: number;
}

/****************************************/

export interface IOperation {

    end(): void;

    progress: IOperationProgress;

    parentOperation: IOperation;

    type: OperationType;

    //subOperations: WebApp.IList<IOperation>;

    addSubOperation(operation: IOperation);

    removeSubOperation(operation: IOperation);

    message: LocalString;
}