import { OperationType } from "../../Core/Abstraction/IOperation";
import { IAsyncTask } from "../../Core/Abstraction/ITask";
import { Format } from "../../Core/Helpers/Format";
import { LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable, Properties } from "../../Templating/Properties";
import { IAction } from "../Abstraction/IAction";
import { IItemAction } from "../Abstraction/IItemAction";
import { ContentView, IContentViewConfig } from "./ContentView";
import { ContextMenu } from "./ContextMenu";
import { IconTextView } from "./IconTextView";
import "Templates/ActionView.html";
import { App, Services } from "../../Core/Global";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface IActionViewConfig extends IContentViewConfig<any> {

    name?: string;

    operation?: OperationType;

    tooltip?: Bindable<LocalString>;

    subActions?: IActionViewConfig[];

    canAggregate?: AggregationMode;

    executeAsync?: IAsyncTask<any>;
}

/****************************************/

export enum AggregationMode {
    None,
    Parallel,
    Serial
}

/****************************************/

export class ActionView extends ContentView<any>  {

    constructor(config?: IActionViewConfig) {

        super({ template: "ActionLink", ...config });

        this.bindConfigString("tooltip", config, StringUsage.Tooltip);

        this.bindConfig("operation", config);

        this.bindConfig("canAggregate", config);

        if (config) {

            if (config.executeAsync)
                this.executeWorkAsync = config.executeAsync;

            if (config.subActions)
                config.subActions.forEach(sub =>
                    this.subActions.add(new ActionView(sub)));
        }
    }

    /****************************************/

    async executeAsync(): Promise<any> {

        if (window.event)
            window.event.stopPropagation();

        if (Services.analytics)
            Services.analytics.action(this.name);

        var op = Services.operation.begin({ message: "Executing " + this.name, type: this.operation });

        this.status = "executing";
        try {

            await this.executeWorkAsync();

            if (this.subActions.count > 0) {

                let menu = new ContextMenu();
                this.subActions.forEach(a => menu.actions.add(a));
                menu.showAsync(window.event.srcElement as HTMLElement);
            }
        }
        catch (ex) {
            App.handleError(this, ex);
            throw ex;
        }
        finally {
            this.status = "";
            op.end();
        }
    }

    /****************************************/

    protected executeWorkAsync(): Promise<any> {

        return Promise.resolve();
    }

    /****************************************/

    static fromAction(action: IAction, config?: IActionViewConfig): ActionView {
        const result = new ActionView({
            name: action.name,
            operation: action.operation,
            styles: action.styles,
            template: "ActionLink",
            content: new IconTextView({
                template: "IconTextViewInline",
                text: Format.action(action.displayName ?? action.name),
                icon: action.icon,
                badge: action.badge
            }),
            executeAsync: action.executeAsync,
            ...config
        });

        if (action.canExecute)
            Properties.converter(result, "visible", () => action.canExecute());

        return result;
    }

    /****************************************/

    static fromItemAction<TItem>(action: IItemAction<TItem>, getItem: () => TItem, config?: IActionViewConfig): ActionView {
        const result = new ActionView({
            name: action.name,
            operation: action.operation,
            styles: action.styles,
            content: new IconTextView({
                template: "IconTextViewInline",
                text: Format.action(action.displayName ?? action.name),
                icon: action.icon,
                badge: action.badge
            }),
            executeAsync: () => action.executeAsync(getItem()),
            ...config
        });

        if (action.canExecute)
            result.bind("visible", result.createComputed(() => action.canExecute(getItem())));

        return result;
    }

    /****************************************/

    static fromActionIcon(action: IAction, config?: IActionViewConfig): ActionView {
        const result = new ActionView({
            name: action.name,
            styles: action.styles,
            operation: action.operation,
            template: "ActionIcon",
            content: action.icon,
            tooltip: action.displayName ?? action.name,
            executeAsync: action.executeAsync,
            ...config
        });

        if (action.canExecute)
            result.bind("visible", result.createComputed(() => action.canExecute()));

        return result;
    }

    /****************************************/

    tooltip: string = null;

    operation: OperationType = OperationType.Global;

    canAggregate: AggregationMode = AggregationMode.None;

    readonly subActions: IObservableList<ActionView> = observableListOf();
}

registerType(ActionView, "ActionView");