import { Http } from "../../Core/Helpers/Http";
import { IViewComponentConfig, ViewComponent } from "./ViewComponent";
import "Templates/FileUploadView.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export type FileStatus = "empty" | "selected" | "uploading" | "uploaded";

export interface IFileUploadViewConfig extends IViewComponentConfig {

    onChanged?(): void;
}

/****************************************/
/* FileViewModel                        */
/****************************************/

export class FileUploadView extends ViewComponent {

    private _input: HTMLInputElement;

    constructor(config?: IFileUploadViewConfig) {

        super(config);

        this._input = document.createElement("input");
        this._input.type = "file";

        this._input.addEventListener("change", () => {

            this.update();

            if (config?.onChanged)
                config.onChanged();
        });

        this.update();
    }

    /****************************************/

    protected update() {

        let file = this._input.files[0];

        if (file) {
            this.text = file.name;
            this.status = "selected";
        }
        else {
            this.text = "Seleziona file";
            this.status = "empty";
        }

        this.isSelected = this._input.files[0] != null;
    }

    /****************************************/

    select() {
        this._input.click();
    }

    /****************************************/

    remove() {

    }

    /****************************************/

    async uploadAsync<T>(url: string): Promise<T> {
        this.status = "uploading";
        let result = await Http.postBinaryAsync<T>(url, this._input.files[0], ev => {

            this.progress = ev.loaded / ev.total;

        });
        this.status = "uploaded";

        return result;
    }

    /****************************************/

    get file() {
        if (!this._input.files || this._input.files.length == 0)
            return null;
        return this._input.files[0];
    }

    /****************************************/

    progress: number = 0;

    text: string = null;

    progressText: string = null;

    status: FileStatus = "empty";

    error: string = null;

    isSelected: boolean = false;
}

registerType(FileUploadView, "FileUploadView");