import { LocalString } from "../../Core/Types/DynamicString";
import { Bindable } from "../../Templating/Properties";
import { IViewComponentConfig, ViewComponent } from "./ViewComponent";
import { registerType } from "../../Core/Utils/ObjectUtils";
import "Templates/IconTextView.html";
import "Templates/Icon.html";

export interface IIconTextViewConfig extends IViewComponentConfig {

    icon?: Bindable<string>;

    text?: Bindable<LocalString>;

    badge?: Bindable<LocalString>;
}

/****************************************/

export class IconTextView extends ViewComponent {

    constructor(config?: IIconTextViewConfig) {

        super(config);

        this.bindConfig("icon", config);

        this.bindConfigString("text", config);

        this.bindConfigString("badge", config);
    }

    /****************************************/

    icon: string = null;

    text: string = null;

    badge: string = null;
}

registerType(IconTextView, "IconTextView");