import { linq } from "../../Core/Linq";
import { getTypeName, registerType } from "../../Core/Utils/ObjectUtils";
import { IObservableList, IObservableListHandler } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { IAsyncLoad } from "../Abstraction/IAsyncLoad";
import { IItemsContainer, IItemsLoader } from "../Abstraction/IItemsLoader";
import { IScrollData, IScrollListener } from "../Abstraction/IScrollListener";
import { ISingleItemSelector } from "../Abstraction/ISingleItemSelector";
import { IView, IViewFactory } from "../Abstraction/IView";
import { isAsyncLoad, isSelectable } from "../Helpers/TypeCheck";
import { FullItemsLoader } from "../Items/FulltemsLoader";
import { IItemChangeListener } from "../Items/ItemsObserver";
import { ItemsSource } from "../Items/ItemsSource";
import { IItemsViewConfig, ItemsView } from "./ItemsView";
import { TextView } from "./TextView";
import { ViewComponentStatus } from "./ViewComponent";
import "Templates/ListView.html";
import { Services } from "../../Core/Global";


type ListViewStatus = ViewComponentStatus | "loading";

export type SelectionMode = "none" | "multiple" | "single";

/****************************************/

export interface IListViewConfig<TItem, TView extends IView, TFilter extends {}> extends IItemsViewConfig<TView> {

    itemsSource?: ItemsSource<TItem, any, any>;

    createItemView?: IViewFactory<TItem, TView>;

    itemsLoader?: IItemsLoader<TItem, any>;

    onSelectdItemChanged?: (item: TItem) => void;

    isListenerActive?: boolean;

    items?: Bindable<IObservableList<TItem>>;

    filter?: TFilter;

    selectionMode?: SelectionMode;

    showSeparator?: boolean;

    isAutoLoad?: boolean;

    footer?: IView[];

    header?: IView[];
}

/****************************************/

export class ListView<TItem = any, TView extends IView = any, TFilter extends {} = any> extends ItemsView<TView> implements IScrollListener, IItemsContainer<TItem, any>, IAsyncLoad, ISingleItemSelector<TItem> {

    protected _isLoaded: boolean = false;
    protected _itemViewMap = new Map<TView, TItem>();
    protected _listener: IItemChangeListener<TItem, any>;

    constructor(config?: IListViewConfig<TItem, TView, TFilter>) {

        super({ template: "ListView", styles: ["default"], ...config });

        this.bindConfig("itemsSource", config);

        this.bindConfig("items", config);

        this.bindConfig("filter", config);

        this.bindConfig("selectionMode", config);

        if (config) {

            if (config.itemsLoader)
                this.itemsLoader = config.itemsLoader;

            if (config.showSeparator !== undefined)
                this.showSeparator = config.showSeparator;

            if (config.createItemView)
                this.createItemViewWork = config.createItemView;

            if (config.header)
                this.header.addRange(config.header);

            if (config.footer)
                this.footer.addRange(config.footer);
        }

        if (!config?.items)
            this.items = observableListOf();

        if (!this.itemsLoader && this.itemsSource)
            this.itemsLoader = FullItemsLoader.instance;

        if (config && config.isAutoLoad)
            this.loadAsync();

        const itemsHandler = <IObservableListHandler<TItem>>{

            onClear: () => {
                this.selectedItem = null;
                this._itemViewMap.clear();
                this.content.clear();
            },

            onItemAdded: (item, index, reason) => {
                if (reason != "replace")
                    this.content.insert(index, this.createItemView(item));
            },

            onItemRemoved: (item, index, reason) => {
                if (reason == "remove")
                    this.content.removeAt(index)
            },

            onItemReplaced: (newItem, oldItem, index) => this.content.set(index, this.createItemView(newItem)),

            onItemSwap: (index, newIndex) => this.content.swap(index, newIndex),
        };

        this.prop("items").subscribe((value, oldValueX) => {

            if (oldValueX != null)
                oldValueX.unsubscribe(itemsHandler);

            this.content.clear();

            if (value) {
                value.subscribe(itemsHandler);
                for (let item of value)
                    this.content.add(this.createItemView(item))
                this.status = "loaded";
            }
        });

        this.prop("items").notifyChanged();

        this.prop("selectedItem").subscribe((value, oldValue) => {

            if (this.selectionMode == "single")
                this.updateItemSelection(oldValue, false);

            if (this.selectionMode != "none") {
                this.updateItemSelection(value, true);

                if (config?.onSelectdItemChanged)
                    config.onSelectdItemChanged(value);
            }
        });

        if (config?.isListenerActive)
            this.activateListener();
    }

    /****************************************/

    activateListener() {

        if (!this.itemsSource?.typeName)
            return;

        if (!this._listener)

            this._listener = {

                onItemAdded: args => this.refreshAsync(),

                onItemRemoved: args => {
                    if (args.value)
                        this.items.removeWhen(a => this.itemsSource.equals(this.itemsSource.getItemValue(a), args.value));
                },

                onItemChanged: async args => {

                    let index = linq(this.items).indexOf(a => this.itemsSource.equals(this.itemsSource.getItemValue(a), args.value));
                    if (index != -1) {
                        const newItem = await this.itemsSource.getItemByValueAsync(args.value);
                        if (newItem)
                            this.items.set(index, newItem);
                    }
                }
            };

        Services.itemsObserver.register(this.itemsSource.typeName, this._listener);
    }

    /****************************************/

    deactivateListener() {

        if (this._listener)
            Services.itemsObserver.unregister(this.itemsSource.typeName, this._listener);
    }

    /****************************************/

    protected updateItemSelection(item: TItem, isSelected: boolean) {

        let view = this.findItemView(item);

        if (isSelectable(view))
            view.isSelected = isSelected;
    }

    /****************************************/

    findItemView(item: TItem): TView {

        if (!item)
            return null;
        return linq(this._itemViewMap.entries()).where(a => a[1] == item).select(a => a[0]).first();
    }

    /****************************************/

    removeItem(item: TItem) {

        this.items.remove(item);
    }

    /****************************************/

    addItem(item: TItem) {

        this.items.add(item);
    }

    /****************************************/

    clear() {
        this.items.clear();
    }

    /****************************************/

    async loadAsync() {

        if (this._isLoaded)
            return;

        this.refreshAsync();
    }

    /****************************************/

    async refreshAsync() {

        let oldSelection = this.selectedItem;

        if (this.itemsLoader) {

            this.beginUpdate();

            await this.itemsLoader.loadItemsAsync(this, 25);

            if (!oldSelection && this.selectedItem)
                oldSelection = this.selectedItem;

            this.endUpdate();
        }

        this._isLoaded = true;

        if (oldSelection && this.itemsSource)
            this.selectedItem = linq(this.items).first(a => this.itemsSource.itemComparer(a, oldSelection));
        else
            this.selectedItem = null;
    }

    /********************************************/

    protected onItemRemoved(itemView: TView) {

        if (itemView) {

            if (itemView.parentView == this)
                itemView.parentView = null;

            let item = this._itemViewMap.get(itemView);

            if (item == this.selectedItem)
                this.selectedItem = null;

            this._itemViewMap.delete(itemView);

            //TODO remove selection
            /*
            if (ViewUtils.isSelectable(itemView))
                itemView.prop("isSelected").unsubscribe(this.selectionHandler);*/
        }
    }

    /****************************************/

    protected createItemView(item: TItem): TView {

        let itemView = this.createItemViewWork(item);

        if ((this.status == "loading" || this.status == "loaded") && isAsyncLoad(itemView)) {
            itemView.loadAsync(); //Not awaited
        }

        if (itemView) {
            itemView.parentView = this;
            this._itemViewMap.set(itemView, item);
            if (isSelectable(itemView))
                itemView.prop("isSelected").subscribe(value => {
                    if (value)
                        this.selectedItem = item;
                });
        }

        return itemView;
    }

    /****************************************/

    protected createItemViewWork(item: TItem): TView {

        let factory = Services.views[getTypeName(item)];
        if (factory)
            return <TView>factory(item);

        return <any>new TextView({ content: this.itemsSource ? this.itemsSource.getItemText(item) : item.toString() });
    }

    /****************************************/
    
    onScroll(data: IScrollData) {

        if (this.itemsLoader && "onScroll" in this.itemsLoader)
            (this.itemsLoader as unknown as IScrollListener).onScroll(data);
    }

    /****************************************/

    get itemsCount() { return this.content.count }

    get isScrollCheckActive() { return this.itemsLoader && "onScroll" in this.itemsLoader }

    readonly header : IObservableList<IView> = observableListOf();

    readonly footer: IObservableList<IView> = observableListOf();

    itemsSource: ItemsSource<TItem, any, TFilter> = null;

    itemsLoader: IItemsLoader<TItem, TFilter> = null;

    items: IObservableList<TItem> = null;

    selectedItem: TItem = null;

    status: ListViewStatus = "";

    selectionMode: SelectionMode = "none";

    showSeparator: boolean = false;

    filter: TFilter = null;
}

registerType(ListView, "ListView");