import { Format } from "../../Core/Helpers/Format";
import { LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { BindableObject } from "../../Templating/BindableObject";
import { ActionPriority } from "../Abstraction/IAction";
import { IAsyncEditor } from "../Abstraction/IAsyncEditor";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { ViewStyle } from "../Abstraction/IView";
import { PopUpContent } from "../Components/PopUpContent";
import { IItemEditContentConfig, ItemEditContent } from "../Content/ItemEditContent";

export interface IPopUpEditorConfig<TItem, TEdit> extends IItemEditContentConfig<TItem, TEdit> {

    closeLabel?: LocalString;

    editToItemAsync?: (edit: TEdit) => Promise<TItem>;

    name?: string;
}

/********************************************/

export class PopUpEditor<TItem, TEdit = TItem> extends BindableObject implements IAsyncEditor<TItem, TEdit> {

    constructor(config?: IPopUpEditorConfig<TItem, TEdit>) {

        super();

        this.bindConfig("name", config);

        this.bindConfig("editor", config);

        this.bindConfig("styles", config);

        this.bindConfig("saveOnCommit", config);

        this.bindConfig("savePriority", config);

        this.bindConfigString("title", config, StringUsage.Title);

        this.bindConfigString("closeLabel", config, StringUsage.Action);

        this.bindConfigString("saveLabel", config, StringUsage.Action);

        if (config) {

            if (config.editToItemAsync)
                this.editToItemAsync = config.editToItemAsync;
        }

        if (!this.closeLabel)
            this.closeLabel = Format.action("cancel");

        if (!this.saveLabel)
            this.saveLabel = Format.action("ok");
    }

    /********************************************/

    async editAsync(value: TItem, validate?: (edit: TEdit) => Promise<boolean>): Promise<TEdit> {

        const popUp = new PopUpContent<TItem>({
            title: this.title,
            contentProvider: new ItemEditContent<TItem, TEdit>({
                editor: this.editor,
                value: value,
                styles: this.styles,
                savePriority: this.savePriority,
                saveOnCommit: this.saveOnCommit,
                saveLabel: this.saveLabel,
                saveItemAsync: async edit => {
                    if (validate) {
                        const valResult = await validate(edit);
                        if (valResult == null)
                            return null;
                    }
                    return await this.editToItemAsync(edit);
                }
            }),
            closeLabel: this.closeLabel
        });

        const result = await (await popUp.openAsync()).result;

        if (!result)
            return null;

        return this.editor.value;
    }

    /********************************************/

    protected editToItemAsync(edit: TEdit): Promise<TItem> {
        return Promise.resolve(edit as unknown as TItem);
    }

    /********************************************/

    editor: IItemEditor<TItem, TEdit> = null;

    title: string = "Edit";

    saveLabel?: string = null;

    savePriority: ActionPriority = null;

    closeLabel: string = null;

    saveOnCommit: boolean = false;

    styles: ViewStyle[] = [];

    name: string = null;
}

registerType(PopUpEditor, "PopUpEditor");