import { IOperation } from "../../Core/Abstraction/IOperation";
import { Services } from "../../Core/Global";
import { Uri } from "../../Core/Helpers/Uri";
import { linq } from "../../Core/Linq";


export interface ILoader {

    isLoaded(): boolean;

    loadAsync(operation?: IOperation): Promise<boolean>;
}

/****************************************/

export class ScriptLoader implements ILoader {

    constructor(src: string) {

        this.src = Uri.absolute(src.replace("~/", "./"));
    }

    /****************************************/

    isLoaded(): boolean {

        if (this.src.substr(this.src.length - 3) == ".ts")
            return true;
        return linq(document.scripts).any(a => (a as HTMLScriptElement).src.toLowerCase().startsWith(this.src.toLowerCase()));
    }

    /****************************************/

    loadAsync(operation?: IOperation): Promise<boolean> {

        if (this.isLoaded())
            return Promise.resolve(true);

        return new Promise<boolean>((resolve, reject) => {
            const curOperation = Services.operation.begin({ parentOperation: operation, message: "Loading script " + this.src });

            const scriptNode = document.createElement("script");

            scriptNode.onload = () => {
                curOperation.end();
                resolve(true);
            }

            scriptNode.onerror = () => {
                curOperation.end();
                resolve(false);
            }

            scriptNode.type = "text/javascript";
            scriptNode.src = this.src;

            document.body.appendChild(scriptNode);
        });
    }

    /****************************************/

    src: string;
}

/****************************************/

export class StyleLoader implements ILoader {

    constructor(src: string) {
        this.src = Uri.absolute(src.replace("~/", "./"));
    }

    /****************************************/

    isLoaded(): boolean {

        return linq(document.styleSheets).any(a => a.href && a.href.toLowerCase().startsWith(this.src.toLowerCase()));
    }

    /****************************************/

    loadAsync(operation?: IOperation): Promise<boolean> {

        if (this.isLoaded())
            return Promise.resolve(true);

        return new Promise<boolean>((resolve, reject) => {
            const curOperation = Services.operation.begin({ parentOperation: operation, message: "Loading style " + this.src });

            const linkNode = document.createElement("link");

            linkNode.onload = () => {
                curOperation.end();
                resolve(true);
            }

            linkNode.onerror = () => {
                curOperation.end();
                resolve(false);
            }

            linkNode.rel = "stylesheet";
            linkNode.href = this.src;

            document.body.appendChild(linkNode);
        });
    }

    /****************************************/

    src: string;
}