import { linq } from "../../Core/Linq";
import { equalsArray } from "../../Core/Utils/ArrayUtils";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { SelectMultipleItemsActivity } from "../Activities/SelectMultipleItemsActivity";
import { RemovableItemView } from "../Components/RemovableItemView";
import { Actions } from "../Helpers/Actions";
import { BasePicker, IBasePickerConfig } from "./BasePicker";
import "Templates/MultiItemPicker.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface IMultiItemPickerConfig<TItem, TValue, TFilter, TEdit> extends IBasePickerConfig<TItem, TValue, TValue[], TFilter, TEdit> {

}

/****************************************/

export class MultiItemPicker<TItem, TValue, TFilter = any, TEdit = TItem> extends BasePicker<TItem, TValue, TValue[], TFilter, TEdit> {

    constructor(config?: IMultiItemPickerConfig<TItem, TValue, TFilter, TEdit>) {

        super(config);
    }

    /****************************************/

    async selectAsync() {

        let mustRefresh = false;

        if (this.selector == null) {
            this.selector = new SelectMultipleItemsActivity<TItem, TValue, TFilter, TEdit>({
                createItemEditor: () => this.createItemEditor(),
                createItemContentView: item => this.createItemView(item),
                createItemListView: item => this.createItemListView(item),
                itemsSource: this.itemsSource,
                addLabel: this.addLabel,
                searchMode: this.searchMode,
                tooltip: this.pickLabel,
                canAdd: this.canAdd
            });

            this.selector.filters = this.filters;
        }
        else
            mustRefresh = true;

        this.selector.setSelectedItems(linq(this.items).select(a => a.item).toArray());

        if (mustRefresh)
            await this.selector.refreshAsync();

        const result = await (await Actions.loadPageAsync(this.selector)).result;

        if (result != null) {
            const newValue = linq(result).select(a => this.itemsSource.getItemValue(a)).toArray();
            if (!equalsArray(newValue, this.editValue))
                this.editValue = newValue;
        }
    }


    /********************************************/

    protected valueToEdit(value: TValue[]): TValue[] {
        return value ? value.slice() : [];
    }

    /********************************************/

    protected editToValue(value: TValue[]): TValue[] {
        return value ? value.slice() : [];
    }

    /****************************************/

    protected async updateViewAsync() {

        if (this.status == "loading")
            return;

        this.items.clear();

        if (this.editValue) {
            this.status = "loading";
            try {
                for (let value of this.editValue) {

                    const item = await this.itemsSource.getItemByValueAsync(value);

                    if (!item)
                        continue;

                    const itemView = new RemovableItemView({
                        item: item,
                        content: this.createItemView(item),
                        removeAsync: async () => {

                            this.items.remove(itemView);

                            let value = this.itemsSource.getItemValue(itemView.item);

                            let index = linq(this.editValue).indexOf(a => this.itemsSource.equals(a, value));

                            this.editValue.splice(index, 1);

                            await this.notifyEditValueChangedAsync();
                        }
                    });

                    this.items.add(itemView);
                }
            }
            finally {
                this.status = "loaded";
            }
        }
    }

    /****************************************/

    selector: SelectMultipleItemsActivity<TItem, TValue, TFilter, TEdit> = null;

    readonly items: IObservableList<RemovableItemView<TItem>> = observableListOf();
}


registerType(MultiItemPicker, "MultiItemPicker");