import { App } from "../../Core/Global";
import { Format } from "../../Core/Helpers/Format";
import { Uri } from "../../Core/Helpers/Uri";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { IMultiPageHost } from "../Abstraction/IPageHost";
import { IPageConfig, Page } from "./Page";
import { WebView } from "./WebView";

export interface IWebPageConfig extends IPageConfig<WebView> {

}

/****************************************/

export class WebPage extends Page<WebView> {

    constructor(config?: IWebPageConfig) {

        super(config);

        this.view = new WebView();
        this.updateUrl();

        this.prop("url").subscribe(() => this.updateUrl());
    }

    /****************************************/

    protected updateUrl() {
        this.view.content = Uri.absolute(Format.replaceArgs(this.url, App.startupArgs));
    }

    /****************************************/

    protected loadWorkAsync(): Promise<void> {

        if (!this.view.window || this.view.window.document.readyState == "complete")
            return Promise.resolve();

        return new Promise((res, rej) => {

            function onLoad(ev: Event) {
                this.view.window.removeEventListener("load", onLoad);
                res();
            }

            function onError(ev: Event) {
                this.view.window.removeEventListener("error", onError);
                rej();
            }

            this.view.window.addEventListener("load", onLoad);
            this.view.window.addEventListener("error", onError);
        });
    }

    /****************************************/

    refreshAsync(): Promise<void> {

        if (!this.view.window)
            return Promise.resolve();

        return new Promise((res, rej) => {

            function onLoad(ev: Event) {
                this.view.window.removeEventListener("load", onLoad);
                res();
            }

            function onError(ev: Event) {
                this.view.window.removeEventListener("error", onError);
                rej();
            }

            this.view.window.addEventListener("load", onLoad);
            this.view.window.addEventListener("error", onError);

            this.view.window.document.location.reload();
        });
    }

    /****************************************/

    async closeAsync() {

        this.view.window.close();

        if ("goBackAsync" in this.host)
            await (this.host as IMultiPageHost).goBackAsync();

        return true;
    }
}


registerType(WebPage, "WebPage");