import { DynamicString, StringUsage } from "../../Core/Types/DynamicString";
import { Guid } from "../../Core/Types/Types";
import { ContextMenu } from "../Components/ContextMenu";
import { FileUploadView } from "../Components/FileUploadView";
import { IMediaViewConfig, MediaType, MediaView } from "../Components/MediaView";
import { BaseEditor, IBaseEditorConfig } from "./BaseEditor";
import "Templates/MediaEditor.html";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { Format } from "../../Core/Helpers/Format";

export type ImageEditStatus = "unchanged" | "deleted" | "new" | "empty";

export interface IMediaData {

    id?: Guid;

    type?: MediaType;

    status: ImageEditStatus;
}

/****************************************/

export interface IMediaUploadConfig extends IBaseEditorConfig<IMediaData> {

    getUploadUrl: (fileName: string) => string;

    noMediaUrl: string;

    getMediaUrl?(data: IMediaData): string;

    maxFileSize?: number;

    validMedias?: MediaType[];

    mediaView?: IMediaViewConfig;
}

/****************************************/

export class MediaEditor extends BaseEditor<IMediaData> {

    constructor(config?: IMediaUploadConfig) {

        super({ template: "MediaEditor", ...config });

        this.bindConfig("noMediaUrl", config);

        this.bindConfig("maxFileSize", config);

        this.bindConfig("validMedias", config);

        if (config) {

            if (config.getUploadUrl)
                this.getUploadUrl = config.getUploadUrl;

            if (config.getMediaUrl)
                this.getMediaUrl = () => config.getMediaUrl(this.editValue);
        }

        this.mediaView = new MediaView({ image: { template: "ImageInline" }, video: { template: "VideoViewInline" }, ...config?.mediaView });

        this.fileUpload = new FileUploadView({

            onChanged: () => this.notifyEditValueChangedAsync()
        });

        this.fileUpload.visible = false;

        this.mediaView.content = {
            type: "empty"
        };
    }

    /********************************************/

    protected getUploadUrl(fileName: string): string {

        throw "Not implemented";
    }

    /********************************************/

    protected validateAsyncWork(force?: boolean): Promise<boolean> {

        if (this.fileUpload.status == "selected") {

            if (this.validMedias.indexOf("image") != -1 && this.fileUpload.file.type.startsWith("image/"))
                this.mediaView.content.type = "image";

            else if (this.validMedias.indexOf("video") != -1 && this.fileUpload.file.type.startsWith("video/"))
                this.mediaView.content.type = "video";

            else
                this.error = Format.message("msg-invalid-media");

            if (this.maxFileSize && this.fileUpload.file.size > this.maxFileSize)
                this.error = Format.message("msg-max-size", (this.maxFileSize / 1024 / 1024).toString());
        }

        return Promise.resolve(!this.error);
    }

    /********************************************/

    protected async commitAsyncWork(force?: boolean) {

        if (this.fileUpload.status == "selected") {

            const result = await this.fileUpload.uploadAsync<Guid>(this.getUploadUrl(this.fileUpload.file.name));

            this.editValue.id = result;
            this.editValue.status = "new";
            this.editValue.type = this.mediaView.content.type;

            this.mediaView.content = {
                src: this.getMediaUrl(),
                type: this.mediaView.content.type
            };

            this.notifyEditValueChangedAsync();
        }
        return true;
    }

    /********************************************/

    protected getMediaUrl(): string {

        throw "Not Implemented";
    }

    /********************************************/

    async beginEditAsync(value: IMediaData) {

        await super.beginEditAsync(value);

        if (!this.editValue)
            this.editValue = {
                id: null,
                type: "empty",
                status: "empty"
            };

        if (this.editValue.status == "empty")
            this.mediaView.content = {
                type: "image",
                src: this.noMediaUrl
            };

        else {
            this.mediaView.content = {
                type: this.editValue.type,
                src: this.getMediaUrl()
            };
        }
    }

    /****************************************/

    change() {

        this.fileUpload.visible = true;

        this.fileUpload.select();
    }

    /****************************************/

    remove() {

        this.editValue.status = "deleted";

        this.mediaView.content = {
            type: "image",
            src: this.noMediaUrl
        };

        this.notifyEditValueChangedAsync();
    }

    /****************************************/

    showMenu() {

        const menu = new ContextMenu();

        if (this.editValue.status == "new" || this.editValue.id) {
            menu.addAction({
                name: "remove-item",
                icon: "fas fa-trash",
                displayName: "delete",
                executeAsync: async () => this.remove()
            });
        }

        menu.addAction({
            name: "edit-item",
            icon: "fas fa-edit",
            displayName: "change",
            executeAsync: async () => this.change()
        })

        menu.showAsync(undefined, <MouseEvent>window.event);
    }

    /****************************************/

    fileUpload: FileUploadView = null;

    mediaView: MediaView = null;

    noMediaUrl: string = null;

    maxFileSize: number = null;

    validMedias: MediaType[] = ["video", "image"];
}

registerType(MediaEditor, "MediaEditor");