import { App } from "../Global";

export namespace Uri {

    var _relativeSegment: string;
    var _absoluteSegment: string;

    /****************************************/

    export function absolute(uri: string): string {

        if (uri.substr(0, 2) == "./" || uri.substr(0, 2) == "~/")
            return getAbsoluteSegment(document.URL) + App.baseUrl.substr(1) + uri.substr(2);

        else if (uri.substr(0, 1) == "/")
            return getAbsoluteSegment(document.URL) + uri.substr(1);

        else if (isAbsolute(uri))
            return uri;

        return getRelativeSegment(document.URL) + uri.substr(1);
    }

    /****************************************/

    export function isAbsolute(uri: string): boolean {
        return uri.indexOf("://") != -1;
    }

    /****************************************/

    export function getRelativeSegment(uri: string): string {

        if (!_relativeSegment) {

            _relativeSegment = "";

            let index = uri.lastIndexOf("/");
            if (index != -1)
                _relativeSegment = uri.substr(0, index + 1);
        }
        return _relativeSegment;
    }

    /****************************************/

    export function getAbsoluteSegment(uri: string): string {

        if (!_absoluteSegment) {

            _absoluteSegment = "";

            let index = uri.indexOf("://");
            if (index != -1) {
                index = uri.indexOf("/", index + 4);
                if (index != -1)
                    _absoluteSegment = uri.substr(0, index + 1);
            }
        }

        return _absoluteSegment;
    }
}