import { App } from "../../Core/Global";
import { Type } from "../../Core/Types/Types";
import { IPage } from "../Abstraction/IPage";
import { IPageLoadOptions } from "../Abstraction/IPageHost";
import { ISelectOptions } from "../Abstraction/ISelectOptions";
import { IViewApplication } from "../Abstraction/IViewApplication";
import { IViewContentProvider } from "../Abstraction/IViewContentProvider";
import { ContentActivity, IContentActivitConfig } from "../Activities/ContentActivity";
import { WebPage } from "../Components/WebPage";
import { PopUpEditor } from "../Editors/PopupEditor";
import { SingleItemSelector } from "../Editors/SingleItemSelector";
import { ILocation } from "../Types/Location";


interface ISelectFileOptions {

}

export const Actions: IActions = {} as IActions;

declare global {

    export interface IActions {

        loadPageAsync<TPage extends IPage>(page: TPage, options?: IPageLoadOptions): Promise<TPage>;

        go(url: string): Promise<void>;

        content<TContent extends IViewContentProvider>(conetent: TContent | Type<TContent>, contentOptions?: Partial<IContentActivitConfig<ContentActivity>>, pageOptions?: IPageLoadOptions): Promise<ContentActivity>;

        call(phoneNumber: string): Promise<void>;

        email(email: string): Promise<void>;

        webPage(url: string): Promise<WebPage>;

        navigate(uri: string): Promise<void>;

        facebookPage(pageId: string): Promise<void>;

        selectFileAsync(options?: ISelectFileOptions): Promise<File>;

        maps(location: ILocation, zoomLevel?: number): Promise<void>;

        selectOptionAsync<TItem, TValue>(options: ISelectOptions<TItem, TValue>): Promise<TValue>;
    }
}


/****************************************/

Actions.selectFileAsync = options => {

    return new Promise<File>((res, rej) => {

        var input = document.createElement("input");
        input.type = "file";

        input.onchange = () => {
            res(input.files[0]);
        }

        input.click();
    });
};

/****************************************/

Actions.selectOptionAsync = async options => {

    let editor = new PopUpEditor({

        editor: new SingleItemSelector({
            template: "ItemSelectorList",
            itemsSource: options.itemsSource,
            styles: ["no-padding-items-h"]
        }),
        saveOnCommit: options.saveOnCommit == undefined ? true : options.saveOnCommit,
        saveLabel: options.saveLabel,
        closeLabel: options.closeLabel,
        title: options.title,
        styles: options.styles
    });

    return await editor.editAsync(options.value)
}

/****************************************/

Actions.loadPageAsync = async (page, options) => {

    return await (App as IViewApplication).pageHost.loadAsync(page, options);
}

/****************************************/

Actions.content = (content,  contentOptions, pageOptions) => {

    const value = typeof content == "function" ? new content() : content;

    const activity = new ContentActivity({
        providers: [
            value
        ],
        ...contentOptions
    });
    return Actions.loadPageAsync(activity, pageOptions);
}

/****************************************/

Actions.go = url => {

    window.location.href = url;
    return Promise.resolve();
}

/****************************************/

Actions.call = phoneNumber => {

    window.location.href = "tel:" + phoneNumber;
    return Promise.resolve();
}

/****************************************/

Actions.email = email => {

    window.location.href = "mailto:" + email;
    return Promise.resolve();
}

/****************************************/

Actions.webPage = url => {

    return Actions.loadPageAsync(new WebPage({ url: url }));
}

/****************************************/

Actions.navigate = uri => {

    return new Promise((res, rej) => {
        const newWindows = window.open(uri, "_blank");
        newWindows.addEventListener("load", ev => {
            res();
        });
        newWindows.addEventListener("error", ev => {
            rej();
        });
    });
}

/****************************************/

Actions.facebookPage = pageId =>  {

    window.location.href = "https://www.facebook.com/" + pageId;
    //window.location.href = "fb://page/" + pageId;
    return Promise.resolve();
}

/****************************************/

Actions.maps = (location, zoomLevel = 17) => {

    if (location.position)
        window.location.href = "https://www.google.com/maps/search/?api=1&query=" + location.position.latitude + "," + location.position.longitude;
    //window.location.href = "geo:" + location.position.latitude + "," + location.position.longitude;
    else
        window.location.href = "https://www.google.com/maps/search/?api=1&query=" + encodeURIComponent(location.address);

    return Promise.resolve();
}