import { registerType } from "../../Core/Utils/ObjectUtils";
import { CatalogTemplate } from "../../Templating/Abstraction/ITemplateProvider";
import { Bindable } from "../../Templating/Properties";
import { IContentView } from "../Abstraction/IContentView";
import { isView } from "../Helpers/TypeCheck";
import { IViewComponentConfig, ViewComponent } from "./ViewComponent";

export interface IContentViewConfig<TContent> extends IViewComponentConfig {

    content?: Bindable<TContent>;

    contentTemplate?: CatalogTemplate<TContent>;
}

/********************************************/

export class ContentView<TContent = any> extends ViewComponent implements IContentView<TContent> {

    constructor(config?: IContentViewConfig<TContent>) {

        super(config);

        this.bindConfig("content", config, null, () => this.onContentChanged());

        this.bindConfig("contentTemplate", config);

        if (this.contentTemplate) 
            this.template = t => t.template(t.model.contentTemplate, t.model.content);
    }


    /********************************************/

    protected onContentChanged() {

        if (isView(this.content))
            this.content.parentView = this;
    }

    /********************************************/

    content: TContent = null;

    contentTemplate: CatalogTemplate<TContent> = null;
}

registerType(ContentView, "ContentView");