import { Format } from "../Helpers/Format";
import { DynamicString, LocalString } from "../Types/DynamicString";
import { TimeSpan } from "../Types/TimeSpan";
import { padLeft } from "./StringUtils";

export const WEEK_DAYS = ["Luned", "Marted", "Mercoled", "Gioved", "Venerd", "Sabato", "Domenica"];
export const MONTHS = ["Gennaio", "Febbraio", "Marzo", "Aprile", "Maggio", "Giugno", "Luglio", "Agosto", "Settembre", "Ottobre", "Novembre", "Dicembre"];

/****************************************/

export function parseDate(value: Date | string): Date {

    if (value instanceof Date)
        return value;

    return new Date(value);
}

/****************************************/

export function addDays(date: Date | string, value: number): Date {
    return dateAdd(date, TimeSpan.fromDays(value));
}

/****************************************/

export function dateAdd(date: Date | string, value: TimeSpan): Date {
    date = parseDate(date);
    return new Date(date.getTime() + value.ticks);
}

/****************************************/

export function dateDiff(date1: Date | string, date2: Date | string): TimeSpan {
    return new TimeSpan(parseDate(date1).getTime() - parseDate(date2).getTime());
}

/****************************************/

export function now(): Date {
    return new Date();
}

/****************************************/

export function today(): Date {
    return truncateTime(now());
}

/****************************************/

export function truncateTime(date: Date): Date {
    date = parseDate(date);
    return new Date(date.getFullYear(), date.getMonth(), date.getDate());
}

/****************************************/

export function timeOfDay(date: Date): TimeSpan {
    date = parseDate(date);
    return new TimeSpan(date.getTime() - truncateTime(date).getTime());
}

/****************************************/

export function formatDate(date: Date | string, format: LocalString): string {
    date = parseDate(date);
    return Format.replaceArgs(Format.text(format), arg => formatDateArgument(date, arg));
}

/****************************************/

export function formatDateArgument(value: Date | string, arg: string): string {
    value = parseDate(value);
    switch (arg) {
        case "D":
            return value.getDate().toString();
        case "DD":
            return padLeft(value.getDate().toString(), 2, "0");
        case "W":
            return WEEK_DAYS[(value.getDay() + 6) % 7].substr(0, 3);
        case "WW":
            return WEEK_DAYS[(value.getDay() + 6) % 7];
        case "M":
            return value.getMonth().toString();
        case "MM":
            return padLeft((value.getMonth() + 1).toString(), 2, "0");
        case "MMM":
            return MONTHS[value.getMonth()].substr(0, 3);
        case "MMMM":
            return MONTHS[value.getMonth()];
        case "YYYY":
            return value.getFullYear().toString();
        case "h":
            return value.getHours().toString();
        case "hh":
            return padLeft(value.getHours().toString(), 2, "0");
        case "m":
            return value.getMinutes().toString();
        case "mm":
            return padLeft(value.getMinutes().toString(), 2, "0");
        case "s":
            return value.getSeconds().toString();
        case "ss":
            return padLeft(value.getSeconds().toString(), 2, "0");
        case "f":
            return (value.getMilliseconds() / 100).toString();
        case "ff":
            return (value.getMilliseconds() / 10).toString();
        case "fff":
            return value.getMilliseconds().toString();
    }
    return arg;
}