import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { IView } from "../Abstraction/IView";
import { ContentView, IContentViewConfig } from "./ContentView";
import "Templates/RemovableItemView.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface IRemovableItemViewConfig<TItem, TView extends IView> extends IContentViewConfig<TView> {

    item?: TItem;

    removeAsync?: () => Promise<void>;
}

/****************************************/

export class RemovableItemView<TItem, TView extends IView = any> extends ContentView<TView> {

    constructor(config?: IRemovableItemViewConfig<TItem, TView>) {
        super(config);

        this.bindConfig("item", config);

        if (config) {

            if (config.removeAsync)
                this.removeWorkAsync = config.removeAsync;
        }
    }

    /****************************************/

    async removeAsync(): Promise<void> {

        this.status = "removed";
        await delayAsync(150);
        await this.removeWorkAsync();
    }

    /****************************************/

    protected removeWorkAsync(): Promise<void> {

        throw "Not supported";
    }

    /****************************************/

    item: TItem = null;
}

registerType(RemovableItemView, "RemovableItemView");