import { LocalString } from "../../Core/Types/DynamicString";
import { Bindable } from "../../Templating/Properties";
import { IconTextView, IIconTextViewConfig } from "./IconTextView";
import "Templates/SimpleItemView.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface ISimpleItemViewConfig<TITem, TStatus> extends IIconTextViewConfig {

    subText?: Bindable<LocalString>;

    status?: Bindable<TStatus>;

    item?: Bindable<TITem>;
}

/****************************************/

export class SimpleItemView<TITem = any, TStatus extends string = any> extends IconTextView {

    constructor(config?: ISimpleItemViewConfig<TITem, TStatus>) {

        super(config);

        this.bindConfigString("subText", config);

        this.bindConfig("status", config);

        this.bindConfig("item", config);
    }

    /****************************************/

    subText: string = null;

    status: TStatus = null;

    item: TITem = null;
}

registerType(SimpleItemView, "SimpleItemView");
