import { OperationType } from "../../Core/Abstraction/IOperation";
import { LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { BindableObject } from "../../Templating/BindableObject";
import { Bindable } from "../../Templating/Properties";
import { ActionPriority, IAction } from "../Abstraction/IAction";
import { IActivable } from "../Abstraction/IActivable";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { ViewStyle } from "../Abstraction/IView";
import { IViewContent } from "../Abstraction/IViewContent";
import { IViewContentHost } from "../Abstraction/IViewContentHost";
import { IViewContentInfo } from "../Abstraction/IViewContentInfo";
import { IViewContentProvider } from "../Abstraction/IViewContentProvider";
import { Container } from "../Components/Container";
import { isActivable } from "../Helpers/TypeCheck";

export interface IItemEditContentConfig<TItem, TEdit> {

    editor?: IItemEditor<TItem, TEdit>;

    value?: TItem;

    title?: Bindable<LocalString>;

    url?: string;

    saveLabel?: LocalString;

    savePriority?: ActionPriority;

    styles?: ViewStyle[];

    saveOnCommit?: boolean;

    saveItemAsync?: (item: TEdit) => Promise<TItem>;
}

/****************************************/

export class ItemEditContent<TItem, TEdit> extends BindableObject implements IViewContentProvider {

    private _host: IViewContentHost;

    constructor(config: IItemEditContentConfig<TItem, TEdit>) {
        super();

        this.bindConfig("editor", config);

        this.bindConfig("value", config);

        this.bindConfig("styles", config);

        this.bindConfig("url", config);

        this.bindConfig("savePriority", config);

        this.bindConfig("saveOnCommit", config);

        this.bindConfigString("title", config, StringUsage.Title);

        this.bindConfigString("saveLabel", config, StringUsage.Action);

        if (config) {
            if (config.saveItemAsync)
                this.saveItemAsync = config.saveItemAsync;
        }
    }

    /****************************************/

    async getContentAsync(host: IViewContentHost) {

        this._host = host;

        await this.editor.beginEditAsync(this.value);

        const container = new Container({
            name: "editor-container",
            title: this.title,
            content: this.editor.view
        });

        if (isActivable(this.editor))
            setTimeout(() => (this.editor as unknown as IActivable).activateAsync(), 200);

        const actions: IAction[] = [];

        if (this.saveOnCommit) {
            this.editor.prop("value").subscribe(value =>
                this.saveAsync());
        }
        else {
            actions.push({
                name: "save",
                icon: "fas fa-save",
                displayName: this.saveLabel,
                operation: OperationType.Global,
                priority: this.savePriority,
                executeAsync: () => this.saveAsync()
            });
        }


        return Promise.resolve({
            views: [container],
            actions: actions,
            styles: this.styles,
            title: this.title,
            url: this.url
        } as IViewContent);
    }

    /****************************************/

    protected saveItemAsync(item: TEdit): Promise<TItem> {

        return Promise.resolve(item as unknown as TItem);
    }

    /****************************************/

    async saveAsync() {

        //TODO check why it was needed before
        //(App as IViewApplication).unblock(true);

        if (this.saveOnCommit) {

            if (!this.editor.isValid)
                return;
        }
        else {

            if (!await this.editor.commitAsync(true))
                return;
        }

        const result = await this.saveItemAsync(this.editor.value);

        if (result == null)
            return;

        await this._host.closeAsync(result);
    }

    /****************************************/

    editor: IItemEditor<TItem, TEdit> = null;

    title: string = null;

    url: string = null;

    value: TItem = null;

    saveLabel: string = "Save";

    saveOnCommit: boolean = false;

    savePriority: ActionPriority = null;

    styles: ViewStyle[] = ["vertical", "scroll", "fill-h", "fit-items-h"];

    readonly info = {
        name: "item-edit",
        icon: "fas fa-edit",
        displayName: "Edit",
    } as IViewContentInfo;
}