import { Type } from "../../Core/Types/Types";
import { getBaseType, getType, getTypeName, registerType } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { IHTMLContainer } from "../Abstraction/IHTMLContainer";
import { ViewStyle } from "../Abstraction/IView";
import { IViewComponent } from "../Abstraction/IViewComponent";
import { formatForCss } from "../Helpers/ViewUtils";
import { IViewConfig, View } from "./View";

export interface IViewComponentConfig extends IViewConfig {

    name?: string;

    enabled?: Bindable<boolean>;

    visible?: Bindable<boolean>;

    styles?: ViewStyle[];
}

export type ViewComponentStatus = "" | "hidden" | string;

/****************************************/

export class ViewComponent extends View implements IViewComponent, IHTMLContainer {

    constructor(config?: IViewComponentConfig) {

        super(config);

        this.bindConfig("enabled", config);

        this.bindConfig("visible", config);

        if (config) {

            if (config.styles)
                this.styles = config.styles;

            if (config.name)
                this.name = config.name;
        }

        this.prop("styles").subscribe(() => this.buildStyles());

        this.buildStyles();
    }

    /****************************************/

    addStyle(value: ViewStyle) {
        if (this.styles.indexOf(value) != -1)
            return;
        this.styles.push(value);
        this.buildStyles();
    }

    /****************************************/

    removeStyle(value: ViewStyle) {
        const index = this.styles.indexOf(value);
        if (index == -1)
            return;
        this.styles.splice(index, 1);
        this.buildStyles();
    }

    /****************************************/

    buildStyles() {

        let result = "";

        let curType: Type<any> = getType(this);

        while (curType != ViewComponent) {
            let typeName = getTypeName(curType);
            result += formatForCss(typeName) + " ";
            curType = getBaseType(curType);
        }

        if (this.name)
            result += formatForCss(this.name) + " ";

        if (this.styles)
            result += this.styles.join(" ");

        this.className = result.trim();
    }

    /****************************************/

    get debugName() {
        return getTypeName(this) + ": " + this.name;
    }

    /****************************************/

    enabled: boolean = true;

    visible: boolean = true;

    styles: ViewStyle[] = [];

    name: string = null;

    nodes: Node[] = null;

    isCacheEnabled: boolean = false;

    className: string;

    status: ViewComponentStatus = "";
}


registerType(ViewComponent, "ViewComponent");