export function randomString(length: number, chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789"): string {

    let result = "";

    for (let i = 0; i < length; i++)
        result += chars.charAt(Math.floor(Math.random() * chars.length));

    return result;
}

/****************************************/

export function isUpperCase(value: string): boolean {
    return value.toUpperCase() === value;
}

/****************************************/

export function repeatString(value: string, count: number): string {
    let result = "";
    for (let i = 0; i < count; i++)
        result += value;
    return result;
}

/****************************************/

export function padLeft(value: string, count: number, char: string): string {
    if (value == null)
        return;
    if (value.length >= count)
        return value;
    return repeatString(char, count - value.length) + value;
}

/****************************************/

export function padRight(value: string, count: number, char: string): string {
    if (value == null)
        return;
    if (value.length >= count)
        return value;
    return value + repeatString(char, count - value.length);
}

/****************************************/

export function uuidv4(): string {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        const r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}