import { Format } from "../../Core/Helpers/Format";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { CatalogTemplate } from "../../Templating/Abstraction/ITemplateProvider";
import { Bindable } from "../../Templating/Properties";
import { IAction } from "../Abstraction/IAction";
import { IViewContentHost } from "../Abstraction/IViewContentHost";
import { IViewContentProvider } from "../Abstraction/IViewContentProvider";
import { ActionView } from "./ActionView";
import { IPanelConfig, Panel } from "./Panel";

export interface IContentHostViewConfig<TProvider extends IViewContentProvider> extends IPanelConfig {

    contentProvider?: Bindable<TProvider>;

    actionTemplate?: CatalogTemplate<ActionView>;
}

/****************************************/

export class ContentHostView<TProvider extends IViewContentProvider = any> extends Panel implements IViewContentHost {

    private _body: Panel;
    private _actions: Panel;

    constructor(config?: IContentHostViewConfig<TProvider>) {

        super({
            ...config
        });

        this.bindConfig("contentProvider", config);

        this.bindConfig("actionTemplate", config);

        this._body = new Panel({ name: "body" });

        this._actions = new Panel({ name: "actions" });

        this.addView(this._body);

        this.addView(this._actions);

        this._body.emptyView = this.emptyView;

        this.emptyView = null;
    }

    /********************************************/

    async loadAsync() {

        if (this.contentProvider) {

            await this.closeAsync();

            const content = await this.contentProvider.getContentAsync(this);

            if (content.styles)
                this._body.styles = content.styles;

            this.name = this.contentProvider.info.name;

            this.title = Format.title(content.title);

            content.views.forEach(a => this._body.addView(a));

            if (content.actions)
                content.actions.forEach(a => this.addAction(a));

            await super.loadAsync();

            if (this.contentProvider.activateAsync)
                await this.contentProvider.activateAsync();
        }

        this._actions.visible = this._actions.content.count > 0;
    }

    /********************************************/

    protected addAction(action: IAction) {

        this._actions.addView(ActionView.fromAction(action, { template: this.actionTemplate }));
    }

    /********************************************/

    notifyContentChanged(provider: IViewContentProvider<this>) {

        this.loadAsync();
    }

    /********************************************/

    async closeAsync(result?: any) {

        if (this.contentProvider?.deactivateAsync)
            await this.contentProvider.deactivateAsync();

        this._body.clear();

        this._actions.clear();

        this.title = null;

        return true;
    }

    /********************************************/

    title: string = null;

    actionTemplate: CatalogTemplate<ActionView> = "ActionButton";

    contentProvider: TProvider = null;
}

registerType(ContentHostView, "ContentHostView");