import { DynamicString } from "../Core/Types/DynamicString";
import { IObservableList } from "../Templating/Abstraction/IObservableList";
import { BindableObject } from "../Templating/BindableObject";
import { observableListOf } from "../Templating/ObservableList";
import { IItemAction } from "./Abstraction/IItemAction";
import { IMultiItemSelector } from "./Abstraction/IMultiItemSelector";
import { ISelectable } from "./Abstraction/ISelectable";
import { ActionView } from "./Components/ActionView";

export class SelectionManager<TItem extends ISelectable> extends BindableObject implements IMultiItemSelector<TItem> {

    constructor() {

        super();

        this.selectedItems.subscribe({
            onChanged: () => {
                this.updateView();
                if (this.selectedItems.count == 0)
                    this.close();
            }
        })
    }

    /****************************************/

    close() {

        this.isActive = false;

        for (var i = this.selectedItems.count - 1; i >= 0; i--)
            this.selectedItems.get(i).isSelected = false;

        this.selectedItems.clear();
        this.actions.clear();
        this.updateView();
    }

    /****************************************/

    open() {

        this.isActive = true;
    }

    /****************************************/

    addAction(action: IItemAction<TItem[]>) {

        const result = ActionView.fromItemAction(action, () => this.selectedItems.toArray());
        this.actions.add(result);
        return result;
    }

    /****************************************/

    protected updateView() {
        this.selectionText = DynamicString.getValue("selection-count", { params: [this.selectedItems.count.toString()], cardinality: this.selectedItems.count });
    }

    /****************************************/

    readonly selectedItems: IObservableList<TItem> = observableListOf();

    readonly actions: IObservableList<ActionView> = observableListOf();

    selectionText: string = null;

    isActive: boolean = false;
}