import { LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { TimeSpan } from "../../Core/Types/TimeSpan";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { TemplateBuilder } from "../../Templating/TemplateBuilder";
import { IViewComponentConfig, ViewComponent } from "./ViewComponent";

export interface IToastConfig extends IViewComponentConfig {

    showTime?: Bindable<TimeSpan>;

    message: Bindable<LocalString>;

    icon?: Bindable<string>;
}

/****************************************/

export class Toast extends ViewComponent {

    private static _container: HTMLElement;

    private static _items = observableListOf<Toast>();

    protected _showResolve: (res?: string) => void;

    constructor(config?: IToastConfig) {

        super({ template: "Toast", visible: false, ...config });

        if (Toast._container == null)
            Toast.init();

        this.bindConfigString("message", config, StringUsage.Tooltip);

        this.bindConfig("showTime", config);

        this.bindConfig("icon", config);

    }

    /****************************************/

    static init() {

        Toast._container = document.createElement("div");

        Toast._container.className = "toast-container";

        const builder = new TemplateBuilder(Toast._items, Toast._container);

        builder.template("ToastContainer", a => a);

        document.body.appendChild(Toast._container);
    }

    /****************************************/

    async showAsync() {

        Toast._items.add(this);

        await delayAsync(10);

        this.visible = true;

        let showMs = this.showTime.totalMilliseconds;

        if (showMs == 0)
            showMs = Math.min(Math.max(this.message.length * 70, 2000), 7000);

        setTimeout(() => this.hideAsync(), showMs);

        return new Promise<string>(res => this._showResolve = res);
    }

    /****************************************/

    async hideAsync() {

        this.visible = false;

        await delayAsync(600);

        Toast._items.remove(this);

        if (this._showResolve) {

            this._showResolve();
            this._showResolve = null;
        }
    }

    /****************************************/

    message: string = null;

    icon: string = null;

    showTime?: TimeSpan = TimeSpan.fromSeconds(0);
}


registerType(Toast, "Toast");