import { IOperation, IOperationProgress, OperationType } from "../Core/Abstraction/IOperation";
import { IOperationConfig, IOperationManager } from "../Core/Abstraction/IOperationManager";
import { isString } from "../Core/Helpers/TypeCheck";
import { DynamicString, LocalString } from "../Core/Types/DynamicString";
import { listOf } from "../Templating/ObservableList";
import { IViewConfig, View } from "./Components/View";
import { AppEvent, event } from "../Core/Types/Event";
import { IList } from "../Core/Abstraction/IList";
import { IViewApplication } from "./Abstraction/IViewApplication";
import { App, Services } from "../Core/Global";
import { registerType } from "../Core/Utils/ObjectUtils";
import { Format } from "../Core/Helpers/Format";

export interface IOperationViewConfig extends IOperationConfig, IViewConfig {

    type?: OperationType;

    parentOperation?: IOperation;
} 

/****************************************/

class OperationView extends View implements IOperation {

    private _progress: IOperationProgress;
    private _type: OperationType;

    /****************************************/

    constructor(config: IOperationViewConfig) {

        super(config);

        this.bindConfig("message", config);

        if (!config.type)
            this._type = OperationType.Global;
        else
            this._type = config.type;

        this.parentOperation = config.parentOperation;
    }

    /****************************************/

    end(): void {
        Services.operation.end(this);
    }

    /****************************************/

    get type(): OperationType {
        return this._type;
    }

    /****************************************/

    get progress(): IOperationProgress {
        return this._progress;
    }

    /****************************************/

    set progress(value: IOperationProgress) {

        this._progress = value;

        if (this._progress) {

            console.log(this.getProgressDescription(this._progress));

            if (this._progress.message)
                this.message = this._progress.message;
        }
        else
            this.message = undefined;
    }

    /****************************************/

    addSubOperation(operation: IOperation) {
        this.subOperations.add(operation);
    }

    /****************************************/

    removeSubOperation(operation: IOperation) {
        this.subOperations.remove(operation);
    }

    /****************************************/

    getProgressDescription(value: IOperationProgress): string {

        let msg = "Progress: ";

        if (value.message)
            msg += "'" + value.message + "'";

        if (value.current != null && value.totCount != null)
            msg += " - " + value.current + "/" + value.totCount + " (" + Math.round(100 / value.totCount * value.current) + "%)";

        return msg;
    }

    /****************************************/

    readonly subOperations = listOf<IOperation>();

    message: LocalString = null;

    parentOperation: IOperation = null;
}

/****************************************/

export class ViewOperationManager implements IOperationManager {

    progress(progress: IOperationProgress | string) {

        if (isString(progress))
            progress = <IOperationProgress>{ message: progress };

        if (this.current)
            this.current.progress = <IOperationProgress>progress;
    }

    /****************************************/

    begin(configOrMessge: IOperationViewConfig | string): IOperation {

        if (isString(configOrMessge))
            configOrMessge = <IOperationProgress>{ message: configOrMessge };

        const operation = new OperationView(configOrMessge);

        console.group("Begin operation: ", Format.text(operation.message));

        operation.progress = configOrMessge;

        if (operation.parentOperation === undefined)
            operation.parentOperation = this.current;

        this.operations.add(operation);

        if (operation.parentOperation)
            operation.parentOperation.addSubOperation(operation);

        if (operation.type != OperationType.Local)
           (App as IViewApplication).block();

        return operation;
    }

    /****************************************/

    end(operation: IOperation) {

        console.groupEnd();

        console.log("End operation: ", Format.text(operation.message));

        this.operations.remove(operation);

        if (operation.parentOperation)
            operation.parentOperation.removeSubOperation(operation);

        if (operation.type != OperationType.Local)
            (App as IViewApplication).unblock();
    }

    /****************************************/

    get current(): IOperation {
        return this.operations.count > 0 ? this.operations.get(this.operations.count - 1) : null;
    }

    /****************************************/

    readonly operations: IList<IOperation> = listOf();

    readonly onBegin: AppEvent<IOperation> = event();

    readonly onEnd: AppEvent<IOperation> = event();

    readonly onProgress: AppEvent<{ operation: IOperation, progress: IOperationProgress }> = event();
}

/****************************************/


registerType(OperationView, "OperationView");