import { linq } from "../../Core/Linq";
import { IBehavoir } from "../../Templating/Abstraction/IBehavoir";
import { CatalogTemplate } from "../../Templating/Abstraction/ITemplateProvider";
import { Bindable } from "../../Templating/Properties";
import { IContentView } from "../Abstraction/IContentView";
import { IView } from "../Abstraction/IView";
import { IconTextView, IIconTextViewConfig } from "./IconTextView";
import { IItemsViewConfig, ItemsView } from "./ItemsView";
import "Templates/NavBar.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface INavBarItemConfig<TContent> extends IIconTextViewConfig {

    name?: string;

    content?: TContent;

    badge?: Bindable<string>;

    behavoirs?: Bindable<(IBehavoir | string)[]>;
}

/****************************************/

type NavigationBarItemStatus = "active" | "";

/****************************************/

export class NavBarItem<TContent> extends IconTextView implements IContentView<TContent> {

    constructor(config: INavBarItemConfig<TContent>) {
        super(config);

        this.prop("status");

        this.bindConfig("badge", config);

        this.bindConfig("behavoirs", config);

        this.bindConfig("content", config);

        if (config)
            this.name = config.name;
    }

    /****************************************/

    select() {
        throw "Not implemented";
    }

    /****************************************/

    name: string = null;

    status: NavigationBarItemStatus = "";

    badge: string = null;

    behavoirs: (IBehavoir | string)[] = [];

    content: TContent = null;
}

/****************************************/

interface INavBarConfig<TContent> extends IItemsViewConfig<NavBarItem<TContent>> {

    items?: INavBarItemConfig<TContent>[];

    itemTemplate?: Bindable<CatalogTemplate<IView>>;

    itemBehavoirs?: Bindable<(IBehavoir | string)[]>;

    selectedItem?: Bindable<string>;

    onItemSelected?: (item?: NavBarItem<TContent>) => void;
}

/****************************************/

export class NavBar<TContent = any> extends ItemsView<NavBarItem<TContent>>  {

    constructor(config?: INavBarConfig<TContent>) {
        super(config);

        this.bindConfig("itemTemplate", config);

        this.bindConfig("itemBehavoirs", config);

        if (config) {

            if (config.items)
                config.items.forEach(a => this.addItem(a));

            if (config.onItemSelected) {

                this.onSelectedItemChanged = (a, b) => {
                    NavBar.prototype.onSelectedItemChanged.bind(this)(a, b);
                    config.onItemSelected(a);
                };
            }
        }

        this.bindConfig("selectedItem", config, {
            convertTo: a => this.getItem(a as string),
            convertFrom: a => a ? a.name : undefined
        }, this.onSelectedItemChanged);

    }

    /****************************************/

    getItem(name: string): NavBarItem<TContent> {
        return linq(this.content).first(a => a.name == name);
    }

    /****************************************/

    selectItem(name: string) {
        this.selectedItem = this.getItem(name);
    }

    /****************************************/

    addItem(config: INavBarItemConfig<TContent>) {

        let item = this.createItem(config);
        this.content.add(item);
        return item;
    }

    /****************************************/

    protected createItem(config?: INavBarItemConfig<TContent>): NavBarItem<TContent> {

        let item = new NavBarItem({ template: this.prop("itemTemplate"), behavoirs: this.prop("itemBehavoirs"), ...config });
        item.parentView = this;
        item.select = () =>
            this.selectedItem = item;
        return item;
    }

    /****************************************/

    protected onSelectedItemChanged(newItem: NavBarItem<TContent>, oldItem: NavBarItem<TContent>) {
        if (oldItem)
            oldItem.status = "";
        if (newItem)
            newItem.status = "active";
    }

    /****************************************/

    selectedItem: NavBarItem<TContent> = null;

    itemTemplate: CatalogTemplate<IView> = "IconTextView";

    itemBehavoirs: (IBehavoir | string)[] = [];
}

registerType(NavBarItem, "NavBarItem");

registerType(NavBar, "NavBar");