import { LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { Bindable } from "../../Templating/Properties";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";
import { ContentView, IContentViewConfig } from "./ContentView";
import "Templates/Container.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface IContainerConfig<TContent> extends IContentViewConfig<TContent> {

    title?: Bindable<LocalString>;

    isExpanded?: Bindable<boolean>;

    canExpand?: boolean;
}

/********************************************/

export class Container<TContent = any> extends ContentView<TContent> implements IHTMLAttach {

    protected _element: HTMLDivElement;

    constructor(config?: IContainerConfig<TContent>) {
        super({ template: "Container", ...config });

        this.bindConfigString("title", config, StringUsage.Title);

        this.bindConfig("isExpanded", config);

        this.bindConfig("canExpand", config);

        this.prop("content").subscribe(() => this.checkOverflow());
    }

    /********************************************/

    attach(element: HTMLElement): void {

        this._element = <HTMLDivElement>element;

        if (this.canExpand) {
            this._element.addEventListener("resize", () => this.checkOverflow());
            this.checkOverflow();
        }
    }

    /********************************************/

    checkOverflow() {
        if (this._element != null) {
            const wrapper = this._element.querySelector(".content-wrapper");
            if (wrapper != null) {
                this.isOverflow = wrapper.scrollHeight > wrapper.clientHeight;
                if (!this.isOverflow)
                    this.isExpanded = true;
            }
        }
    }

    /********************************************/

    protected onContentChanged() {

    }

    /********************************************/

    toggleExpand() {
        this.isExpanded = !this.isExpanded;
    }

    /********************************************/

    title: string = "";

    isOverflow: boolean = true;

    canExpand: boolean = false;

    isExpanded: boolean = true;
}


registerType(Container, "Container");
