import { linq } from "../../Core/Linq";
import { LocalString } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { IView } from "../Abstraction/IView";
import { ContentView, IContentViewConfig } from "./ContentView";
import { IItemsViewConfig, ItemsView } from "./ItemsView";
import { TextView } from "./TextView";

interface ISectionViewConfig<TContent extends IView, THeader extends IView> extends IContentViewConfig<TContent> {

    header?: LocalString | THeader;
}

/********************************************/

export class SectionView<TContent extends IView = any, THeader extends IView = any> extends ContentView<TContent> {

    constructor(config?: ISectionViewConfig<TContent, THeader>) {

        super(config)

        if (typeof config?.header == "string")
            this.header = <any>new TextView({ content: config.header });
    }

    /********************************************/

    header: THeader;
}

/********************************************/

export interface ISectionsViewConfig extends IItemsViewConfig<SectionView<any, any>> {

    sections?: ISectionViewConfig<any, any>[];
}

/********************************************/

export class SectionsView extends ItemsView<SectionView<IView, IView>> {

    constructor(config?: ISectionsViewConfig) {

        super(config);

        if (config?.sections)
            config.sections.forEach(a => this.content.add(new SectionView(a)));
    }

    /********************************************/

    section(name: string) {
        return linq(this.content).first(a => a.name == name);
    }
}

registerType(SectionsView, "SectionsView");