import { LocalString } from "../../Core/Types/DynamicString";
import { EqualityComparer } from "../../Core/Types/Types";
import { Item } from "./Item";

export interface IItemsSourceConfig<TItem, TValue, TFilter> {

    getItemsAsync?: (filter: TFilter) => Promise<TItem[]>;

    getItemByValueAsync?: (value: TValue) => Promise<TItem>;

    getItemText?: (item: TItem) => string;

    getItemValue?: (item: TItem) => TValue;

    countAsync?: (filter: TFilter) => number;

    getFilter?: (text?: string, offset?: number, pageSize?: number) => TFilter;

    equals?: (a: TValue, b: TValue) => boolean

    displayName?: LocalString;

    typeName?: string;

    allowZero?: boolean;
}

/****************************************/

export class ItemsSource<TItem, TValue = any, TFilter = any> {

    protected _typeName: string;
    protected _allowZero: boolean;

    constructor(config?: IItemsSourceConfig<TItem, TValue, TFilter>) {

        if (config) {

            if (config.getItemsAsync)
                this.getItemsAsync = config.getItemsAsync;

            if (config.allowZero !== undefined)
                this._allowZero = config.allowZero;

            if (config.getItemValue)

                this.getItemValue = (a, bindItem) => {

                    if (bindItem === false)
                        return config.getItemValue(a);

                    if (!a && (a as any !== 0 || !config.allowZero))
                        return undefined;
                    const value = config.getItemValue(a);
                    if (value === null)
                        return null;
                    return Item.create({ type: this.typeName, value: value, text: this.getItemText(a), item: a });
                }

            if (config.getItemText)
                this.getItemText = config.getItemText;

            if (config.getFilter)
                this.getFilter = config.getFilter;

            if (config.getItemByValueAsync) {

                this.getItemByValueAsync = value => {

                    const item = Item.getItem<TItem>(value);

                    if (item)
                        return Promise.resolve(item);

                    return config.getItemByValueAsync(value);
                };
            }

            if (config.equals)
                this.equals = config.equals;

            this._typeName = config.typeName;

            this.displayName = config.displayName;
        }
    }

    /****************************************/

    getItemsAsync(filter?: TFilter): Promise<TItem[]> {

        return Promise.resolve([]);
    }

    /****************************************/

    getItemText(item: TItem): string {

        if (item)
            return item.toString();
    }

    /****************************************/

    getItemValue(item: TItem, bindItem?: boolean): TValue {
        return <any>item;
    }

    /****************************************/

    getFilter(text?: string, offset?: number, pageSize?: number): TFilter {

        return <TFilter>{};
    }

    /****************************************/

    async countAsync(filter: TFilter): Promise<number> {

        return (await this.getItemsAsync(filter)).length;
    }

    /****************************************/

    getItemByValueAsync(value: TValue): Promise<TItem> {

        const item = Item.getItem<TItem>(value);

        if (item)
            return Promise.resolve(item);

        return Promise.resolve(value as unknown as TItem);
    }

    /****************************************/

    equals(a: TValue, b: TValue): boolean {

        if (a == b)
            return true;
        if (a != undefined && a.valueOf && b != undefined && b.valueOf)
            return a.valueOf() == b.valueOf();
        return false;
    }

    /****************************************/

    get itemComparer(): EqualityComparer<TItem> {

        return (a, b) => this.equals(this.getItemValue(a), this.getItemValue(b));
    }

    /****************************************/

    get typeName() {
        return this._typeName;
    }

    /****************************************/

    get allowZero() {
        return this._allowZero;
    }

    /****************************************/

    displayName: LocalString;
}