import { IBehavoir } from "../../Templating/Abstraction/IBehavoir";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";
import { IView } from "../Abstraction/IView";

export class AttachBehavoir implements IBehavoir<HTMLElement, IView & IHTMLAttach> {

    attach(element: HTMLElement, viewModel?: IView & IHTMLAttach) {

        if ("attach" in viewModel) {
            setTimeout(() => viewModel.attach(element));
        }
    }

    /****************************************/

    detach(element: HTMLElement, viewModel?: IView & IHTMLAttach) {
    }

    /****************************************/

    static readonly instance = new AttachBehavoir();
}