import { MONTHS } from "../../Core/Utils/DateUtils";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { BaseEditor, IBaseEditorConfig } from "./BaseEditor";

export interface IDateEditorComboConfig extends IBaseEditorConfig<Date> {

    minYear?: number;

    maxYear?: number;
}

interface IMonthView {
    number: string;
    name: string;
}

/********************************************/

export class DateEditorCombo extends BaseEditor<Date> {

    constructor(config?: IDateEditorComboConfig) {
        super(config)

        for (let i = 1; i <= 31; i++)
            this.days.push(i.toString());

        for (let i = 1; i <= 12; i++)
            this.months.push({ number: i.toString(), name: MONTHS[i - 1] });

        const maxYear = config?.maxYear ?? new Date().getFullYear();

        const minYear = config?.minYear ?? new Date().getFullYear() - 100;

        for (let i = minYear; i <= maxYear; i++)
            this.years.push(i.toString());

        this.prop("selectedDay").subscribe(() => this.tryCommit());
        this.prop("selectedMonth").subscribe(() => this.tryCommit());
        this.prop("selectedYear").subscribe(() => this.tryCommit());


    }

    /********************************************/

    protected tryCommit() {
        this.commitAsync();
    }

    /********************************************/

    commitAsync(force?: boolean) {
        if (this.editValue?.getTime() != this.value?.getTime())
            this.value = this.editValue;
        return Promise.resolve(true);
    }

    /********************************************/

    get editValue(): Date {

        if (!this.selectedDay || !this.selectedMonth || !this.selectedYear)
            return null;
        return new Date(parseInt(this.selectedYear), parseInt(this.selectedMonth) - 1, parseInt(this.selectedDay));
    }

    /********************************************/

    set editValue(value: Date) {

        if (value?.getTime() == this.editValue?.getTime())
            return;

        if (!value) {
            this.selectedDay = null;
            this.selectedMonth = null;
            this.selectedYear = null;
        }
        else {
            this.selectedDay = (value.getDate()).toString();
            this.selectedMonth = (value.getMonth() + 1).toString();
            this.selectedYear = value.getFullYear().toString();
        }
    }

    /********************************************/

    selectedDay: string = null;

    selectedMonth: string = null;

    selectedYear: string = null;

    days: string[] = [];

    months: IMonthView[] = [];

    years: string[] = [];
}

registerType(DateEditorCombo, "DateEditorCombo");