import { Format } from "../../Core/Helpers/Format";
import { IViewContent } from "../Abstraction/IViewContent";
import { IViewContentHost } from "../Abstraction/IViewContentHost";
import { IViewContentInfo } from "../Abstraction/IViewContentInfo";
import { IViewContentProvider } from "../Abstraction/IViewContentProvider";
import { ActionView } from "../Components/ActionView";
import { IconTextView } from "../Components/IconTextView";
import { TextView } from "../Components/TextView";
import { View } from "../Components/View";
import { IMessageOptions } from "../Helpers/Interaction";

export class MessageContent implements IViewContentProvider {

    protected _options: IMessageOptions;

    /****************************************/

    constructor(options: IMessageOptions) {

        this._options = options;
    }

    /****************************************/

    async getContentAsync(host: IViewContentHost) {

        const views: View[] = [];

        views.push(new IconTextView({
            icon: this._options.icon,
            text: this._options.message,
            styles: ["message-view"]
        }));

        if (this._options.customActions) {
            this._options.customActions.forEach(action => views.push(new ActionView({
                name: action.name,
                template: "ActionButton",
                styles: ["primary"],
                content: new TextView({ content: Format.action(action.displayName) }),
                executeAsync: async () => {
                    await host.closeAsync();
                    await action.executeAsync();
                }
            })));
        }

        return Promise.resolve({
            views: views,
            title: this._options.title,
            styles: ["vertical", "margin-items-v", "scroll", "fit-items-h", "surface", "padding", "small"],
        } as IViewContent);
    }

    /****************************************/

    get info() {
        return {
            name: "message",
            icon: null,
            displayName: this._options.title
        } as IViewContentInfo;
    }
}