import { IDictionary } from "../../Core/Abstraction/IDictionary";
import { Format } from "../../Core/Helpers/Format";
import { DynamicString, LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { IAction } from "../Abstraction/IAction";
import { IAsyncLoad } from "../Abstraction/IAsyncLoad";
import { IValidable } from "../Abstraction/IValidator";
import { IView } from "../Abstraction/IView";
import { isAsyncLoad, isValidable } from "../Helpers/TypeCheck";
import { ActionView } from "./ActionView";
import { ContentView, IContentViewConfig } from "./ContentView";
import { IconTextView } from "./IconTextView";
import { IItemsViewConfig, ItemsView } from "./ItemsView";


interface IWizardStepConfig<TContent extends IView> extends IContentViewConfig<TContent> {

    loadAsync?(step: WizardStepView<TContent>): Promise<void>;

    validateAsync?(step: WizardStepView<TContent>): Promise<boolean>;

    title?: Bindable<LocalString>;

    actions?: IAction[];

    nextLabel?: LocalString;

    coverImage?: string;

    index?: number;
}

/****************************************/

export class WizardStepView<TContent extends IView> extends ContentView<TContent> implements IValidable {

    constructor(config?: IWizardStepConfig<TContent>) {

        super({ template: "ContentView", ...config });

        this.bindConfig("index", config);

        this.bindConfig("coverImage", config);

        this.bindConfigString("title", config, StringUsage.Title);

        this.bindConfigString("nextLabel", config, StringUsage.Action);

        if (config) {

            if (config.loadAsync)
                this.loadAsync = () => config.loadAsync(this);

            if (config.validateAsync)
                this.validateAsync = force => config.validateAsync(this);

            if (config.actions)
                config.actions.forEach(a => this.addAction(a));

        }
    }

    /****************************************/

    addAction(action: IAction) {
        let view = ActionView.fromAction(action, { template: "ActionButton", styles: ["primary"] });
        this.actions.add(view);
        return view;
    }

    /****************************************/

    loadAsync(): Promise<void> {

        if (isAsyncLoad(this.content))
            return this.content.loadAsync();
        return Promise.resolve();
    }

    /****************************************/

    async validateAsync(force?: boolean) {

        if (isValidable(this.content))
            this.isValid = await this.content.validateAsync(force);
        return this.isValid;
    }

    /****************************************/

    select(): void {

        (this.parentView as WizardView).selectStepAsync(this.index);
    }

    /****************************************/

    error: string = null;

    isValid: boolean = true;

    index: number = 0;

    title: string = "";

    isActive: boolean = false;

    nextLabel: string = null;

    coverImage: string = null;

    readonly actions: IObservableList<ActionView> = observableListOf();
}

/****************************************/

interface IWizardConfig extends IItemsViewConfig<WizardStepView<any>> {

    steps?: IWizardStepConfig<any>[];

    finishAsync?: () => Promise<any>;

    finishLabel?: LocalString;

    nextLabel?: LocalString;

    prevLabel?: LocalString;
}

/****************************************/
/* WizardViewModel
/****************************************/

export class WizardView extends ItemsView<WizardStepView<any>> implements IAsyncLoad {

    constructor(config?: IWizardConfig) {

        super({ template: "WizardView", ...config });

        this.actions = {
            next: new ActionView({
                name: "next",
                template: "ActionButton",
                styles: ["secondary"],
                content: new IconTextView({
                    template: "IconTextViewInline",
                    text: this.prop("nextLabel")
                }),
                executeAsync: () => this.nextAsync()
            }),
            prev: new ActionView({
                name: "prev",
                template: "ActionButton",
                styles: ["secondary"],
                content: new IconTextView({
                    template: "IconTextViewInline",
                    text: this.prop("prevLabel")
                }),
                executeAsync: () => this.previousAsync()
            }),
            finish: new ActionView({
                name: "finish",
                template: "ActionButton",
                styles: ["secondary"],
                content: new IconTextView({
                    template: "IconTextViewInline",
                    text: this.prop("finishLabel")
                }),
                executeAsync: () => this.finishAsync()
            })
        }

        this.prop("currentStepIndex").subscribe((value, oldValue) => {

            if (oldValue != -1)
                this.content.get(oldValue).isActive = false;

            this.content.get(value).isActive = true;

            this.currentView = this.content.get(value)

            this.actions.next.visible = this.hasNextStep();

            this.actions.prev.visible = this.hasPrevStep();

            this.actions.finish.visible = !this.hasNextStep();

            if (this.currentView.nextLabel)
                this.nextLabel = this.currentView.nextLabel;
            else
                this.nextLabel = Format.action(config?.nextLabel ?? "wizard-next");
        });

        this.bindConfigString("nextLabel", config, StringUsage.Action);

        this.bindConfigString("prevLabel", config, StringUsage.Action);

        this.bindConfigString("finishLabel", config, StringUsage.Action);

        if (config) {

            if (config.steps) {
                config.steps.forEach((step, i) =>
                    this.content.add(new WizardStepView({ ...step, index: i })));

                this.currentStepIndex = 0;
            }

            if (config.finishAsync)
                this.doFinishAsync = config.finishAsync;
        }

        if (!config?.prevLabel)
            this.prevLabel = Format.action("wizard-prev");

        if (!config?.finishLabel)
            this.prevLabel = Format.action("wizard-finish");
    }

    /****************************************/

    async finishAsync() {

        if (await this.validateUntilAsync(this.content.count - 1) !== true)
            return;

        await this.doFinishAsync();
    }

    /****************************************/

    protected async doFinishAsync() {

        return Promise.resolve();
    }

    /****************************************/

    clearErrors() {
    }

    /****************************************/

    async validateUntilAsync(targetIndex: number): Promise<boolean | number> {

        let curStep = this.currentStepIndex;

        while (curStep <= targetIndex) {
            if (! await this.content.get(curStep).validateAsync())
                return curStep;
            curStep++;
        }

        return true;
    }

    /****************************************/

    async loadAsync() {

        if (this.content.count > 0)
            await this.content.get(this.currentStepIndex).loadAsync();
    }

    /****************************************/

    async selectStepAsync(index: number) {

        if (index == this.currentStepIndex)
            return;

        this.clearErrors();

        if (index > this.currentStepIndex) {

            let result = await this.validateUntilAsync(index - 1);

            if (result === true) {
                this.currentStepIndex = index;
                await this.content.get(index).loadAsync();

            }
            else {

                this.currentStepIndex = result as number;

                if (result != this.currentStepIndex)
                    await this.content.get(result as number).loadAsync();
            }
        }
        else
            this.currentStepIndex = index;
    }

    /****************************************/

    async nextAsync() {

        if (this.currentStepIndex + 1 < this.content.count)
            await this.selectStepAsync(this.currentStepIndex + 1);
    }

    /****************************************/

    async previousAsync() {

        if (this.currentStepIndex > 0)
            await this.selectStepAsync(this.currentStepIndex - 1);
    }

    /****************************************/

    async endAsync() {

        await this.selectStepAsync(this.content.count - 1);
    }

    /****************************************/

    hasPrevStep(): boolean {
        let curIndex = this.currentStepIndex - 1;
        while (curIndex >= 0) {
            if (this.content.get(curIndex).visible)
                return true;
            curIndex--;
        }
        return false;
    }

    /****************************************/

    hasNextStep(): boolean {
        let curIndex = this.currentStepIndex + 1;
        while (curIndex <= this.content.count - 1) {
            if (this.content.get(curIndex).visible)
                return true;
            curIndex++
        }
        return false;
    }

    /****************************************/

    isComplete(): boolean {
        return !this.hasNextStep();
    }

    /****************************************/

    currentStepIndex: number = -1;

    currentView: WizardStepView<any>;

    actions: IDictionary<ActionView>;

    nextLabel: string = "Next";

    prevLabel: string = "Previous";

    finishLabel: string = "Finish";
}

registerType(WizardStepView, "WizardStepView");

registerType(WizardView, "WizardView");