import { OperationType } from "../../Core/Abstraction/IOperation";
import { Services } from "../../Core/Global";

export function loadImageAsync(src: string): Promise<HTMLImageElement> {

    const op = Services.operation.begin({ message: "Loading image: " + src, type: OperationType.Local });

    return new Promise((res, rej) => {

        const img = document.createElement("img");

        img.addEventListener("load", () => {
            res(img);
            op.end()
        });

        img.addEventListener("error", ev => {
            rej(ev.error)
            op.end();
        });

        img.src = src;

    });
}