import { IList } from "../Core/Abstraction/IList";
import { EqualityComparer } from "../Core/Types/Types";
import { IObservableList, IObservableListHandler } from "./Abstraction/IObservableList";

export class ObservableList<T> implements IObservableList<T> {

    protected _items: T[] = [];
    protected _handlers: IObservableListHandler<T>[];

    /****************************************/

    constructor(items?: T[], equalityComparer?: EqualityComparer<T>) {

        this._items = items || [];
        this.equalityComparer = equalityComparer;
    }

    /****************************************/

    add<T2 extends T>(item: T2): T2 {
        this._items.push(item);
        this.invoke(a => a.onItemAdded ? a.onItemAdded(item, this._items.length - 1, "add") : undefined);
        this.invoke(a => a.onChanged ? a.onChanged() : undefined);
        return item;
    }

    /****************************************/

    addRange(items: Iterable<T>) {
        for (let item of items) {
            this._items.push(item);
            this.invoke(a => a.onItemAdded ? a.onItemAdded(item, this._items.length - 1, "add") : undefined);
        }
        this.invoke(a => a.onChanged ? a.onChanged() : undefined);
    }

    /****************************************/

    insert<T2 extends T>(index: number, item: T2): T2 {
        this._items.splice(index, 0, item);
        this.invoke(a => a.onItemAdded ? a.onItemAdded(item, index, "insert") : undefined);
        this.invoke(a => a.onChanged ? a.onChanged() : undefined);
        return item;
    }

    /****************************************/

    remove(item: T) {
        const index = this.indexOf(item);
        if (index != -1)
            this.removeAt(index);
    }

    /****************************************/

    removeWhen(condition: (T) => boolean) {
        for (let i = this._items.length - 1; i >= 0; i--) {
            if (condition(this._items[i]))
                this.removeAt(i);
        }
    }

    /****************************************/

    removeRange(startIndex: number, count: number) {

        const items = this._items.splice(startIndex, count);

        let isChanged = false;

        for (let i = items.length - 1; i >= 0; i--) {
            this.invoke(a => a.onItemRemoved ? a.onItemRemoved(items[i], startIndex + i, "remove") : undefined);
            isChanged = true;
        }

        if (isChanged)
            this.invoke(a => a.onChanged ? a.onChanged() : undefined);

        return items;
    }

    /****************************************/

    removeAt(index: number): T {
        const item = this._items.splice(index, 1)[0];
        this.invoke(a => a.onItemRemoved ? a.onItemRemoved(item, index, "remove") : undefined);
        this.invoke(a => a.onChanged ? a.onChanged() : undefined);
        return item;
    }

    /****************************************/

    clear() {

        if (this._items.length == 0)
            return;

        for (let i = this._items.length - 1; i >= 0; i--)
            this.invoke(a => a.onItemRemoved ? a.onItemRemoved(this._items[i], i, "clear") : undefined);

        this._items.splice(0, this._items.length);
        this.invoke(a => a.onClear ? a.onClear() : undefined);
        this.invoke(a => a.onChanged ? a.onChanged() : undefined);
    }

    /****************************************/

    contains(item: T): boolean {
        return this.indexOf(item) != -1;
    }

    /****************************************/

    indexOf(item: T): number {

        if (this.equalityComparer) {

            for (let i = 0; i < this._items.length; i++) {
                if (this.equalityComparer(item, this._items[i]))
                    return i;
            }
            return -1;
        }

        return this._items.indexOf(item);
    }

    /****************************************/

    *[Symbol.iterator](): Iterator<T> {
        for (let item of this._items)
            yield item;
    }

    /****************************************/

    toArray(): T[] {
        return this._items;
    }

    /****************************************/

    get(index: number): T {
        return this._items[index];
    }

    /****************************************/

    set(index: number, value: T) {
        const oldItem = this._items[index];
        if (oldItem == value)
            return;
        this.invoke(a => a.onItemRemoved ? a.onItemRemoved(oldItem, index, "replace") : undefined);
        this._items[index] = value;
        this.invoke(a => a.onItemAdded ? a.onItemAdded(value, index, "replace") : undefined);
        this.invoke(a => a.onItemReplaced ? a.onItemReplaced(value, oldItem, index) : undefined);
        this.invoke(a => a.onChanged ? a.onChanged() : undefined);
    }

    /****************************************/

    swap(index: number, newIndex: number): void {
        const temp = this._items[newIndex];
        this._items[newIndex] = this._items[index];
        this._items[index] = temp;
        this.invoke(a => a.onItemSwap ? a.onItemSwap(index, newIndex) : undefined);
        this.invoke(a => a.onChanged ? a.onChanged() : undefined);
    }

    /****************************************/

    subscribe(handler: IObservableListHandler<T>) {
        if (!this._handlers)
            this._handlers = [];
        const index = this._handlers.indexOf(handler);
        if (index == -1)
            this._handlers.push(handler);
        return handler;
    }

    /****************************************/

    unsubscribe(handler: IObservableListHandler<T>) {
        if (!this._handlers)
            return;
        const index = this._handlers.indexOf(handler);
        if (index != -1)
            this._handlers.splice(index, 1);
    }

    /****************************************/

    forEach(action: (item: T, index?: number) => void) {
        let index = 0;
        for (let item of this) {
            action(item, index);
            index++;
        }

    }

    /****************************************/

    protected invoke(action: (hander: IObservableListHandler<T>) => void) {

        if (!this._handlers)
            return;
        this._handlers.forEach(handler =>
            action(handler));
    }

    /****************************************/

    get count() {
        return this._items.length;
    }

    toJSON() {
        return this._items;
    }

    /****************************************/

    equalityComparer: EqualityComparer<T>;
}
/****************************************/

export function listOf<T>(items?: T[]): IList<T> {
    return new ObservableList(items);
}

/****************************************/

export  function observableListOf<T>(items?: T[], equalityComparer?: EqualityComparer<T>): IObservableList<T> {
    return new ObservableList(items, equalityComparer);
}
