import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { ICloneable } from "../Abstraction/ICloneable";
import { IList } from "../Abstraction/IList";
import { IStateManager } from "../Abstraction/IStateManager";
import { Guid } from "../Types/Types";

const GUID_REGEXP = /^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$/i;

export function isList(obj: any): obj is IList<any> {

    return obj && typeof obj == "object" && "add" in obj && "get" in obj && "count" in obj;
}

/****************************************/

export function isCloneable(obj: any): obj is ICloneable {

    return obj && typeof obj == "object" && "clone" in obj && typeof obj["clone"] == "function";
}

/****************************************/

export function isObservableList(obj: any): obj is IObservableList<any> {

    return isList(obj) && "subscribe" in obj;
}

/****************************************/

export function isString(obj: any): obj is string {

    return typeof obj == "string";
}

/****************************************/

export function isFunction(obj: any): obj is Function {

    return typeof obj == "function";
}

/****************************************/

export function isObject(obj: any): obj is object {

    return typeof obj == "object";
}

/****************************************/

export function isStateManager(obj: any): obj is IStateManager {

    return obj && typeof obj == "object" && "setState" in obj && "getState" in obj;
}


export function isGuid(obj: any): obj is Guid {

    return GUID_REGEXP.test(obj?.toString());

}
