import { LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { equals, registerType } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { IAsyncLoad } from "../Abstraction/IAsyncLoad";
import { IEditor } from "../Abstraction/IEditor";
import { IHasLabel } from "../Abstraction/IHasLabel";
import { IValidationContext, IValidator } from "../Abstraction/IValidator";
import { NumberEditor } from "../Editors/NumberEditor";
import { IViewComponentConfig, ViewComponent } from "./ViewComponent";
import "Templates/PropertyView.html";
import { Format } from "../../Core/Helpers/Format";
import { IPropertiesHost } from "../Abstraction/IPropertiesHost";

type NewType<TValue, TObj extends object> = IValidator<TValue, TObj>;

export type PropertyChangeReason = "editor" | "setter";

export interface IPropertyViewConfig<TValue, TObj extends object = any> extends IViewComponentConfig {

    host?: IPropertiesHost<TObj>;

    editor: Bindable<IEditor<TValue, any>>;

    value?: Bindable<TValue>;

    label?: Bindable<LocalString>;

    readonly?: Bindable<boolean>;

    showLabel?: Bindable<boolean>;

    validators?: NewType<TValue, TObj>[];

    onChanged?: (newValue: TValue, oldValue: TValue, target: TObj, reason: PropertyChangeReason) => void;
}

/********************************************/

export class PropertyView<TValue, TEditor extends IEditor<TValue, any> = IEditor<TValue, any>, TObj extends object = any> extends ViewComponent implements IHasLabel, IAsyncLoad {

    protected _lastValidationValue: TValue = undefined;

    constructor(config?: IPropertyViewConfig<TValue, TObj>) {

        super({ template: "PropertyView", ...config });

        this.bindConfig("host", config);

        this.bindConfig("editor", config);

        this.bindConfig("readonly", config);

        this.bindConfig("showLabel", config);

        this.bindConfig("value", config);

        this.bindConfigString("label", config, StringUsage.Label);

        this.prop("error");

        if (config) {

            if (config.onChanged)
                this.prop("value").subscribe((o, n) => config.onChanged(o, n, this.host.editValue, "editor"));

            if (config.validators)
                this.validators = config.validators;
        }

        this.prop("editor").subscribe(() => this.bindEditor());

        this.bindEditor();
    }

    /********************************************/

    protected bindEditor() {

        if (this.editor) {
            this.editor.name = this.name;
            this.editor.value = this.value;

            this.bind("value", this.editor.prop("value"));

            this.bind("isDirty", this.editor.prop("isDirty"));

            if ("label" in this.editor) {
                this.bind("label", (this.editor as unknown as IHasLabel & IEditor<TValue, any>).prop("label"));
                this.showLabel = false;
            }

            this.editor.prop("isValid").subscribe(value => {

                if (!value)
                    this.isValid = false;

                this.error = this.editor.error;
            });
        }
    }

    /********************************************/

    clearError() {

        this.editor.error = null;
        this.error = null;
    }

    /********************************************/

    async validateAsync(target: TObj, force?: boolean): Promise<boolean> {

        console.group("begin validation: " + this.label, " - needValidation: ", this.isDirty);

        try {

            //TODO fix this, sometimes is editValue other time value
            const curValue = this.editor instanceof NumberEditor ? this.editor.value : this.editor.editValue;

            if (!force && this._lastValidationValue !== undefined && equals(this._lastValidationValue, curValue))
                return this.isValid;

            this.clearError();

            this._lastValidationValue = curValue;

            if (!await this.editor.validateAsync(force)) {

                this.isValid = false;
                this.error = this.editor.error;
                return false;
            }

            const ctx: IValidationContext<TValue, TObj> = {
                value: curValue,
                fieldName: this.label,
                target: target
            };

            for (let validator of this.validators) {
                const result = await validator.validateAsync(ctx);
                if (!result.isValid) {
                    this.error = Format.message(result.error);
                    this.isValid = false;
                    return false;
                }
            }

            this.isValid = true;
            return true;
        }
        finally {
            console.groupEnd();
        }
    }

    /********************************************/

    loadAsync() {

        return this.editor.loadAsync();
    }

    /********************************************/

    clear() {
        this.editor.clear();
        this.clearError();
    }

    /********************************************/

    isDirty: boolean = null;

    isValid: boolean = true;

    value: TValue = null;

    editor: TEditor = null;

    error: string = null;

    label: string = null;

    readonly: boolean = false;

    showLabel: boolean = true;

    host: IPropertiesHost<TObj> = null;

    validators: IValidator<TValue, TObj>[] = [];
}


registerType(PropertyView, "PropertyView");