import { INamedContent } from "../../Core/Abstraction/INamedContent";
import { OperationType } from "../../Core/Abstraction/IOperation";
import { Format } from "../../Core/Helpers/Format";
import { DynamicString, LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { ActionPriority } from "../Abstraction/IAction";
import { IAsyncEditor } from "../Abstraction/IAsyncEditor";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { IView } from "../Abstraction/IView";
import { ActionView } from "../Components/ActionView";
import { SelectableItemView } from "../Components/SelectableItemView";
import { TextView } from "../Components/TextView";
import { ActivityEditor } from "../Editors/ActivityEditor";
import { isAsyncEditor } from "../Helpers/TypeCheck";
import { EditableItemsSource } from "../Items/EditableItemsSource";
import { ISearchActivityConfig, SearchActivity } from "./SearchActivity";

export interface ISelectSingleItemActivityConfig<TItem, TValue, TFilter, TEdit> extends ISearchActivityConfig<TItem, IView> {

    itemsSource?: EditableItemsSource<TItem, TValue, TFilter, TEdit>;

    filters?: INamedContent<TFilter>[];

    createItemEditor?: () => IItemEditor<TItem, TEdit> | IAsyncEditor<TItem, TEdit>;

    createItemContentView?: (item: TItem) => IView;

    canAdd?: boolean;

    addLabel?: LocalString;

    selectedValue?: TValue;
}

/****************************************/

export class SelectSingleItemActivity<TItem, TValue, TFilter, TEdit = TItem> extends SearchActivity<TItem, IView, TItem>    {

    constructor(config?: ISelectSingleItemActivityConfig<TItem, TValue, TFilter, TEdit>) {

        super(config);

        this.bindConfig("canAdd", config);

        this.bindConfig("itemsSource", config);

        this.bindConfig("selectedValue", config);

        this.bindConfigString("addLabel", config);

        if (config) {

            if (config.createItemView)
                this.createItemContentView = config.createItemContentView;

            if (config.createItemEditor)
                this.createItemEditor = config.createItemEditor;

            if (config.filters)
                this.filters.addRange(config.filters);
        }

        if (!config?.tooltip)
            this.tooltip = Format.tooltip("select-item", this.itemsSource.displayName);

        if (!config?.addLabel)
            this.addLabel = Format.action("new-item", this.itemsSource.displayName);
    }

    /****************************************/

    protected async createAsync() {

        await super.createAsync();

        this._actionBar.addAction({
            name: "confirm",
            icon: "fas fa-check",
            displayName: "confirm",
            executeAsync: () => this.confirmAsync(),
            priority: ActionPriority.Primary
        });
    }

    /****************************************/

    async confirmAsync() {

        await this.closeAsync();
    }

    /****************************************/

    protected configureListView() {

        const header: IView[] = [];

        if (this.canAdd)
            header.push(ActionView.fromAction({
                name: "add-item",
                icon: "fas fa-plus",
                operation: OperationType.Local,
                executeAsync: () => this.addItemAsync(),
                displayName: this.addLabel
            }));

        const result = <any>{
            header: header,
        };

        if (this.pageSize > 0)
            result.itemsSource = this.itemsSource;

        return result;
    }

    /****************************************/

    protected searchWorkAsync(query: string) {

        return this.itemsSource.getItemsAsync(this.getFilter(query));
    }

    /****************************************/

    protected getFilter(query: string, offset?: number, pageSize?: number) {

        let filter = this.itemsSource.getFilter(query, offset, pageSize);
        if (this.filters.count == 1)
            filter = { ...filter, ...this.filters.get(0).content };
        return filter;
    }

    /****************************************/

    protected createItemContentView(item: TItem): IView {

        return new TextView({ content: this.itemsSource.getItemText(item) });
    }

    /****************************************/

    protected createItemEditor(): IItemEditor<TItem, TEdit> | IAsyncEditor<TItem, TEdit> {

        throw "Not Supported";
    }

    /****************************************/

    protected createItemView(item: TItem) {

        const view = new SelectableItemView({
            content: this.createItemContentView(item),
            styles: ["single"],
            item: item
        });

        if (this.itemsSource.equals(this.itemsSource.getItemValue(item), this.selectedValue))
            view.isSelected = true;

        view.prop("isSelected").subscribe(value => {

            if (value)
                this.setSelectionAsync(item);
        });

        return view;
    }

    /****************************************/

    protected setSelectionAsync(item: TItem) {

        return this.closeAsync(item);
    }

    /****************************************/

    async addItemAsync() {

        const editor = this.createItemEditor();

        let editValue: TEdit;

        if (isAsyncEditor(editor))

            editValue = await editor.editAsync(this.itemsSource.newItem());

        else {
            const activity = new ActivityEditor<TItem, TEdit>({
                editor: editor,
                title: this.addLabel
            });

            editValue = await activity.editAsync(this.itemsSource.newItem());
        }

        if (!editValue)
            return;

        const newItem = await this.itemsSource.addItemAsync(editValue);

        if (newItem)
            await this.setSelectionAsync(newItem);
    }

    /****************************************/

    canAdd: boolean = false;

    itemsSource: EditableItemsSource<TItem, TValue, TFilter, TEdit> = null;

    addLabel: string = "Add New";

    filters: IObservableList<INamedContent<TFilter>> = observableListOf();

    selectedValue: TValue = null;

}

registerType(SelectSingleItemActivity, "SelectSingleItemActivity");