import { IAppArgs, IApplication } from "./Abstraction/IApplication";
import { Type } from "./Types/Types";

export var App: IApplication<IAppArgs> = null;

export const Services: IServices = {} as IServices;

/****************************************/

export async function runAsync<TApp extends IApplication<TArgs>, TArgs extends IAppArgs>(app: Type<TApp> | TApp, args: TArgs, action: () => void) {

    if (typeof app == "function")
        App = new app();
    else
        App = app;

    window.addEventListener("load", async () => {

        await App.runAsync(args);

        action();
    });
}


/****************************************/

export async function install(workerUri: string, versionUri: string) {

    if ("serviceWorker" in navigator) {

        const curVer = localStorage.getItem("AppVer");
        const newVer = JSON.parse(await (await fetch(versionUri)).text());
        const curReg = await navigator.serviceWorker.getRegistration(workerUri);

        if (curVer != newVer || !curReg) {

            const registrations = await navigator.serviceWorker.getRegistrations();
            for (let registration of registrations)
                await registration.unregister();

            await navigator.serviceWorker.register(workerUri);

            localStorage.setItem("AppVer", newVer);

            if (curVer && curVer != newVer) {
                //Interaction.info("msg-upgrading");
                document.location.reload();
            }
        }
    };
}