import { getTypeName, registerType } from "../../Core/Utils/ObjectUtils";
import { CatalogTemplate } from "../../Templating/Abstraction/ITemplateProvider";
import { BindableObject } from "../../Templating/BindableObject";
import { Bindable } from "../../Templating/Properties";
import { IView } from "../Abstraction/IView";

export interface IViewConfig {

    template?: Bindable<CatalogTemplate<IView>>;

    parentView?: IView;
}

/********************************************/

export class View extends BindableObject implements IView {

    constructor(config?: IViewConfig) {

        super();

        this.bindConfig("template", config);

        if (!this.template)
            this.template = getTypeName(this);

        if (config) {
            if (config.parentView)
                this.parentView = config.parentView;
        }

        this.debugTypeName();
    }

    /********************************************/

    protected debugTypeName() {

        var curName = this.constructor.name;
        var regName = this.constructor["@typeName"];
        if (!regName)
            console.warn("Type '" + curName + "' is not registered.")
    }

    /********************************************/

    template: CatalogTemplate<this> = null;

    parentView: IView = null;
}


registerType(View, "View");