import { INamedContent } from "../../Core/Abstraction/INamedContent";
import { OperationType } from "../../Core/Abstraction/IOperation";
import { Format } from "../../Core/Helpers/Format";
import { DynamicString, LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { IAsyncEditor } from "../Abstraction/IAsyncEditor";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { IView } from "../Abstraction/IView";
import { ItemSearchMode } from "../Activities/SearchActivity";
import { ActionView } from "../Components/ActionView";
import { TextView } from "../Components/TextView";
import { EditableItemsSource } from "../Items/EditableItemsSource";
import { BaseEditor, IBaseEditorConfig } from "./BaseEditor";

export interface IBasePickerConfig<TItem, TValue, TEditorValue, TFilter, TEdit> extends IBaseEditorConfig<TEditorValue> {

    itemsSource?: EditableItemsSource<TItem, TValue, TFilter, TEdit>;

    filters?: INamedContent<TFilter>[];

    createItemView?: (item: TItem) => IView;

    createItemListView?: (item: TItem) => IView;

    createItemEditor?: () => IItemEditor<TItem, TEdit> | IAsyncEditor<TItem, TEdit>;

    pageSize?: number;

    canAdd?: Bindable<boolean>;

    addLabel?: LocalString;

    pickLabel?: LocalString;

    searchMode?: ItemSearchMode;
}

/****************************************/

export abstract class BasePicker<TItem, TValue, TEditorValue, TFilter = any, TEdit = TItem> extends BaseEditor<TEditorValue> {

    constructor(config?: IBasePickerConfig<TItem, TValue, TEditorValue, TFilter, TEdit>) {

        super(config);

        this.bindConfig("canAdd", config);

        this.bindConfig("itemsSource", config);

        this.bindConfig("pageSize", config);

        this.bindConfigString("addLabel", config);

        this.bindConfigString("pickLabel", config);

        this.bindConfig("searchMode", config);

        if (config) {

            if (config.filters)
                this.filters.addRange(config.filters);

            if (config.createItemView)
                this.createItemView = config.createItemView;

            if (config.createItemListView)
                this.createItemListView = config.createItemListView;

            if (config.createItemEditor)
                this.createItemEditor = config.createItemEditor;
        }

        if (!config?.addLabel)
            this.addLabel = Format.title("new-item", this.itemsSource.displayName);

        if (!config?.pickLabel)
            this.pickLabel = Format.title("select-item", this.itemsSource.displayName);

        this.createEditValueProp();

        this.selectAction = ActionView.fromActionIcon({
            name: "select",
            icon: "fas fa-list",
            operation: OperationType.Local,
            displayName: this.pickLabel,
            executeAsync: () => this.selectAsync()
        });
    }

    /****************************************/

    abstract selectAsync(): Promise<void>;

    /****************************************/

    protected abstract updateViewAsync(): Promise<void>;

    /****************************************/

    protected createItemEditor(): IItemEditor<TItem, TEdit> | IAsyncEditor<TItem, TEdit> {

        throw "Not Supported";
    }

    /****************************************/

    protected createItemView(item: TItem): IView {

        return new TextView({ content: this.itemsSource.getItemText(item) });
    }

    /****************************************/

    protected createItemListView(item: TItem): IView {

        return this.createItemView(item);
    }


    /****************************************/

    protected async notifyEditValueChangedAsync() {

        await super.notifyEditValueChangedAsync();

        await this.updateViewAsync();
    }

    /****************************************/

    canAdd: boolean = false;

    addLabel: string = "Add";

    pickLabel: string = "Select";

    selectAction: ActionView = null;

    itemsSource: EditableItemsSource<TItem, TValue, TFilter, TEdit> = null;

    pageSize: number = 0;

    searchMode?: ItemSearchMode = "default";

    readonly filters: IObservableList<INamedContent<TFilter>> = observableListOf();
}

registerType(BasePicker, "BasePicker");