import { IAsyncEditor } from "../Abstraction/IAsyncEditor";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { IView } from "../Abstraction/IView";
import { SelectSingleItemActivity } from "../Activities/SelectSingleItemActivity";
import { ActionView } from "../Components/ActionView";
import { Actions } from "../Helpers/Actions";
import { BasePicker, IBasePickerConfig } from "./BasePicker";
import "Templates/SingleItemPicker.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface ISingleItemPickerConfig<TItem, TValue, TFilter, TEdit> extends IBasePickerConfig<TItem, TValue, TValue, TFilter, TEdit> {

}

/****************************************/

export class SingleItemPicker<TItem, TValue, TFilter = any, TEdit = TItem> extends BasePicker<TItem, TValue, TValue, TFilter, TEdit> {

    constructor(config?: ISingleItemPickerConfig<TItem, TValue, TFilter, TEdit>) {

        super(config);

        this.clearAction = ActionView.fromActionIcon({
            name: "clear",
            icon: "fas fa-times",
            displayName: "clear",
            executeAsync: () => Promise.resolve(this.clear())
        });
    }

    /****************************************/

    async selectAsync() {

        let mustRefresh = false;

        if (this.selector == null) {
            this.selector = new SelectSingleItemActivity<TItem, TValue, TFilter, TEdit>({
                itemsSource: this.itemsSource,
                createItemEditor: () => this.createItemEditor(),
                createItemContentView: item => this.createItemListView(item),
                pageSize: this.pageSize,
                addLabel: this.addLabel,
                tooltip: this.pickLabel,
                canAdd: this.canAdd
            });

            this.selector.filters = this.filters;
        }
        else
            mustRefresh = true;

        this.selector.selectedValue = this.editValue;

        if (mustRefresh)
            await this.selector.refreshAsync();

        const result = await (await Actions.loadPageAsync(this.selector)).result;

        if (result != null)
            this.editValue = this.itemsSource.getItemValue(result);
    }

    /****************************************/

    protected createItemEditor(): IItemEditor<TItem, TEdit> | IAsyncEditor<TItem, TEdit> {

        throw "Not Supported";
    }


    /****************************************/

    protected async updateViewAsync() {

        if (this.status == "loading")
            return;

        if (this.editValue) {

            this.status = "loading";
            try {
                const item = await this.itemsSource.getItemByValueAsync(this.editValue);
                this.contentView = this.createItemView(item);
            }
            finally {
                this.status = "loaded";
            }
        }
        else
            this.contentView = null;

    }

    /****************************************/

    selector: SelectSingleItemActivity<TItem, TValue, TFilter, TEdit> = null;

    contentView: IView = null;

    clearAction: ActionView = null;
}


registerType(SingleItemPicker, "SingleItemPicker");