import { DynamicString, LocalString } from "../../Core/Types/DynamicString";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { ActionPriority, IAction } from "../Abstraction/IAction";
import { ViewStyle } from "../Abstraction/IView";
import { IViewContentHost } from "../Abstraction/IViewContentHost";
import { IViewContentProvider } from "../Abstraction/IViewContentProvider";
import { ActionView } from "./ActionView";
import { BasePopUpMessage, IBasePopUpMessageConfig } from "./BasePopUpMessage";
import { Panel } from "./Panel";
import "Templates/PopUpContent.html";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { Format } from "../../Core/Helpers/Format";

export interface IPopUpContentConfig extends IBasePopUpMessageConfig {

    contentProvider?: IViewContentProvider;

    title?: Bindable<LocalString>;

    contentStyle?: ViewStyle[]

    closeLabel?: LocalString;
}

/****************************************/

export class PopUpContent<TResult = any> extends BasePopUpMessage implements IViewContentHost {

    protected _resultResolve: (value: TResult) => void;

    constructor(config?: IPopUpContentConfig) {

        super({ template: "PopUpContent", className: "pop-up-content", ...config });

        this.bindConfig("contentProvider", config);

        this.bindConfig("contentStyle", config);

        this.bindConfigString("title", config);

        this.bindConfigString("closeLabel", config);
    }

    /****************************************/

    protected async createAsync() {

        const viewContent = await this.contentProvider.getContentAsync(this);

        if (this.closeLabel) {
            this.addAction({
                name: "close",
                icon: "fas fa-close",
                displayName: this.closeLabel,
                executeAsync: () => this.closeAsync()
            });
        }

        if (viewContent.actions) 
            viewContent.actions.forEach(a => this.addAction(a));

        if (viewContent.title)
            this.title = Format.title(viewContent.title);

        this.contentView.content.clear();

        if (viewContent.views)
            viewContent.views.forEach(a => this.contentView.addView(a));

        if (viewContent.styles && viewContent.styles.length > 0)
            this.contentView.styles = viewContent.styles;
        else
            this.contentView.styles = this.contentStyle;


    }

    /****************************************/

    get result(): Promise<TResult> {
        return new Promise<TResult>(res => this._resultResolve = res);
    }

    /****************************************/

    async openAsync() {

        await this.createAsync();

        await this.contentView.loadAsync();

        this.showAsync(); //Not awaited

        return this;
    }

    /****************************************/

    notifyContentChanged(provider: IViewContentProvider) {

        this.createAsync();
    }

    /****************************************/

    closeAsync(result?: any) {

        if (this._resultResolve)
            this._resultResolve(result);

        this.hide("");

        return Promise.resolve(true);
    }

    /****************************************/

    addAction(action: IAction) {

        let actionView = ActionView.fromAction(action);

        if (action.priority == ActionPriority.Evidence) {
            actionView.styles.push("primary");
            actionView.buildStyles();
        }

        this.actions.add(actionView);

        return actionView;
    }

    /****************************************/

    contentProvider: IViewContentProvider = null;

    readonly actions: IObservableList<ActionView> = observableListOf();

    readonly contentView = new Panel();

    title: string = null;

    contentStyle: ViewStyle[] = ["vertical", "fill-h", "fit-items-h"];

    closeLabel?: string = "Cancel";
}


registerType(PopUpContent, "PopUpContent");