import { Services } from "../../Core/Global";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { IContentView } from "../Abstraction/IContentView";
import { IStaticMapOptions } from "../Abstraction/IMapManager";
import { IViewComponentConfig, ViewComponent } from "./ViewComponent";

export interface IStaticMapViewConfig extends IViewComponentConfig {

    options?: Bindable<IStaticMapOptions>;
}

/********************************************/

export class StaticMapView extends ViewComponent implements IContentView<string>  {

    constructor(config?: IStaticMapViewConfig) {
        super({ template: "ImageView", ...config });

        this.bindConfig("options", config);

        this.update();
    }

    /********************************************/

    update() {
        this.content = Services.mapManager().staticMap(this.options);
    }

    /********************************************/

    options: IStaticMapOptions;

    content: string;
}

registerType(StaticMapView, "StaticMapView");
