import { INamedContent } from "../../Core/Abstraction/INamedContent";
import { isString } from "../../Core/Helpers/TypeCheck";
import { linq } from "../../Core/Linq";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { IAsyncLoad } from "../Abstraction/IAsyncLoad";
import { IPanel } from "../Abstraction/IPanel";
import { IView } from "../Abstraction/IView";
import { loadAllAsync } from "../Helpers/ViewUtils";
import { IItemsViewConfig, ItemsView } from "./ItemsView";

export interface IPanelConfig extends IItemsViewConfig<IView> {

    viewContent?: IView | IView[];
}

/********************************************/

export class Panel extends ItemsView<IView> implements IPanel, IAsyncLoad {

    constructor(config?: IPanelConfig) {
        super({ template: "ItemsView", ...config });

        if (config?.viewContent) {
            if (Array.isArray(config.viewContent))
                config.viewContent.forEach(a => this.addView(a));
            else
                this.addView(config.viewContent);
        }
    }

    /********************************************/

    loadAsync(): Promise<any> {

        return loadAllAsync(this.content);
    }

    /********************************************/

    clear() {
        this.content.clear();
    }

    /********************************************/

    addView<TView extends IView>(view: TView): TView {
        this.content.add(view);
        return view;
    }

    /********************************************/

    removeView(view: IView) {
        this.content.remove(view);
    }

    /********************************************/

    replaceView(oldViewName: string, newView: IView)

    replaceView(oldView: IView, newView: IView)

    replaceView(oldView: string | IView, newView: IView) {

        let index: number;

        if (isString(oldView))
            index = linq(this.content).indexOf(a => "name" in a && (a as unknown as INamedContent).name == oldView);
        else
            index = this.content.indexOf(oldView);

        if (index != -1)
            this.content.set(index, newView);
    }

    /********************************************/

    protected onItemAdded(item: IView) {
        if (item)
            item.parentView = this;
    }

    /********************************************/

    protected onItemRemoved(item: IView) {
        if (item && item.parentView == this)
            item.parentView = null;
    }
}

registerType(Panel, "Panel");