import { IList } from "../../Core/Abstraction/IList";
import { OperationType } from "../../Core/Abstraction/IOperation";
import { Format } from "../../Core/Helpers/Format";
import { linq } from "../../Core/Linq";
import { LocalString } from "../../Core/Types/DynamicString";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { BindableObject } from "../../Templating/BindableObject";
import { setEnumerable } from "../../Templating/Functions";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { ActionPriority } from "../Abstraction/IAction";
import { IAsyncEditor } from "../Abstraction/IAsyncEditor";
import { IContentView } from "../Abstraction/IContentView";
import { IItemAction } from "../Abstraction/IItemAction";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { IValidable } from "../Abstraction/IValidator";
import { IView } from "../Abstraction/IView";
import { ContentActivity } from "../Activities/ContentActivity";
import { ActionView } from "../Components/ActionView";
import { IItemViewConfig, ItemView } from "../Components/ItemView";
import { TextView } from "../Components/TextView";
import { ItemEditContent } from "../Content/ItemEditContent";
import { Actions } from "../Helpers/Actions";
import { Interaction } from "../Helpers/Interaction";
import { isAsyncEditor, isValidable } from "../Helpers/TypeCheck";
import { EditableItemsSource } from "../Items/EditableItemsSource";
import { BaseEditor, IBaseEditorConfig } from "./BaseEditor";
import "Templates/ItemsEditor.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export type ItemStatus = "unchanged" | "modified" | "removed" | "added";

export type ItemEditMode = "inline" | "external";

type ItemCapability<TItem> = boolean | { (item: TItem): boolean };

/********************************************/

export interface IItemsEditorConfig<TItem, TValue, TUpdate, TCreate> extends IBaseEditorConfig<TValue[]> {

    createItemView?(item: TItem): IView;

    createItemUpdateEditor?(item: TItem): IItemEditor<TItem, TUpdate> | IAsyncEditor<TItem, TUpdate>;

    createItemAddEditor?(item: TItem): IItemEditor<TItem, TCreate> | IAsyncEditor<TItem, TCreate>;

    newItem?: () => TItem;

    itemsSource?: EditableItemsSource<TItem, any, any, TUpdate, TCreate>;

    openItem?(item: TItem): void;

    editMode?: Bindable<ItemEditMode>;

    canAdd?: boolean;

    canOpen?: ItemCapability<TItem>;

    canRemove?: ItemCapability<TItem>;

    canEdit?: ItemCapability<TItem>;

    isConfirmRemove?: boolean;

    addActionLabel?: LocalString;

    editActionLabel?: LocalString;

    removeActionLabel?: LocalString;

    editActionsPriority?: Bindable<ActionPriority>;

    itemActions?: IItemAction<ItemsEditorItemView<TItem, TValue, TUpdate, TCreate>>[];

    isAutoLoad?: boolean;

    attachItem?(item: TItem, isNew: boolean): void;
}

/********************************************/

export interface IItemsEditorItemViewConfig<TItem, TValue, TUpdate, TCreate> extends IItemViewConfig<TItem> {

    value?: TValue;

    editActionsPriority: Bindable<ActionPriority>;

    editActionLabel: LocalString;

    removeActionLabel: LocalString;

    addTitle: LocalString;

    editTitle: LocalString;

    itemActions: IItemAction<ItemsEditorItemView<TItem, TValue, TUpdate, TCreate>>[];

    canRemove: boolean;

    canEdit?: boolean;

    itemsSource: EditableItemsSource<TItem, any, any, TUpdate, TCreate>;
}

/********************************************/

export class ItemsEditorItemView<TItem, TValue, TUpdate, TCreate> extends ItemView<TItem> {


    constructor(config: IItemsEditorItemViewConfig<TItem, TValue, TUpdate, TCreate>) {

        super({
            ...config
        });

        if (config.canEdit) {
            this.addAction({
                name: "edit-item",
                icon: "fas fa-edit",
                operation: OperationType.Local,
                priority: BindableObject.bindValue(config.editActionsPriority),
                displayName: config.editActionLabel,
                executeAsync: () => this.editAsync()
            });
        }

        if (config.canRemove) {
            this.addAction({
                name: "remove-item",
                icon: "fas fa-trash",
                operation: OperationType.Local,
                priority: BindableObject.bindValue(config.editActionsPriority),
                displayName: config.removeActionLabel,
                executeAsync: () => this.removeAsync()
            });
        }

        if (config.itemActions) {
            config.itemActions.forEach(action => this.addAction({
                name: action.name,
                icon: action.icon,
                priority: action.priority,
                operation: action.operation,
                displayName: Format.action(action.displayName, config.itemsSource.displayName),
                canExecute: action.canExecute ? () => action.canExecute(this) : undefined,
                executeAsync: () => action.executeAsync(this)
            }));
        }

        this.value = config.value;
    }

    /********************************************/

    updateView() {

        this.content.clear();

        if (this.isEdit && this.editMode == "inline" && !isAsyncEditor(this.editor))
            this.content.add(this.editor.view);
        else
            this.content.add(this.view);
    }

    /********************************************/

    updateValueAsync(): Promise<void> {

        throw "Not implemented";
    }

    /********************************************/

    createUpdateEditor(): IItemEditor<TItem, TUpdate> {

        throw "Not implemented";
    }

    /********************************************/

    createAddEditor(): IItemEditor<TItem, TCreate> {

        throw "Not implemented";
    }

    /********************************************/

    executeUpdateAsync(item: TUpdate): Promise<TItem> {

        throw "Not implemented";
    }

    /********************************************/

    executeAddAsync(item: TCreate): Promise<TItem> {

        throw "Not implemented";
    }

    /********************************************/

    async editAsync(): Promise<boolean> {

        if (this.isEdit)
            return false;

        if (!this.editor) {

            if (this.status == "added")
                this.editor = this.createAddEditor();
            else
                this.editor = this.createUpdateEditor();
        }

        this.isEdit = true;

        if (this.editMode == "external") {

            let newItem: TItem = null;

            if (isAsyncEditor(this.editor)) {

                const editValue = await this.editor.editAsync(this.item);

                if (editValue) {
                    if (this.status == "added")
                        newItem = await this.executeAddAsync(editValue as TCreate);
                    else
                        newItem = await this.executeUpdateAsync(editValue as TUpdate)
                }
            }
            else {

                const activity = new ContentActivity<TItem>({
                    providers: [new ItemEditContent({
                        editor: this.editor,
                        title: item => this.status == "added" ? this.addTitle : this.editTitle,
                        value: this.item,
                        saveItemAsync: item => this.status == "added" ? this.executeAddAsync(item as TCreate) : this.executeUpdateAsync(item as TUpdate)
                    })]
                });

                newItem = await (await Actions.loadPageAsync(activity)).result;
            }

            if (newItem) {

                this.item = newItem;

                this.status = "unchanged";

                this.isEdit = false;

                await this.updateValueAsync();
            }
            else
                this.isEdit = false;

            this.editor = null;

            return newItem != null;
        }
        return false;
    }

    /********************************************/

    cancelEdit(): void {

        this.isEdit = false;
    }


    /********************************************/

    removeAsync(): Promise<void> {
        throw "Not implemented";
    }

    /********************************************/

    clearErrors() {
        this.error = null;
    }

    /********************************************/

    async validateAsync(force?: boolean) {

        if (this.isEdit) {

            if (isValidable(this.editor))
                return await this.editor.validateAsync();
        }
        else {

            if (isValidable(this.view))
                return await this.view.validateAsync();
        }
        return true;
    }

    /********************************************/

    async loadAsync(): Promise<any> {
        this.updateView();
    }

    /********************************************/

    get itemsEditor() {
        return <ItemsEditor<TItem, TValue, TUpdate, TCreate>>this.parentView;
    }

    /********************************************/

    error: string = null;

    addTitle: string = null;

    editTitle: string = null;

    editor: IItemEditor<TItem, TUpdate | TCreate> | IAsyncEditor<TItem, TUpdate | TCreate> = null;;

    view: IView = null;

    value: TValue = null;

    status: ItemStatus = "unchanged";

    editMode: ItemEditMode = "external";

    isEdit: boolean = false;
}

/********************************************/

export class ItemsEditor<TItem, TValue = TItem, TUpdate = TItem, TCreate = TItem> extends BaseEditor<TValue[]> implements IContentView<IList<ItemsEditorItemView<TItem, TValue, TUpdate, TCreate>>>, IValidable {

    protected _changesCount: number = 0;
    protected _isUpdating: number = 0;

    constructor(config?: IItemsEditorConfig<TItem, TValue, TUpdate, TCreate>) {

        super({ template: "ItemsEditor", ...config });

        this.bindConfig("editMode", config);

        this.bindConfig("canAdd", config);

        this.bindConfig("canOpen", config);

        this.bindConfig("canRemove", config);

        this.bindConfig("isConfirmRemove", config);

        this.bindConfig("canEdit", config);

        this.bindConfig("itemsSource", config);

        this.bindConfig("itemActions", config);

        this.bindConfig("editActionsPriority", config);

        this.bindConfig("removeActionLabel", config);

        this.bindConfig("editActionLabel", config);

        if (config) {

            if (!config.editActionLabel)
                this.editActionLabel = Format.action("edit-item", this.itemsSource?.displayName);

            if (!config.removeActionLabel)
                this.removeActionLabel = Format.action("remove-item", this.itemsSource?.displayName);

            if (config.createItemAddEditor)
                this.createItemAddEditor = config.createItemAddEditor;

            if (config.createItemUpdateEditor)
                this.createItemUpdateEditor = config.createItemUpdateEditor;

            if (config.createItemView)
                this.createItemView = config.createItemView;

            if (config.attachItem)
                this.attachItem = config.attachItem;

            if (config.newItem)
                this.newItem = config.newItem;

            if (config.openItem) {
                this.openItem = config.openItem;

                if (config.canOpen == undefined)
                    this.canOpen = true;
            }
        }

        if (!this.itemsSource)
            this.itemsSource = new EditableItemsSource();

        this.addAction = ActionView.fromAction({
            name: "add-item",
            icon: "fas fa-plus",
            operation: OperationType.Local,
            displayName: config?.addActionLabel ?? Format.action("add-item", this.itemsSource.displayName),
            executeAsync: async () => await this.addAsync()
        })

        if (this.value)
            this.beginEditAsync(this.value,);
    }

    /********************************************/

    protected async loadAsyncWork(): Promise<any> {

        const awaiters: Promise<void>[] = [];

        for (let item of this.content)
            awaiters.push(item.loadAsync());

        await Promise.all(awaiters);
    }

    /********************************************/

    clear() {
        this.content.clear();
    }

    /********************************************/

    clearErrors() {
        for (let item of this.content)
            item.clearErrors();
    }

    /********************************************/

    insert() {

    }

    /********************************************/

    async addAsync() {

        const item = this.newItem();

        this.attachItem(item, true);

        const itemView = this.createItem(this.itemToValue(item), item);

        itemView.item = item;

        itemView.status = "added";

        if (await itemView.editAsync()) {

            if (linq(this.content).any(a => this.itemsSource.itemComparer(a.item, itemView.item)))
                return;

            this.content.add(itemView);

            await this.notifyEditValueChangedAsync();
        }
    }

    /********************************************/

    protected openItem(item: TItem) {

    }

    /********************************************/

    protected attachItem(item: TItem, isNew: boolean) {

    }

    /********************************************/

    protected isCapable(selector: ItemCapability<TItem>, item: TItem): boolean {

        if (typeof selector == "boolean")
            return selector;

        return selector(item);

    }

    /********************************************/

    protected createItem(value: TValue, item?: TItem) {

        const result = new ItemsEditorItemView<TItem, TValue, TUpdate, TCreate>({
            value: value,
            item: item,
            addTitle: Format.title("add-item", this.itemsSource.displayName),
            editTitle: "",
            itemsSource: this.itemsSource,
            editActionsPriority: this.editActionsPriority,
            editActionLabel: this.editActionLabel,
            removeActionLabel: this.removeActionLabel,
            itemActions: this.itemActions,
            canRemove: this.isCapable(this.canRemove, item),
            canEdit: this.isCapable(this.canEdit, item),
            canOpen: this.isCapable(this.canOpen, item),
            open: () => this.openItem(result.item)
        });

        result.parentView = this;

        result.isConfirmRemove = this.isConfirmRemove;

        result.createAddEditor = () => this.createItemAddEditor(result.item);

        result.createUpdateEditor = () => this.createItemUpdateEditor(result.item);

        result.executeAddAsync = item => this.itemsSource.addItemAsync(item);

        result.executeUpdateAsync = editItem => this.itemsSource.updateItemAsync(editItem, result.item);

        result.loadAsync = async () => {

            if (!result.item) {

                result.item = await this.valueToItemAsync(result.value);
                result.canOpen = this.isCapable(this.canOpen, result.item);
                result.editTitle = Format.title("edit-item", this.itemsSource.getItemText(result.item));

                this.attachItem(result.item, result.status != "added");
            }

            result.view = this.createItemView(result.item);

            result.updateView();
        };

        result.updateValueAsync = async () => {

            result.canOpen = this.isCapable(this.canOpen, result.item);

            result.value = this.itemToValue(result.item);
            result.view = this.createItemView(result.item);
            result.editTitle = Format.title("edit-item", this.itemsSource.getItemText(result.item));

            result.updateView();

            await this.notifyEditValueChangedAsync();
        }

        result.removeAsync = async () => {

            if (result.status == "added")
                this.content.remove(result);
            else {
                if (this.isConfirmRemove) {
                    if (!await Interaction.confirmAsync(Format.message("msg-remove-confirm", this.itemsSource.displayName)))
                        return;
                }

                if (this.status == "none")
                    this.status = "loaded";

                result.status = "removed";
                await this.notifyEditValueChangedAsync();
            }
        }

        result.value = value;

        if (this.status != "none")
            result.loadAsync();

        return result;
    }

    /********************************************/

    protected itemToValue(item: TItem): TValue {

        return <any>item;
    }

    /********************************************/

    protected valueToItemAsync(value: TValue): Promise<TItem> {

        return Promise.resolve(value as unknown as TItem);
    }

    /********************************************/

    protected onItemChanged(oldValue: TValue, newValue: TValue) {

        this._needValidation = true;

        if (this._isUpdating) {
            this._changesCount++;
            return;
        }

        this.notifyEditValueChangedAsync();
    }

    /********************************************/

    set editValue(value: TValue[]) {

        if (!this.content || this._isUpdating)
            return;

        this._isUpdating++;
        this._changesCount = 0;

        this.content.clear();

        try {

            if (value) {
                for (let itemValue of value)
                    this.content.add(this.createItem(itemValue));
            }
        }
        finally {
            this._isUpdating--;
        }

        if (this._changesCount > 0)
            this.notifyEditValueChangedAsync();
    }

    /********************************************/

    get editValue(): TValue[] {

        const result: TValue[] = [];
        for (let item of this.content) {

            if (item.status == "removed")
                continue;

            result.push(item.value);
        }
        return result;
    }


    /********************************************/

    protected async validateAsyncWork(force?: boolean): Promise<boolean> {

        let isValid = true;

        for (let item of this.content) {

            if (!await item.validateAsync(force))
                isValid = false;
        }

        return isValid;
    }


    /********************************************/

    protected async commitAsyncWork(force?: boolean) {

        let isValid = true;
        let isChanged = false;

        /*
        for (var item of this.content) {
            if (item.isEdit) {
                if (!await item.commitAsync())
                    isValid = false;
            }
        }
        */

        if (!isValid)
            return false;

        for (let i = this.content.count - 1; i >= 0; i--) {

            const item = this.content.get(i);

            item.clearErrors();

            if (item.status == "removed") {
                if (await this.itemsSource.removeItemAsync(item.item))
                    this.content.removeAt(i);
                else {
                    this.error = "";
                    isValid = false;
                }
                isChanged = true;
            }
            else if (item.status == "modified" && !isAsyncEditor(item.editor)) {
                const updateItem = await this.itemsSource.updateItemAsync(item.editor.value as TUpdate, item.item);
                if (updateItem) {
                    item.item = updateItem;
                    item.status = "unchanged";
                    item.updateView();
                }
                else {
                    this.error = "";
                    isValid = false;

                }
                isChanged = true;
            }
            else if (item.status == "added" && !isAsyncEditor(item.editor)) {
                const addItem = await this.itemsSource.addItemAsync(item.editor.value as TCreate);
                if (addItem) {
                    item.item = addItem;
                    item.status = "unchanged";
                    item.updateView();
                }
                else {
                    this.error = "";
                    isValid = false;
                }
                isChanged = true;
            }

        }

        if (isValid) {

            if (isChanged)
                this.onValueChanged(this.editValue);
        }

        return isValid;
    }

    /********************************************/

    protected newItem(): TItem {

        return this.itemsSource.newItem();
    }

    /********************************************/

    protected getItemViewContent(item: TItem): any {

        return this.itemsSource ? this.itemsSource.getItemText(item) : item.toString();
    }

    /********************************************/

    protected createItemView(item: TItem): IView {

        return new TextView({ content: this.getItemViewContent(item) });
    }

    /********************************************/

    protected createItemUpdateEditor(item: TItem): IItemEditor<TItem, TUpdate> | IAsyncEditor<TItem, TUpdate> {

        throw "Not Supported";
    }

    /********************************************/

    protected createItemAddEditor(item: TItem): IItemEditor<TItem, TCreate> | IAsyncEditor<TItem, TCreate> {

        throw "Not Supported";
    }

    /********************************************/

    readonly content: IObservableList<ItemsEditorItemView<TItem, TValue, TUpdate, TCreate>> = observableListOf();

    editActionsPriority: ActionPriority = ActionPriority.Secondary;

    editActionLabel: string = "Edit";

    removeActionLabel: string = "Remove";

    canAdd: boolean = true;

    canRemove: ItemCapability<TItem> = true;

    canEdit: ItemCapability<TItem> = false;

    canOpen: ItemCapability<TItem> = false;

    editMode: ItemEditMode = "inline";

    itemActions: IItemAction<ItemsEditorItemView<TItem, TValue, TUpdate, TCreate>>[] = [];

    itemsSource: EditableItemsSource<TItem, any, any, TUpdate, TCreate> = null;

    addAction: ActionView;

    isConfirmRemove: boolean = true;
}


registerType(ItemsEditor, "ItemsEditor");
registerType(ItemsEditorItemView, "ItemsEditorItemView");
