import { IDictionary } from "../../Core/Abstraction/IDictionary";
import { LocalString } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { BindableObject } from "../../Templating/BindableObject";
import { observableListOf } from "../../Templating/ObservableList";
import { ActionPriority } from "../Abstraction/IAction";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { IView } from "../Abstraction/IView";
import { IconTextView } from "../Components/IconTextView";
import { ListView } from "../Components/ListView";
import { Panel } from "../Components/Panel";
import { ViewComponent } from "../Components/ViewComponent";
import { BooleanEditor } from "../Editors/BooleanEditor";
import { PopUpEditor } from "../Editors/PopupEditor";


declare global {

    export interface IServices {

        permissionManager: PermissionManager;
    }
}


export interface IPermission {

    name: string;

    ask(): Promise<boolean>;

    isGranted(): Promise<boolean>;
}

/****************************************/

export interface IPermissionRequest {

    permission: IPermission;

    description: LocalString;

    granted?: boolean;
}


/****************************************/

export class PermissionRequestItemView extends ViewComponent {

    constructor(request: IPermissionRequest) {
        super();

        this.request = request;

        this.switchView = new BooleanEditor({
            label: this.request.permission.name,
            template: "Switch",
            styles: ["primary"]
        });

        this.switchView.beginEditAsync(request.granted);
    }

    /****************************************/

    request: IPermissionRequest = null;

    switchView: BooleanEditor = null;

    isShowInfo: boolean = false;
}

/****************************************/

class PermissionEditor extends BindableObject implements IItemEditor<IPermissionRequest[]> {

    private _listView: ListView<IPermissionRequest, PermissionRequestItemView> = null;

    beginEditAsync(item: IPermissionRequest[]) {

        const panel = new Panel({
            styles: ["vertical"]
        });


        this._listView = panel.addView(new ListView({
            styles: ["surface"],
            items: observableListOf(item),
            isAutoLoad: true,
            createItemView: item => new PermissionRequestItemView(item)
        }));

        panel.addView(new IconTextView({ text: "msg-permission-request", styles: ["message warning"] }));

        this.view = panel;

        this.value = item;

        return Promise.resolve();
    }

    /****************************************/

    commitAsync(force?: boolean) {

        for (let item of this._listView.content)
            item.request.granted = item.switchView.editValue;


        return Promise.resolve(true);
    }

    /****************************************/

    view: IView = null;

    value: IPermissionRequest[] = null;

    readonly isValid: boolean = true;

}

/****************************************/

export class PermissionManager {

    constructor() {

    }

    /****************************************/

    async requireAsync(...permissions: IPermissionRequest[]) {

        let toAsk: IPermissionRequest[] = [];

        let result: IDictionary<boolean> = {};


        for (let perm of permissions) {
            if (!(await perm.permission.isGranted()))
                toAsk.push(perm);
            else {
                result[perm.permission.name] = true;
                perm.granted = true;
            }
        }

        if (toAsk.length > 0) {

            const editor = new PopUpEditor<IPermissionRequest[]>({
                editor: new PermissionEditor(),
                saveLabel: "accept",
                savePriority: ActionPriority.Evidence,
                title: "permission-request",
            });

            const editResult = await editor.editAsync(toAsk);

            if (editResult) {

                for (let item of editResult) {

                    if (item.granted)
                        item.granted = await item.permission.ask();

                    result[item.permission.name] = item.granted;
                }
            }
        }

        return result;
    }

    /****************************************/

    geolocation: IPermission = {

        name: "geolocation",

        ask: () => new Promise(res => {

            function setResult(result: boolean) {
                localStorage.setItem("geolocation", result ? "granted" : "denied");
                res(result);
            }

            navigator.geolocation.getCurrentPosition(
                () => setResult(true),
                error => setResult(error.code != error.PERMISSION_DENIED)
                , { maximumAge: Infinity, timeout: 2000 });

        }),

        isGranted: async () => {
            if ("permissions" in navigator)
                return (await navigator.permissions.query({ name: "geolocation" })).state == "granted";

            return localStorage.getItem("geolocation") == "granted";
        }
    }

    /****************************************/

    notification: IPermission = {

        name: "notification",

        ask: () => new Promise(res => {

            if (!("Notification" in window)) {
                console.warn("Notification not supported");
                res(false);
            }

            Notification.requestPermission(p => {
                res(p == "granted");
            });
        }),

        isGranted: () => Promise.resolve(!("Notification" in window) ? true : Notification.permission == "granted")
    }
}

registerType(PermissionRequestItemView, "PermissionRequestItemView");