import { getTypeName, isInstanceOf, registerType } from "../../Core/Utils/ObjectUtils";
import { IActivable } from "../Abstraction/IActivable";
import { IComponent } from "../Abstraction/IComponent";
import { DeactivateContentReason } from "../Abstraction/IViewContentProvider";
import { IPageConfig, Page } from "../Components/Page";
import { Panel } from "../Components/Panel";
import { formatForCss } from "../Helpers/ViewUtils";

export interface IActivityConfig extends IPageConfig<Panel> {

}

/****************************************/

export class Activity<TResult = void> extends Page<Panel> implements IActivable {

    protected _viewItems: IComponent[] = [];
    protected _resultResolve: (value: TResult) => void;

    constructor(config?: IActivityConfig) {

        super(config);

        this.view = new Panel({ name: formatForCss(getTypeName(this)), styles: ["vertical", "activity", "page", "relative"] });
        //this.view.isCacheEnabled = true;

        if (this.name)
            this.view.styles.push(this.name);
    }

    /****************************************/

    get result(): Promise<TResult> {
        return new Promise<TResult>(res => this._resultResolve = res);
    }

    /****************************************/

    activateAsync() {
        return Promise.resolve();
    }

    /****************************************/

    deactivateAsync(reason: DeactivateContentReason) {
        return Promise.resolve();
    }

    /****************************************/

    async closeAsync(result?: TResult) {

        const closeResult = await super.closeAsync(result);

        if (this._resultResolve)
            this._resultResolve(result);

        return closeResult;
    }

    /****************************************/

    protected loadWorkAsync() {

        return this.createAsync();
    }

    /****************************************/

    protected createAsync(): Promise<void> {

        return Promise.resolve();
    }

    /****************************************/

    static isActivity(obj: any): obj is Activity {

        return isInstanceOf(obj, Activity);
    }
}

registerType(Activity, "Activity");