import { Services } from "../../Core/Global";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { Actions } from "../Helpers/Actions";
import { ILocation } from "../Types/Location";
import { ISize } from "../Types/Size";
import { ContentView, IContentViewConfig } from "./ContentView";

export interface ILocationViewConfig extends IContentViewConfig<ILocation> {

    mapSize?: ISize;

    zoomLevel?: number;
}

/********************************************/

export class LocationView extends ContentView<ILocation>  {

    constructor(config?: ILocationViewConfig) {
        super({ template: "LocationView", ...config });

        if (config) {

            if (config.mapSize)
                this.mapSize = config.mapSize;

            if (config.zoomLevel)
                this.zoomLevel = config.zoomLevel;
        }

        this.updateAsync();
    }

    /********************************************/

    protected onContentChanged() {

        this.updateAsync();
    }

    /********************************************/

    showMap() {
        Actions.maps(this.content);
    }

    /********************************************/

    async updateAsync(): Promise<void> {

        let manager = Services.mapManager();
        let pos = this.content.position;

        if (!pos)
            pos = await manager.getLocationAsync(this.content.address);

        this.map = manager.staticMap({
            center: pos,
            size: this.mapSize,
            zoomLevel: this.zoomLevel,
            pins: [{
                center: pos,
                icon: 113,
                name: this.content.name
            }]
        });
    }

    /********************************************/

    map: string;

    mapSize: ISize = { width: 1280, height: 720 };

    zoomLevel: number = 16;
}

registerType(LocationView, "LocationView");