import { IEditor } from "../Abstraction/IEditor";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { BooleanEditor, IBooleanEditorConfig } from "../Editors/BooleanEditor";
import { DateEditorCombo, IDateEditorComboConfig } from "../Editors/DateEditorCombo";
import { ItemEditorConverter } from "../Editors/ItemEditorConverter";
import { IItemsEditorConfig, ItemsEditor } from "../Editors/ItemsEditor";
import { INumberEditorConfig, NumberEditor } from "../Editors/NumberEditor";
import { IObjectEditorConfig, ObjectEditor } from "../Editors/ObjectEditor";
import { RichTextEditor } from "../Editors/RichTextEditor";
import { ISingleItemSelectorConfig, SingleItemSelector } from "../Editors/SingleItemSelector";
import { ITextEditorConfig, TextEditor } from "../Editors/TextEditor";

export namespace Editors {

    export function richText() {

        return new RichTextEditor();
    }

    /****************************************/

    export function currency() {
        return new NumberEditor({
            allowNull: false,
            min: 0.01,
        });
    }

    /****************************************/

    export function converter<TItem, TEdit>(editor: IEditor<TEdit, any>, itemToEdit: (item: TItem) => TEdit, editToItem?: (item: TItem, editValue: TEdit) => void): IItemEditor<TItem, TEdit> {

        return new ItemEditorConverter({
            editor: editor,
            editToItem: editToItem,
            itemToEdit: itemToEdit
        });
    }

    /****************************************/

    export function items<TItem, TValue, TUpdate, TCreate>(config?: IItemsEditorConfig<TItem, TValue, TUpdate, TCreate>) {

        return new ItemsEditor({ ...config });
    }

    /****************************************/

    export function text(config?: ITextEditorConfig) {
        return new TextEditor({ ...config });
    }

    /****************************************/

    export function textMultiLine(config?: ITextEditorConfig) {

        return new TextEditor({ template: "TextEditorMultiLine", ...config });
    }

    /****************************************/

    export function number(config?: INumberEditorConfig) {
        return new NumberEditor({ ...config });
    }

    /****************************************/

    export function boolean(config?: IBooleanEditorConfig) {
        return new BooleanEditor({ ...config });
    }

    /****************************************/

    export function password(config?: ITextEditorConfig) {
        return new TextEditor({ template: "PasswordEditor", ...config });
    }

    /****************************************/

    export function object<TObj extends {}>(config?: IObjectEditorConfig<TObj>) {
        return new ObjectEditor(config);
    }

    /****************************************/

    export function address(config?: ITextEditorConfig) {
        return new TextEditor({ template: "TextEditorMultiLine", ...config });
    }

    /****************************************/

    export function birthDate(minAge?: number, config?: IDateEditorComboConfig) {
        return new DateEditorCombo({ maxYear: new Date().getFullYear() - (minAge ?? 0), minYear: new Date().getFullYear() - 100 });
    }

    /****************************************/

    export function singleItemSelector<TItem, TValue>(config: ISingleItemSelectorConfig<TItem, TValue>) {

        return new SingleItemSelector(config);
    }
}