import { LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { Bindable } from "../../Templating/Properties";
import { BaseTextEditor, IBaseTextEditorConfig } from "./BaseTextEditor";
import "Templates/TextEditor.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface ITextEditorConfig extends IBaseTextEditorConfig<string> {

    placeholder?: Bindable<LocalString>;

    lineCount?: Bindable<number>;

    maxLength?: Bindable<number>;
}

/********************************************/

export class TextEditor extends BaseTextEditor<string> {

    constructor(config?: ITextEditorConfig) {

        super({ template: "TextEditor", ...config });

        this.bindConfigString("placeholder", config, StringUsage.Tooltip);

        this.bindConfig("lineCount", config);

        this.bindConfig("maxLength", config);

        this.bindConfig("trackMode", config);

        this.createEditValueProp();
    }

    /********************************************/

    placeholder: string = null;

    lineCount: number = 1;

    maxLength: number = null;
}

registerType(TextEditor, "TextEditor");