import { Services } from "../../Core/Global";
import { Signal } from "../../Core/Types/Signal";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";
import { IMapManager } from "../Abstraction/IMapManager";
import { IViewComponentConfig, ViewComponent } from "./ViewComponent";

export interface IMapViewConfig extends IViewComponentConfig {

    manager?: IMapManager;

    showCurrentPosition?: Bindable<boolean>;

    onMapLoaded?: () => void;
}

/********************************************/

export class MapView extends ViewComponent implements IHTMLAttach {

    constructor(config?: IMapViewConfig) {
        super({ template: "Attach", ...config });

        if (config) {
            if (config.onMapLoaded)
                this.onMapLoaded = config.onMapLoaded;
            if (config.manager)
                this.manager = config.manager;
            this.bindConfig("showCurrentPosition", config);
        }

        if (!this.manager)
            this.manager = Services.mapManager();

        this.prop("showCurrentPosition").subscribe(value => {
            this.manager.showCurrentPositionAsync(value);
        })
    }

    /********************************************/

    attach(element: HTMLElement): void {

        if (this.manager)
            this.manager.attach(element);

        this.mapReady.set();
        this.onMapLoaded();

        this.manager.showCurrentPositionAsync(this.showCurrentPosition);
    }

    /********************************************/

    onMapLoaded() {

    }

    /********************************************/

    showCurrentPosition: boolean = false;

    manager: IMapManager;

    mapReady = new Signal();
}

registerType(MapView, "MapView");