import { registerType } from "../../Core/Utils/ObjectUtils";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { TemplateBuilder } from "../../Templating/TemplateBuilder";
import { View } from "./View";
import { ViewComponent } from "./ViewComponent";

type ConsoleMessageType = "warn" | "log" | "info" | "trace" | "error" | "debug";

class ConsoleItem extends View {

    constructor(args?: any[]) {
        super();
        this.content = args;
    }

    /****************************************/

    protected debugTypeName() {
    }


    /****************************************/

    content: any[];
}

/****************************************/

export class ConsoleMessageView extends ConsoleItem {

    constructor(type: ConsoleMessageType, ...args: any[]) {
        super(args)

        this.type = type;
        this.template = "ConsoleMessageView";
    }

    /****************************************/

    type: ConsoleMessageType;
}

/****************************************/

export class ConsoleGroupView extends ConsoleItem {

    constructor(...args: any[]) {
        super(args)
        this.template = "ConsoleGroupView";
    }

    /****************************************/

    items: IObservableList<ConsoleItem> = observableListOf();
}

/****************************************/

export class ConsoleView extends ViewComponent {

    private _oldConsole: Console;
    private _groups: ConsoleGroupView[] = [];
    private _activeGroup: ConsoleGroupView;
    private _isCreated = false;

    constructor() {

        super({ template: "ConsoleView" });

        this.root = new ConsoleGroupView();
        this._activeGroup = this.root;
    }

    /****************************************/

    protected debugTypeName() {
    }

    /****************************************/

    show() {
        if (!this._isCreated) {
            var builder = new TemplateBuilder(this, document.body);
            builder.content(this);
        }
        this.visible = true;
    }

    /****************************************/

    hide() {
        this.visible = false;
    }


    /****************************************/

    attach() {

        if (this._oldConsole)
            return;

        this._oldConsole = <any>{};

        for (let key in console)
            this._oldConsole[key] = console[key];

        console.log = (...args) => this.write("log", args);
        console.warn = (...args) => this.write("warn", args);
        console.info = (...args) => this.write("info", args);
        console.trace = (...args) => this.write("trace", args);
        console.error = (...args) => this.write("error", args);
        console.debug = (...args) => this.write("debug", args);
        console.clear = () => this.clear();
        console.group = (...args) => this.group(args);
        console.groupEnd = () => this.groupEnd();
    }

    /****************************************/

    detach() {

        if (!this._oldConsole)
            return;

        for (let key in console)
            console[key] = this._oldConsole[key];
        this._oldConsole = null;
    }

    /****************************************/

    write(logType: ConsoleMessageType, args: any[]) {

        this._oldConsole[logType].apply(console, args);

        this._activeGroup.items.add(new ConsoleMessageView(logType, args));

        if (logType == "error")
            this._activeGroup.items.add(new ConsoleMessageView(logType, new Error().stack));
    }

    /****************************************/

    group(args: any[]) {

        this._oldConsole.group.apply(console, args);

        const newGroup = new ConsoleGroupView(args);
        this._activeGroup.items.add(newGroup);
        this._groups.push(newGroup);
        this._activeGroup = newGroup;
    }

    /****************************************/

    groupEnd() {

        this._oldConsole.groupEnd.apply(console);

        if (this._groups.length > 0)
            this._activeGroup = this._groups.pop();
    }

    /****************************************/

    clear() {
        this._oldConsole.clear.apply(console);

        this._groups = [];
        this.root.items.clear();
        this._activeGroup = this.root;
    }

    /****************************************/

    root: ConsoleGroupView;
}

registerType(ConsoleView, "ConsoleView");
