import { linq, Linq } from "../Linq";
import { randomString } from "./StringUtils";

class LinqNode {

    constructor(node: Node) {
        this.node = node;
    }

    /****************************************/

    get childNodes(): Linq<LinqNode> {
        return linq(this.node.childNodes).select(a => new LinqNode(a));
    }

    /****************************************/

    descendsFrom(target: Node): boolean {
        let curItem = this.node;
        while (curItem != null) {
            if (curItem == target)
                return true;
            curItem = curItem.parentNode;
        }
        return false;
    }

    /****************************************/

    hasClass(className: string): boolean {
        if ("classList" in this.node)
            return (this.node as HTMLElement).classList.contains(className);
        return false;
    }

    /****************************************/

    get discendants(): Linq<LinqNode> {

        function* iterator(node: Node) {

            for (let child of this.node.childNodes) {
                yield new LinqNode(child);
                for (let innerChild of iterator(child))
                    yield innerChild;
            };
        }

        return linq(iterator(this.node));
    }

    /****************************************/

    get parentNodesOrSelf(): Linq<LinqNode> {

        function* iterator(node: Node) {

            let curItem = node;
            while (curItem != null) {
                yield new LinqNode(curItem);
                curItem = curItem.parentNode;
            }
        }

        return linq(iterator(this.node));
    }

    /****************************************/

    get parentNodes(): Linq<LinqNode> {
        return this.parentNodesOrSelf.skip(1);
    }

    /****************************************/

    get name(): string {
        return this.node.nodeName;
    }

    /****************************************/

    node: Node;
}

/****************************************/

export function parentOfClass(src: HTMLElement, className: string): HTMLElement {
    return <HTMLElement>linqNode(src).parentNodes.where(a => a.hasClass(className)).select(a => a.node).first();
}

/****************************************/

export function linqNode(src: Node): LinqNode {
    return new LinqNode(src);
}

/****************************************/

export function isParentOrSelf(element: HTMLElement, parent: HTMLElement): boolean {

    let curElement = element;

    while (curElement) {
        if (curElement == parent)
            return true;
        curElement = curElement.parentElement;
    }
    return false;
}

/****************************************/

export function generateNodeId(base: string = "id_") {
    while (true) {
        const curId = base + randomString(8);
        if (!document.getElementById(curId))
            return curId;
    }
}

/****************************************/

export function removeClass(element: Element, className: string) {
    if (element.classList.contains(className))
        element.classList.remove(className);
}

/****************************************/

export function addClass(element: Element, className: string) {
    if (!element.classList.contains(className))
        element.classList.add(className);
}

/****************************************/

export function isSmallDevice(): boolean {
    return window.innerWidth < 610;
}

/****************************************/

export async function copyText(value: string) {
    if (navigator["clipboard"])
        await navigator.clipboard.writeText(value);
    else {
        const input = document.createElement("textarea");
        document.body.appendChild(input);
        input.value = value
        input.select();
        //input.setSelectionRange(0, input.value.length);
        document.execCommand("copy");
        document.body.removeChild(input);
    }

}

/****************************************/

export function centerElement(element: HTMLElement, always = true): number {

    const topOfPage = document.documentElement.scrollTop;
    const heightOfPage = window.innerHeight;

    let elY = 0;
    let elH = 0;

    for (let p = element; p && p != document.body; p = <HTMLElement>p.offsetParent)
        elY += p.offsetTop;

    elH = element.offsetHeight;

    if (always || elY + elH > topOfPage + heightOfPage || elY < topOfPage)
        document.documentElement.scrollTop = Math.max(0, elY - (heightOfPage - elH) / 2);

    return elY - document.documentElement.scrollTop;

}