import { App } from "../../Core/Global";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { TemplateBuilder } from "../../Templating/TemplateBuilder";
import { IViewApplication } from "../Abstraction/IViewApplication";
import { IViewConfig, View } from "./View";

export interface IBasePopUpMessageConfig extends IViewConfig {
    className?: string;
}

/****************************************/

export abstract class BasePopUpMessage extends View {

    protected _container: HTMLElement;

    protected _showResolve: (value: string) => void;

    protected _isVisible = false;

    /****************************************/

    constructor(config?: IBasePopUpMessageConfig) {

        super(config);

        this.bindConfig("className", config);

        this._container = document.createElement("DIV");
        this._container.className = this.className + "-container";
    }

    /****************************************/

    async showAsync() {

        if (this._isVisible)
            return;

        this._isVisible = true;

        (App as IViewApplication).unblock(true);

        const builder = new TemplateBuilder(this, this._container);

        builder.template(this.template, a => a);

        document.body.appendChild(this._container);

        await delayAsync(0);

        this._container.classList.add("visible");

        return new Promise<string>(res => this._showResolve = res);
    }

    /****************************************/

    hide(actionName: string) {

        if (!this._isVisible)
            return;

        this._isVisible = false;

        (App as IViewApplication).restoreBlock();

        this._container.classList.remove("visible");

        if (this._showResolve) {

            this._showResolve(actionName);
            this._showResolve = null;
        }

        setTimeout(() => {

            if (!this._isVisible)
                document.body.removeChild(this._container);

        }, 500);
    }

    /****************************************/

    className: string = null;
}


registerType(BasePopUpMessage, "BasePopUpMessage");