import { App } from "../../Core/Global";
import { TimeSpan } from "../../Core/Types/TimeSpan";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { TemplateBuilder } from "../../Templating/TemplateBuilder";
import { IAction } from "../Abstraction/IAction";
import { IViewApplication } from "../Abstraction/IViewApplication";
import { ActionView } from "./ActionView";
import { ViewComponent } from "./ViewComponent";

export interface ISnackBarOptions<TContent> {

    showTime?: TimeSpan;

    content: TContent;

    action: IAction | string;
}

/****************************************/

export class SnackBar<TContent> extends ViewComponent {

    protected _showResolve: (value: string) => void;

    private constructor() {

        super({ template: "SnackBar" });

        this.status = "close";

        new TemplateBuilder(this, document.body).content(this);
    }

    /****************************************/

    async showAsync() {

        if (this.status == "open")
            return;

        (App as IViewApplication).unblock(true);

        await delayAsync(0);

        this.status = "open";

        return new Promise<string>(res => this._showResolve = res);
    }

    /****************************************/


    hide(actionName: string) {

        if (this.status != "open")
            return;

        this.status = "close";

        (App as IViewApplication).restoreBlock();

        if (this._showResolve) {

            this._showResolve(actionName);
            this._showResolve = null;
        }
    }

    /****************************************/

    static showAsync<TContent>(options: ISnackBarOptions<TContent>) {

        const instanceId = new Date().valueOf();

        if (!SnackBar.instance)
            SnackBar.instance = new SnackBar();

        SnackBar.instance.content = options.content;

        SnackBar.instance.instanceId = instanceId;

        if (options.action) {

            let action: IAction;

            if (typeof options.action == "string")
                action = {
                    name: options.action,
                    executeAsync: () => Promise.resolve()
                };
            else
                action = options.action;

            SnackBar.instance.action = ActionView.fromAction(action, {
                executeAsync: async () => {
                    await action.executeAsync();
                    SnackBar.instance.hide(action.name);
                }
            });
        }
        else
            SnackBar.instance.action = null;

        if (options.showTime) {

            setTimeout(() => {

                if (SnackBar.instance.instanceId == instanceId && SnackBar.instance._isVisible)
                    SnackBar.instance.hide(null);

            }, options.showTime.totalMilliseconds)
        }

        return SnackBar.instance.showAsync();
    }

    /****************************************/

    action: ActionView = null;

    content: TContent = null;

    showTime?: TimeSpan = TimeSpan.fromSeconds(0);

    instanceId: number = null;

    private static instance = null;
}

registerType(SnackBar, "SnackBar");