import { OperationType } from "../../Core/Abstraction/IOperation";
import { Services } from "../../Core/Global";
import { BindableObject } from "../../Templating/BindableObject";
import { IItemsContainer, IItemsLoader } from "../Abstraction/IItemsLoader";
import { IScrollData, IScrollListener } from "../Abstraction/IScrollListener";

export interface IPagedItemsLoaderConfigConfig<TFilter> {

    pageSize?: number;

    getFilter?: (offset: number, pageSize: number) => TFilter;
}

/****************************************/

export class PagedItemsLoader<TItem = any, TFilter = object> extends BindableObject implements IItemsLoader<TItem, TFilter>, IScrollListener {

    private _hasMoreItems: boolean = true;
    private _container: IItemsContainer<TItem, TFilter>;

    constructor(config?: IPagedItemsLoaderConfigConfig<TFilter>) {

        super();

        this.bindConfig("pageSize", config);

        if (config) {
            if (config.getFilter)
                this.getFilter = config.getFilter;
        }
    }

    /****************************************/

    loadItemsAsync(container: IItemsContainer<TItem, TFilter>, chunkSize: number): Promise<void> {

        this._container = container;

        this._hasMoreItems = true;

        this._container.clear();

        return this.loadNextPageAsync();
    }

    /****************************************/

    async loadNextPageAsync(): Promise<void> {

        if (!this.canLoadNextPage)
            return;


        if (this._container.itemsSource) {

            const filter = this.getFilter(this._container.itemsCount, this.pageSize);

            const operation = Services.operation.begin({ message: "Laoding items from " + this._container.itemsCount, type: OperationType.Local });

            try {

                this._container.status = "loading";

                const newItems = await this._container.itemsSource.getItemsAsync({ ...filter, ...this._container.filter });

                if (!newItems || newItems.length == 0)
                    this._hasMoreItems = false;
                else {

                    newItems.forEach(item =>
                        this._container.addItem(item));

                    this._hasMoreItems = newItems.length == this.pageSize;
                }
            }
            finally {
                operation.end();
                this._container.status = "loaded";
            }

        }
    }

    /****************************************/

    onScroll(data: IScrollData) {
        if (data.pageBottom < 1 && this.canLoadNextPage)
            this.loadNextPageAsync();
    }

    /****************************************/

    getFilter(offset: number, pageSize: number): TFilter {

        return { ...this._container.filter, ...this._container.itemsSource.getFilter(undefined, offset, pageSize) };
    }

    /****************************************/

    protected get canLoadNextPage() {
        return this._hasMoreItems && this._container.status != "loading";
    }

    /****************************************/

    pageSize: number = 20;

    readonly isScrollCheckActive = true;


}