import { IFactory } from "../../Core/Abstraction/IFactory";
import { ISize } from "../Types/Size";
import { IHTMLAttach } from "./IHTMLAttach";


declare global {

    export interface IServices {

        mapManager: IFactory<IMapManager>;
    }
}


export enum MapShapeType {
    None,
    Circle
}

/*******************************/

export interface IMapStyle {

    fillColor?: string;
    strokeColor?: string;
    strokeSize?: number;
    opacity?: number;
}

/*******************************/

export interface ICreateMapItemOptions {

    id: string;
    title?: string;
    description?: string;
    location?: IGeoPoint;
    icon?: any;
    color?: string;
    shape?: IMapShape;
}

/*******************************/

export interface IStaticMapPin {
    center: IGeoPoint;
    icon?: any;
    name?: string;
}

/*******************************/

export interface IStaticMapOptions {
    size?: ISize;
    center?: IGeoPoint;
    pins?: IStaticMapPin[];
    zoomLevel?: number;
}

/*******************************/

export interface IMapManager extends IHTMLAttach {

    addItems(itemsOptions: ICreateMapItemOptions[]): IMapItem[];

    removeItem(item: IMapItem);

    getItemById(id: string): IMapItem;

    clear();

    centerTo(location: IGeoPoint, radiusMeters?: number);

    zoomToItems();

    updateSize();

    getAddressAsync(location: IGeoPoint): Promise<string>;

    getLocationAsync(address: string): Promise<IGeoPoint>;

    staticMap(options: IStaticMapOptions): string;

    showCurrentPositionAsync(value: boolean): Promise<void>;

    selectedItem: IMapItem;

    onAttached();

    onSelectionChanged();

    isReady: boolean;
}

/*******************************/

export interface IGeoPoint {
    latitude: number;
    longitude: number;
}

/*******************************/

export interface IMapItem {
    id: string;
    title: string;
    description: string;
    location: IGeoPoint;
    icon: string;
    color: string;
    shape: IMapShape;
}

/*******************************/

export interface IMapShape {
    type: MapShapeType;
    radius?: number;
    style?: IMapStyle;
}