import { isCloneable, isFunction, isObject, isString } from "../Helpers/TypeCheck";
import { Type } from "../Types/Types";
import { equalsArray } from "./ArrayUtils";

export function isEmpty(value: any): boolean {
    return value === undefined || value === null || (Array.isArray(value) && value.length == 0);
}

/****************************************/

export function equals(valueA: any, valueB: any): boolean {

    if (Array.isArray(valueA) || Array.isArray(valueB))
        return equalsArray(valueA, valueB);

    return valueA == valueB;
}

/****************************************/

export function createInstance<TObj>(typeName: string, ...args: any[]): TObj {

    const parts = typeName.split(".");

    let curType: any = window;
    for (let part of parts)
        curType = curType[part];

    return new curType(...args);
}

/****************************************/

export function hasProp(obj: object, propName: string): boolean {
    return (propName in obj);
}

/****************************************/

export function isInstanceOf<T>(obj: any, type: Type<T>): obj is T {

    if (!obj)
        return;
    let curType = getType(obj);
    while (curType != null) {
        if (curType == type)
            return true;
        curType = getBaseType(curType);
    }
    return false;
}

/****************************************/

export function getOrCreateProperty<T>(obj: object, propName: string, value: T): T {
    let desc = Object.getOwnPropertyDescriptor(obj, propName);
    if (!desc) {
        createProperty(obj, propName, value);
        return value;
    }
    return get(obj, propName);
}

/****************************************/

export function createProperty(obj: object, propName: string, value: any) {
    if (propName.substr(0, 1) == "@") {
        Object.defineProperty(obj, propName, {
            writable: true,
            enumerable: false,
            value: value
        });
    }
    else {
        Object.defineProperty(obj, propName, {
            value: value
        });
    }
}

/****************************************/

export function get<T>(obj: object, propName: string, defValue?: T): T {

    if (!obj)
        return defValue;
    const value = obj[propName];
    if (value === undefined || value === null)
        return defValue;
    return value;
}

/****************************************/

export function set(obj: object, propName: string, value: any) {

    if (propName.substr(0, 1) == "@") {
        const desc = Object.getOwnPropertyDescriptor(obj, propName);
        if (!desc)
            createProperty(obj, propName, value);
        else
            obj[propName] = value;
    }
    else
        obj[propName] = value;
}

/****************************************/

export function setTypeName(obj: object | Type<any>, name: string) {

    if (isObject(obj))
        set(obj, "@typeName", name);
    else
        set(Object.getPrototypeOf(obj), "@typeName", name);
}

/****************************************/

export function getTypeName(obj: any) {

    if (!obj)
        return undefined;

    let name = obj["@typeName"];

    if (!name) {
        name = typeof obj;

        if (name == "function")
            return getFunctionName(obj);

        if (name == "object") {

            const constFunc = obj.constructor;
            if (constFunc)
                return getTypeName(constFunc);
        }
    }
    return name;
}

/****************************************/

export function getBaseType(ctr: Type<any>): Type<any>;

export function getBaseType(obj: object): Type<any>;

export function getBaseType(objOrFun: object | Type<any>): Type<any> {

    let proto: any;

    if (isFunction(objOrFun))
        proto = objOrFun.prototype;
    else
        proto = Object.getPrototypeOf(objOrFun);

    return Object.getPrototypeOf(proto).constructor;
}

/****************************************/

export function getType<TObj>(obj: TObj): Type<TObj>

export function getType<TObj>(typeName: string): Type<TObj>

export function getType<TObj>(typeOrName: string | object): Type<TObj> {

    if (isString(typeOrName)) {
        const parts = typeOrName.split(".");
        let curObj = self;
        parts.forEach(part => {
            curObj = curObj[part];
            if (!curObj)
                return null;
        });
        return <any>curObj;
    }

    return (typeOrName as object).constructor as Type<TObj>;
}

/****************************************/

export function getFunctionName(func: Function): string {

    let curName = func.name;
    if (!curName) {
        const funcNameRegex = /function\s([^(]{1,})\(/;
        const results = (funcNameRegex).exec(func.toString());
        curName = (results && results.length > 1) ? results[1].trim() : "";
    }
    return curName;
}

/****************************************/

export function clone<TObj>(obj: TObj): TObj {

    if (isCloneable(obj))
        return obj.clone();

    return JSON.parse(JSON.stringify(obj));
}

/****************************************/

export function registerType(type: Function, name: string) {
    set(type, "@typeName", name);
}