import { IContext } from "../../Core/Abstraction/IContext";
import { LocalString } from "../../Core/Types/DynamicString";
import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { IAction } from "../Abstraction/IAction";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";
import { IPopUpMenu } from "../Abstraction/IPopUpMenu";
import { IView } from "../Abstraction/IView";
import { ActionView } from "./ActionView";
import { IViewConfig, View } from "./View";
import "Templates/Drawer.html";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { Format } from "../../Core/Helpers/Format";
import { linq } from "../../Core/Linq";
import { GestureType, IGestureData, IGestureListener } from "../Abstraction/IGestureListener";

export interface IDrawerGroupConfig {

    label?: LocalString;

    actions: IAction[];

    icon?: string;
}

/****************************************/

export interface IDrawerConfig extends IViewConfig {

    groups: IDrawerGroupConfig[];

    header?: Bindable<IView>;
}

/****************************************/

export type DrawerStatus = "hidden" | "visible" | "hiding" | "showing";

/****************************************/

export class DrawerGroup {

    private _host: Drawer;

    constructor(host: Drawer, config?: IDrawerGroupConfig) {
        this._host = host;

        if (config) {

            this.label = Format.action(config.label);

            config.actions.forEach(a =>
                this.addAction(a));
        }
    }

    /****************************************/

    addAction(action: IAction) {

        const actionView = ActionView.fromAction(action, {
            executeAsync: async () => {
                this._host.hideAsync();
                await action.executeAsync();
            }
        });

        this.actions.add(actionView);

        return actionView;
    }

    /****************************************/

    readonly actions: IObservableList<ActionView> = observableListOf();

    label: string = null;
}

/****************************************/

export class Drawer extends View implements IPopUpMenu, IHTMLAttach, IGestureListener {

    constructor(config?: IDrawerConfig) {
        super({ template: "Drawer", ...config });

        this.bindConfig("header", config);

        if (config) {
            config.groups.forEach(a =>
                this.addGroup(a));
        }
    }

    /****************************************/

    onGesture(data: IGestureData) {

        if (data.type == GestureType.SwipeLeft)
            this.hideAsync();
    }

    /****************************************/

    getGroup(label: LocalString) {

        const labelTxt = Format.action(label);
        return linq(this.groups).first(a => a.label == labelTxt);
    }

    /****************************************/

    addGroup(config: IDrawerGroupConfig) {

        const group = new DrawerGroup(this, config);

        this.groups.add(group);

        return group;
    }

    /****************************************/

    attach(element: HTMLElement) {

        element.addEventListener("click", e => {

            if (e.srcElement == e.currentTarget)
                this.hideAsync();
        });
    }

    /****************************************/

    async showAsync(ctx?: IContext) {

        this.status = "showing";

        await delayAsync(0);

        this.status = "visible";
    }

    /****************************************/

    async hideAsync() {

        this.status = "hiding";

        await delayAsync(500);

        if (this.status == "hiding")
            this.status = "hidden";
    }

    /****************************************/

    status: DrawerStatus = "hidden";

    header: IView = null;

    readonly groups: IObservableList<DrawerGroup> = observableListOf();
}

registerType(Drawer, "Drawer");