import { TimeSpan } from "./TimeSpan";

export class Signal {

    private _waitHandlers: ((res: boolean) => void)[] = [];
    private _isSet: boolean;

    /****************************************/

    constructor(isSet: boolean = false) {
        this._isSet = isSet;
    }

    /****************************************/

    set() {
        this._isSet = true;
        this._waitHandlers.forEach(handler =>
            handler(true));
        this._waitHandlers = [];
    }

    /****************************************/

    reset() {
        this._isSet = false;
    }

    /****************************************/

    waitFor(timeout?: TimeSpan): Promise<boolean> {

        if (this._isSet)
            return Promise.resolve(true);

        return new Promise((res, rej) => {

            if (timeout) {
                setTimeout(() => {
                    const index = this._waitHandlers.indexOf(res);
                    if (index != -1) {
                        this._waitHandlers.splice(index, 1);
                        res(false);
                    }
                }, timeout.totalMilliseconds);
            }

            this._waitHandlers.push(res);
        });
    }

    /****************************************/

    get isSet() {
        return this._isSet;
    }
}