import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { IBehavoir } from "../../Templating/Abstraction/IBehavoir";

export class RippleClickBehavoir implements IBehavoir<HTMLElement> {

    attach(element: HTMLElement) {
        element.addEventListener("pointerdown", this.onClick, { passive: true });
        element.classList.add("ripple");
    }

    /****************************************/

    detach(element: HTMLElement) {
        element.removeEventListener("pointerdown", this.onClick);
    }

    /****************************************/

    async onClick(e: Event) {

        const curTarget = <HTMLElement>e.currentTarget;
        curTarget.classList.remove("activated");

        await delayAsync(0);

        curTarget.classList.add("activated");

        await delayAsync(500);

        if (curTarget.classList.contains("activated"))
            curTarget.classList.remove("activated");
    }

    /****************************************/

    static readonly instance = new RippleClickBehavoir();
}