import { IItemsSourceConfig, ItemsSource } from "./ItemsSource";

export interface IEditableItemsSourceConfig<TItem, TValue, TFilter, TUpdate, TAdd> extends IItemsSourceConfig<TItem, TValue, TFilter> {

    addItemAsync?: (item: TAdd) => Promise<TItem>;

    updateItemAsync?: (editItem: TUpdate, item?: TItem) => Promise<TItem>;

    removeItemAsync?: (item: TItem) => Promise<boolean>;

    newItem?(): TItem;

}

/****************************************/

export class EditableItemsSource<TItem, TValue = TItem, TFilter = any, TUpdate = TItem, TAdd = TUpdate> extends ItemsSource<TItem, TValue, TFilter>{

    constructor(config?: IEditableItemsSourceConfig<TItem, TValue, TFilter, TUpdate, TAdd>) {

        super(config);

        if (config) {


            if (config.addItemAsync)
                this.addItemAsync = config.addItemAsync;

            if (config.updateItemAsync)
                this.updateItemAsync = config.updateItemAsync;

            if (config.removeItemAsync)
                this.removeItemAsync = config.removeItemAsync;

            if (config.newItem)
                this.newItem = config.newItem;
        }
    }


    /********************************************/

    newItem(): TItem {

        return <TItem>{};
    }

    /********************************************/

    addItemAsync(item: TAdd): Promise<TItem> {

        return Promise.resolve(item as unknown as TItem);
    }

    /********************************************/

    updateItemAsync(editItem: TUpdate, item?: TItem): Promise<TItem> {

        return Promise.resolve(item);
    }

    /********************************************/

    removeItemAsync(item: TItem): Promise<boolean> {

        return Promise.resolve(true);
    }


}