import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { Bindable } from "../../Templating/Properties";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";
import { IView } from "../Abstraction/IView";
import { ContentView, IContentViewConfig } from "./ContentView";
import * as Hammer from "hammerjs";
import { registerType } from "../../Core/Utils/ObjectUtils";

type BottomSheetStatus = "close" | "open" | "head" | "moving";


/********************************************/

export interface IBottomSheetConfig<TContent extends IView> extends IContentViewConfig<TContent> {

    headHeight?: Bindable<number>;

    isShowOpener?: Bindable<boolean>;

    headSelector?: string;

}

/********************************************/

export class BottomSheet<TContent extends IView = IView> extends ContentView<TContent> implements IHTMLAttach {

    protected _element: HTMLElement;
    protected _margin: number = 20;
    private _isAnimating = 0;

    /********************************************/

    constructor(config?: IBottomSheetConfig<TContent>) {
        super({ template: "BottomSheet", ...config });

        this.bindConfig("headHeight", config);

        this.bindConfig("headSelector", config);

        this.bindConfig("isShowOpener", config);
    }

    /********************************************/

    attach(element: HTMLElement): void {

        this._element = element;

        let hammer = new Hammer(element, {
            recognizers: [[Hammer.Pan, { direction: Hammer.DIRECTION_VERTICAL }]]
        });

        let startHeight = 0;
        let oldStatus = this.status;
        let startScrollTop = 0;

        hammer.on("panstart", ev => {

            oldStatus = this.status;
            this.status = "moving";

            startScrollTop = element.scrollTop;
            startHeight = element.clientHeight;
        });

        hammer.on("panend", ev => {

            if (element.clientHeight < this.headHeight - this._margin)
                this.close();
            else {

                if (oldStatus == "open") {
                    if (ev.deltaY > this._margin && this._element.scrollTop <= 0)
                        this.head();
                    else
                        this.open();
                }
                else {
                    if (element.clientHeight > this.headHeight + this._margin)
                        this.open();
                    else
                        this.head();
                }
            }
        });

        hammer.on("panmove", ev => {

            let deltaY = ev.deltaY;

            //Scroll when is opened

            if (oldStatus == "open") {
                let newScrollTop = startScrollTop - deltaY;
                if (deltaY < 0) {
                    this._element.scrollTop = newScrollTop;
                    return;
                }
                else {

                    if (newScrollTop >= 0) {
                        this._element.scrollTop = newScrollTop;
                        return;
                    }
                    this._element.scrollTop = 0;
                    deltaY = -newScrollTop;
                }
            }

            //Oopen, close, head

            let newHeight = startHeight - deltaY;
            let panHeight = newHeight;
            let enableScrolling = false;

            if (newHeight < this._margin)
                newHeight = 0;

            else if (newHeight > (element.parentNode as HTMLElement).clientHeight - this._margin && newHeight < this._element.scrollHeight) {
                newHeight = (element.parentNode as HTMLElement).clientHeight;
                enableScrolling = true;
            }

            else if (newHeight > this._element.scrollHeight)
                newHeight = this._element.scrollHeight;

            else if (newHeight > this.headHeight - this._margin && newHeight < this.headHeight + this._margin)
                newHeight = this.headHeight;

            element.style.height = newHeight + "px";

            //Scroll while opening

            if (panHeight - newHeight > 0 && enableScrolling)
                this._element.scrollTop = panHeight - newHeight;
            else
                this._element.scrollTop = 0;
        });

        switch (this.status) {
            case "head":
                this.head();
                break;
            case "open":
                this.open();
                break;
            case "close":
                this.close();
                break;
        }
    }


    /********************************************/

    protected async setHeightAsync(value: number) {

        this._isAnimating++;

        if (this._isAnimating == 1)
            this._element.classList.add("animate");

        await delayAsync(0);

        this._element.style.height = value.toString() + "px";

        await delayAsync(500);

        this._isAnimating--;

        if (this._isAnimating == 0)
            this._element.classList.remove("animate");
    }

    /********************************************/

    toggle() {

        if (this.status == "open" || this.status == "close")
            this.head();
        else
            this.open();
    }

    /********************************************/

    open() {

        this.status = "open";

        if (this._element)
            this.setHeightAsync(this._element.scrollHeight);
    }

    /********************************************/

    head() {

        this.status = "head";

        if (this._element) {

            if (this.headSelector) {

                let headEl = this._element.querySelector(this.headSelector);
                if (headEl) {
                    this.headHeight = headEl.clientHeight;
                    if (this.isShowOpener)
                        this.headHeight += this._element.querySelector(".opener").clientHeight;
                }
            }

            this.setHeightAsync(this.headHeight);
        }
    }


    /********************************************/

    close() {

        this.status = "close";

        if (this._element)
            this.setHeightAsync(0);
    }

    /********************************************/

    isShowOpener: boolean = false;

    headHeight: number = 100;

    headSelector: string = null;

    status: BottomSheetStatus = "close";
}

registerType(BottomSheet, "BottomSheet");