import { registerType } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { ISelectable } from "../Abstraction/ISelectable";
import { ContentView, IContentViewConfig } from "./ContentView";
import "Templates/SelectableItemView.html";

export interface ISelectableItemViewConfig<TItem, TContent> extends IContentViewConfig<TContent> {

    isSelected?: Bindable<boolean>;

    item?: TItem;
}

/****************************************/

export class SelectableItemView<TItem, TContent> extends ContentView<TContent> implements ISelectable {
    constructor(config?: ISelectableItemViewConfig<TItem, TContent>) {

        super(config);

        this.bindConfig("isSelected", config);

        this.bindConfig("item", config);
    }

    /****************************************/

    select() {

        this.isSelected = true;
    }

    /****************************************/

    toggle() {

        this.isSelected = !this.isSelected;
    }

    /****************************************/

    isSelected: boolean = false;

    item: TItem = null;
}

registerType(SelectableItemView, "SelectableItemView");