import { get, getTypeName, set, setTypeName } from "../../Core/Utils/ObjectUtils";

export interface IItemCreateOptions<TItem, TValue> {

    value?: TValue;

    item?: TItem;

    text?: string;

    type?: string;
}

/****************************************/

export class Item {

    static create<TItem, TValue = TItem>(options: IItemCreateOptions<TItem, TValue>): TValue {

        let newValue: any = options.value;

        if (typeof newValue == "string")
            newValue = new String(newValue);
        else if (typeof newValue == "number")
            newValue = new Number(newValue);
        else if (typeof newValue == "boolean")
            newValue = new Boolean(newValue);
        else if (newValue instanceof Date)
            newValue = new Date(newValue.getTime());

        if (options.item) {
            options.item.valueOf = () => options.value;
            if (options.value)
                options.item["toJSON"] = () => options.value;
            if (options.text)
                options.item.toString = () => options.text;
            if (options.type)
                options.item["$type"] = () => options.type;
        }

        if (newValue) {

            if (options.text)
                newValue.toString = () => options.text;

            if (options.type)
                setTypeName(newValue, options.type);

            if (options.item !== undefined)
                set(newValue, "@item", options.item);

            newValue["toJSON"] = () => {
                if ((options.value as any)?.toJSON)
                    return (options.value as any).toJSON();
                return options.value;
            }
        }

        if (newValue)
            return newValue;

        return <any>options.item;
    }

    /****************************************/

    static getText(value: any): string {

        if (value == null || value == undefined)
            return "";

        const item = this.getItem(value);
        if (item !== value)
            return item.toString();

        return value.toString();
    }

    /****************************************/

    static getType(value: any): string {

        return getTypeName(value);
    }

    /****************************************/

    static getValue<TValue>(value: any): TValue {

        if (!value)
            return;

        if (value.valueOf)
            return <TValue>value.valueOf();

        return <TValue>value;
    }

    /****************************************/

    static getItem<TItem>(value: any): TItem {
        if (!value)
            return;

        const item = get<TItem>(value, "@item");
        if (item)
            return item;

        return undefined;
    }
}