import { registerType } from "../../Core/Utils/ObjectUtils";
import { IView } from "../Abstraction/IView";
import { IListViewConfig, ListView } from "./ListView";

export interface IGridViewConfig<TItem, TView extends IView, TFilter extends {}> extends IListViewConfig<TItem, TView, TFilter> {

    colsCount?: number;

    rowsCount?: number;
}

/****************************************/

export class GridView<TItem = any, TView extends IView = any, TFilter extends {} = any> extends ListView<TItem, TView, TFilter> {

    constructor(config?: IGridViewConfig<TItem, TView, TFilter>) {

        super({ styles: ["grid"], ...config });

        this.bindConfig("colsCount", config); 

        this.bindConfig("rowsCount", config);
    }

    /****************************************/

    colsCount: number = 0;

    rowsCount: number = 0;
}

registerType(GridView, "GridView");