import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { ContentView, IContentViewConfig } from "./ContentView";
import { View } from "./View";
import "Templates/ItemsView.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface IItemsViewConfig<TItem> extends IContentViewConfig<IObservableList<TItem>> {

    emptyView?: View;
}

/********************************************/

export class ItemsView<TItem = any> extends ContentView<IObservableList<TItem>> {

    protected _updateCount: number = 0;

    constructor(config?: IItemsViewConfig<TItem>) {

        super({ content: observableListOf<TItem>(), ...config });

        this.content.subscribe({
            onItemAdded: (item: TItem) => {

                if (this._updateCount == 0)
                    this.onItemAdded(item);
            },

            onItemRemoved: (item: TItem) => {
                if (this._updateCount == 0)
                    this.onItemRemoved(item);
            }
        });

        this.bindConfig("emptyView", config);
    }

    /********************************************/

    protected initItems() {
        if (this.content)
            this.content.toArray().forEach(a => this.onItemAdded(a));
    }

    /********************************************/

    beginUpdate() {
        this._updateCount++;
    }

    /********************************************/

    endUpdate() {
        this._updateCount--;
    }

    /********************************************/

    protected onItemAdded(item: TItem) {

    }

    /********************************************/

    protected onItemRemoved(item: TItem) {

    }

    emptyView: View = null;
}

registerType(ItemsView, "ItemsView");