import { IList } from "../../Core/Abstraction/IList";
import { equals, isEmpty, registerType } from "../../Core/Utils/ObjectUtils";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { IContentView } from "../Abstraction/IContentView";
import { IEditor } from "../Abstraction/IEditor";
import { IPropertiesHost } from "../Abstraction/IPropertiesHost";
import { IPropertyViewConfig, PropertyView } from "../Components/PropertyView";
import { View } from "../Components/View";
import { BaseEditor, IBaseEditorConfig } from "./BaseEditor";

type PropertiesOf<TObj extends object> = {
    [TKey in keyof TObj]: PropertyView<TObj[TKey], IEditor<TObj[TKey], any>, TObj>
};


/********************************************/

export interface IObjectEditorConfig<TObj extends object> extends IBaseEditorConfig<TObj> {

    properties?: { [TKey in keyof Partial<TObj>]: IPropertyViewConfig<TObj[TKey], TObj> };

    propertyTemplateName?: Bindable<string>;

    defaultProperty?: keyof TObj;

    emptyView?: View;

    onPropertyChanged?: <TKey extends keyof TObj>(propName: TKey, value: TObj[TKey], oldValue: TObj[TKey], target: TObj) => void;
}

/********************************************/

interface ICommitPropertyResult {
    isValid: boolean;
    isChanged: boolean;
}

/********************************************/

export class ObjectEditor<TObj extends object> extends BaseEditor<TObj> implements IContentView<IList<PropertyView<any>>>, IPropertiesHost<TObj> {

    protected _proxy: TObj;
    protected _changesCount: number = 0;
    protected _isUpdating: number = 0;
    protected _onPropertyChanged?: <TKey extends keyof TObj>(propName: TKey, value: TObj[TKey], oldValue: TObj[TKey], target: TObj) => void;

    constructor(config?: IObjectEditorConfig<TObj>) {

        super({ template: "ItemsViewForm", validationMode: "always", commitMode: "manual", ...config });

        this.bindConfig("propertyTemplateName", config);

        this.bindConfig("defaultProperty", config);

        this.bindConfig("emptyView", config);

        this.prop("content");

        if (config) {
            this._onPropertyChanged = config.onPropertyChanged;

            if (config.properties) {
                for (let propName in config.properties) {
                    config.properties[propName].name = propName;
                    this.addProperty(config.properties[propName]);
                }
            }
        }

        this.createProxy();

        if (!isEmpty(this.value))
            this.beginEditAsync(this.value);
    }

    /********************************************/

    addProperty(config: IPropertyViewConfig<TObj[keyof TObj], TObj>) {

        const item = new PropertyView({
            name: config.name,
            template: this.prop("propertyTemplateName"),
            ...config,
            host: this
        });
        

        this.properties[config.name] = item;

        return item;
    }

    /********************************************/

    protected async beginEditWorkAsync(value: TObj) {

        if (!this.value)
            this.value = <any>{};

        this.isDirty = true

        for (let propName in this.properties) {

            const prop = this.properties[propName];

            if (prop.value != undefined)
                await prop.editor.beginEditAsync(prop.value);
        }
    }

    /********************************************/

    clearErrors() {
        for (let propName in this.properties)
            this.properties[propName].error = null;
    }

    /********************************************/

    protected async validateAsyncWork(force?: boolean) {

        let isValid = true;

        const curValue = this.editValue;

        for (let propName in this.properties) {

            const prop = this.properties[propName];

            if (!await prop.validateAsync(curValue, force)) {
                this.isValid = false;
                isValid = false;
            }
        }

        return isValid;
    }

    /********************************************/

    protected createProxy() {

        this._proxy = <any>{};

        this.content.clear();

        for (let propName in this.properties) {

            const curProp = this.properties[propName];

            curProp.prop("value").subscribe((value, oldValue) =>
                this.onPropertyValueChanged(propName, value as any, oldValue as any));

            curProp.prop("isValid").subscribe(value => {

                if (!value)
                    this.isValid = false;
            });

            curProp.prop("isDirty").subscribe(value => {

                if (value)
                    this.isDirty = true;
            });

            this.content.add(curProp);

            Object.defineProperty(this._proxy, propName, {
                enumerable: true,
                get: () => curProp.value,
                set: value => curProp.value = value
            })
        }
    }

    /********************************************/

    activateAsync(): Promise<void> {

        if (this.defaultProperty)
            return this.editor(this.defaultProperty).activateAsync();

        return Promise.resolve();
    }

    /********************************************/

    protected async loadAsyncWork() {

        const awaiters: Promise<void>[] = [];

        for (let propName in this.properties)
            awaiters.push(this.properties[propName].loadAsync());

        await Promise.all(awaiters);
    }

    /********************************************/

    clear() {

        this._isUpdating++;

        try {

            for (let propName in this.properties)
                this.property(propName).clear();
        }
        finally {
            this._isUpdating--;
        }
    }

    /********************************************/

    protected editToValue(value: TObj): TObj {
        return undefined;
    }

    /********************************************/

    protected async commitAsyncWork(force?: boolean) {

        let isChanged = false;

        let isValid = true;

        if (!this.value)
            this.value = {} as TObj;

        for (let propName in this.properties) {

            const commitResult = await this.commitPropertyAsync(propName, false);

            if (!commitResult.isValid)
                isValid = false;

            if (!commitResult.isChanged)
                isChanged = true;
        }

        if (isValid) {

            if (isChanged)
                this.onValueChanged(this.value);
        }

        return isValid;
    }

    /********************************************/

    protected async commitPropertyAsync(propName: keyof TObj, validate: boolean) {

        const result: ICommitPropertyResult = {
            isValid: true,
            isChanged: false
        };

        const prop = this.property(propName);

        if (validate && !await prop.validateAsync(this.editValue, false))
            result.isValid = false;

        else if (!await prop.editor.commitAsync())
            result.isValid = false;

        else {

            if (!equals(prop.value, this.value[propName])) {
                this.value[propName] = prop.value;
                result.isChanged = true;
            }
        }

        return result;
    }

    /********************************************/

    editor<TKey extends keyof TObj>(propName: TKey): IEditor<TObj[TKey], any> {
        return this.properties[propName].editor;
    }

    /********************************************/

    property<TKey extends keyof TObj>(propName: TKey): PropertyView<TObj[TKey]> {
        return this.properties[propName];
    }

    /********************************************/

    protected async onPropertyValueChanged<TKey extends keyof TObj>(propName: TKey, value: TObj[TKey], oldValue: TObj[TKey]) {

        this._needValidation = true;

        if (this._isUpdating || this._isEditing) {

            this._changesCount++;
            return;
        }

        if (this.commitMode == "onchange") {

            const commitResult = await this.commitPropertyAsync(propName as any, true);
            if (commitResult.isChanged) {
                this._needValidation = true;
                this.isDirty = true;
            }
        }
        else
            await this.notifyEditValueChangedAsync();

        if (this._onPropertyChanged)
            this._onPropertyChanged(propName, value, oldValue, this._proxy as TObj);
    }

    /********************************************/

    set editValue(value: TObj) {

        if (this._isUpdating)
            return;

        this._isUpdating++;

        this._changesCount = 0;

        try {

            if (isEmpty(value))
                this.clear();
            else {

                for (let propName in this.properties)
                    this._proxy[propName] = value[propName];
            }
        }
        finally {
            this._isUpdating--;
        }

        if (this._changesCount > 0)
            this.notifyEditValueChangedAsync();

    }

    /********************************************/

    get editValue(): TObj {
        return this._proxy;
    }

    /********************************************/

    properties: PropertiesOf<TObj> = <any>{};

    defaultProperty: keyof TObj = null;

    propertyTemplateName = "PropertyView";

    emptyView: View = null;

    readonly content: IObservableList<PropertyView<any>> = observableListOf();
}

registerType(ObjectEditor, "ObjectEditor");