import { linq } from "../../Core/Linq";
import { LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { CatalogTemplate } from "../../Templating/Abstraction/ITemplateProvider";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";
import { ItemsSource } from "../Items/ItemsSource";
import { BaseEditor, IBaseEditorConfig } from "./BaseEditor";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { PopUpEditor } from "./PopupEditor";
import { SelectableItemView } from "../Components/SelectableItemView";
import { ISelectableItemView } from "../Abstraction/ISelectableItemView";

import "Templates/SingleItemSelector.html";
import "Templates/ItemSelector.html";


export interface ISingleItemSelectorConfig<TItem, TValue> extends IBaseEditorConfig<TValue> {

    itemsSource?: Bindable<ItemsSource<TItem, TValue>>;

    items?: TItem[];

    createItemView?: (item: TItem) => ISelectableItemView<TValue>;

    placeholder?: Bindable<LocalString>;

    emptyText?: LocalString;

    itemTemplate?: CatalogTemplate<ISelectableItemView<TValue>>;
}

/****************************************/

export class SingleItemSelector<TItem, TValue> extends BaseEditor<TValue> implements IHTMLAttach {

    private _element: HTMLElement
    private _isSelecting: boolean;

    constructor(config?: ISingleItemSelectorConfig<TItem, TValue>) {

        super({ template: "SelectEditor", ...config });

        this.bindConfig("itemsSource", config);

        this.bindConfig("itemTemplate", config);

        this.bindConfigString("placeholder", config);

        this.bindConfigString("emptyText", config, StringUsage.Title);

        if (config?.createItemView)
            this.createItemViewWork = config?.createItemView;

        if (config?.items) {

            if (this.allowNull)
                this.items.add(this.createEmptyItem());

            config.items.forEach(a => this.items.add(this.createItemView(a)));

            this.status = "loaded";
        }

        this.prop("selectedItem").subscribe(async (value, oldValue) => {

            if (this._isSelecting)
                return;

            if (!value && this.emptyText) {
                this.selectedItemText = this.emptyText;
                this.selectedItem = this.items.get(0);
                return;
            }

            this._isSelecting = true;
            try {
                if (oldValue)
                    oldValue.isSelected = false;

                if (value)
                    value.isSelected = true;

                this.selectedIndex = this.items.indexOf(value);

                if (this.itemsSource)
                    this.selectedItemText = value?.content ?? "";

                if (this.status != "loading")
                    await this.notifyEditValueChangedAsync();

            }
            finally {
                this._isSelecting = false;
            }
        });

        this.prop("selectedIndex").subscribe(value => {

            if (value == -1 && this.emptyText) {
                this.selectedIndex = 0;
                return;
            }

            this.selectedItem = value == -1 ? null : this.items.get(value);

            if (this._element?.tagName == "SELECT")
                (this._element as HTMLSelectElement).selectedIndex = value;
        });
    }

    /********************************************/

    attach(element: HTMLElement) {

        this._element = element;

        if (element.tagName == "SELECT") {

            (this._element as HTMLSelectElement).selectedIndex = this.selectedIndex;

            this._element.addEventListener("change",
                () => this.selectedIndex = (element as HTMLSelectElement).selectedIndex);
        }
    }

    /********************************************/

    async selectAsync() {
        let editor = new PopUpEditor({

            editor: new SingleItemSelector({
                template: "ItemSelectorList",
                itemsSource: this.itemsSource,
                allowNull: false,
                styles: ["no-padding-items-h"]
            }),
            saveOnCommit: true,
            saveLabel: "select",
            closeLabel: "cancel",
            title: "select-an-item",
            styles: [""]
        });

        const result = await editor.editAsync(this.value)
        if (result !== null)
            this.editValue = result;
    }

    /********************************************/

    findItemByValue(value: TValue) {

        if (this.itemsSource)
            return linq(this.items).first(a => this.itemsSource.equals(a.item, value));
        return linq(this.items).first(a => a.item == value);
    }

    /********************************************/

    set editValue(value: TValue) {

        if (this.editValue == value)
            return;

        if (value == null && this.emptyText)
            this.selectedItem = this.items[0];
        else
            this.selectedItem = this.findItemByValue(value);
    }

    get editValue(): TValue {

        return this.selectedItem?.item;
    }

    /****************************************/

    protected async loadAsyncWork() {

        if (this.itemsSource) {

            this.items.clear();

            if (this.allowNull)
                this.items.add(this.createEmptyItem());

            const items = await this.itemsSource.getItemsAsync(this.itemsSource.getFilter());
            if (items)
                items.forEach(a => this.items.add(this.createItemView(a)));
        }

        this.selectedIndex = 0;
        this.isDirty = true;
    }

    /****************************************/

    protected createEmptyItem() {

        const result = new SelectableItemView({
            item: null,
            content: this.emptyText ?? "",
            template: this.itemTemplate,
            styles: ["single", "checkbox"]
        });

        result.prop("isSelected").subscribe(value => {

            if (value)
                this.selectedItem = null
        });
        return result;
    }

    /****************************************/

    protected createItemViewWork(item: TItem): ISelectableItemView<TValue> {

        if (this.itemsSource)
            return new SelectableItemView({
                item: this.itemsSource.getItemValue(item),
                content: this.itemsSource.getItemText(item),
                template: this.itemTemplate,
                styles: ["single", "checkbox"] 
            });
    }

    /****************************************/

    protected createItemView(item: TItem) {

        const itemView = this.createItemViewWork(item);

        itemView.prop("isSelected").subscribe(value => {

            if (value)
                this.selectedItem = itemView
        });

        return itemView;
    }

    /****************************************/

    itemTemplate: CatalogTemplate<ISelectableItemView<TValue>> = "SelectableItemView";

    placeholder: string = null;

    emptyText: string = null;

    selectedItemText: string = null;

    selectedItem: ISelectableItemView<TValue> = null;

    selectedIndex: number = -1;

    itemsSource: ItemsSource<TItem, TValue> = null;

    readonly items: IObservableList<ISelectableItemView<TValue>> = observableListOf();
}


registerType(SingleItemSelector, "SingleItemSelector");