import { IDictionary } from "./Abstraction/IDictionary";
import { IDynamicString } from "./Types/DynamicString";

declare global {
    interface IStringTable {
        [key: string]: IDynamicString|string;
    }
}

interface IStringTableConfig {
    language: string;
    sector?: string;
}

export var Strings: IStringTable;

/****************************************/

export class StringTable {

    private static _tables: IDictionary<IStringTable> = {};
    private static _currentConfig: IStringTableConfig;

    /****************************************/

    private static getKey(config: IStringTableConfig): string {
        let key = config.language;
        if (config.sector)
            key += "|" + config.sector;
        return key;
    }

    /****************************************/

    static get(config?: IStringTableConfig): IStringTable {

        if (!config)
            config = this._currentConfig;

        let result = StringTable._tables[this.getKey(config)];
        if (!result && config.sector)
            result = StringTable._tables[this.getKey({ language: config.language })];
        return result;
    }

    /****************************************/

    static add(value: Partial<IStringTable>, config: IStringTableConfig): void {
        const key = this.getKey(config);
        let curValue = this._tables[key];
        if (!curValue)
            this._tables[key] = value as IStringTable;
        else {
            for (let str in value)
                curValue[str] = value[str];
        }
    }

    /****************************************/

    static get currentConfig(): IStringTableConfig {
        return this._currentConfig;
    }

    /****************************************/

    static set currentConfig(value: IStringTableConfig) {
        this._currentConfig = value;
        Strings = StringTable.get();
    }
}