import { ContentView, IContentViewConfig } from "./ContentView";
import { IImageViewConfig, ImageView } from "./ImageView";
import { IVideoViewConfig, VideoView } from "./VideoView";
import "Templates/MediaView.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export type MediaType = "empty" | "image" | "video";

export interface IMediaContent {

    type: MediaType;

    src?: string;
}

/********************************************/

export interface IMediaViewConfig extends IContentViewConfig<IMediaContent> {

    image?: IImageViewConfig;

    video?: IVideoViewConfig;
}

/********************************************/

export class MediaView extends ContentView<IMediaContent> {

    private _imageConfig: IImageViewConfig = {};
    private _videoConfig: IVideoViewConfig = {};

    constructor(config?: IMediaViewConfig) {

        super({ template: "MediaView", ...config });

        if (config) {

            if (config.image)
                this._imageConfig = config.image;

            if (config.video)
                this._videoConfig = config.video;
        }

        this.updateContent();
    }


    /********************************************/

    loadAsync() {

        if (this.activeView)
            return this.activeView.loadAsync();
        return Promise.resolve();
    }

    /********************************************/

    protected onContentChanged() {

        this.updateContent();
    }

    /********************************************/

    protected updateContent() {

        if (this.content && this.content.type == "image")
            this.activeView = new ImageView({ ...this._imageConfig, content: this.content.src });

        if (this.content && this.content.type == "video")
            this.activeView = new VideoView({ ...this._videoConfig, content: this.content.src });

    }

    /********************************************/

    activeView: VideoView | ImageView = null;
}

registerType(MediaView, "MediaView");