import { registerType } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { BaseTextEditor, IBaseTextEditorConfig } from "./BaseTextEditor";

export interface INumberEditorConfig extends IBaseTextEditorConfig<number> {

    placeholder?: Bindable<string>;

    min?: number;

    max?: number;

    precision?: number;
}

/********************************************/

export class NumberEditor extends BaseTextEditor<number> {

    constructor(config?: INumberEditorConfig) {

        super({ template: "NumberEditor", ...config });

        this.bindConfig("placeholder", config);

        this.bindConfig("precision", config);

        this.createEditValueProp();
    }

    /********************************************/

    protected valueToEdit(value: number): string {

        return isNaN(value) ? "" : (this.precision != null ? value.toFixed(this.precision) : value.toString()).replace(".", ",");
    }

    /********************************************/

    protected editToValue(value: string): number {

        var result = parseFloat(value?.replace(",", "."));
        return isNaN(result) ? null : result;
    }

    /********************************************/

    placeholder: string = null;

    min?: number = null;

    max?: number = null;

    precision: number = null;
}

registerType(NumberEditor, "NumberEditor");