import { linq } from "../../Core/Linq";
import { Type } from "../../Core/Types/Types";
import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { IPage } from "../Abstraction/IPage";
import { IMultiPageHost, IPageLoadOptions, PageTransactionType } from "../Abstraction/IPageHost";
import { ITransactionListener } from "../Abstraction/ITransactionListener";
import { IView } from "../Abstraction/IView";
import { Activity } from "../Activities/Activity";
import { IViewConfig, View } from "./View";
import { App, Services } from "../../Core/Global";
import { registerType } from "../../Core/Utils/ObjectUtils";
import "Templates/PageHost.html"; 

interface IPageView {

    content?: IView;

    className: string;
}

/****************************************/

interface IPageStackItem {

    page: IPage;

    options?: IPageLoadOptions;
}

/****************************************/

interface ISlidePageHostConfig extends IViewConfig {

    defaultTransaction?: PageTransactionType;
} 

/****************************************/

export class SlidePageHost extends View implements IMultiPageHost, ITransactionListener {

    private _activeViewIndex: number = 0;
    private _pageStack: IPageStackItem[];
    private _currentIndex: number;
    private _hideTimerId: number;

    /****************************************/

    constructor(config?: ISlidePageHostConfig) {
        super(config);

        this.pageViews = [{ content: null, className: null },
        { content: null, className: null }];

        this.clearAsync();

        if (config) {
            if (config.defaultTransaction)
                this.defaultTransaction = config.defaultTransaction;
        }
    }

    /****************************************/

    clearAsync(): void {
        this._pageStack = [];
        this._currentIndex = -1;
        this._activeViewIndex = 0;

        this.pageViews[0].content = null;
        this.pageViews[0].className = "immediate next";

        this.pageViews[1].content = null;
        this.pageViews[1].className = "immediate prev";
    }


    /****************************************/

    async goBackAsync() {
        if (this._currentIndex > 0)
            await this.goToAsync(this._currentIndex - 1);
    }

    /****************************************/

    async goForwardAsync() {
        if (this._currentIndex > 0)
            await this.goToAsync(this._currentIndex + 1);
    }

    /****************************************/

    async bringFrontAsync(page: IPage) {

        let pageIndex = linq(this._pageStack).indexOf(a => a.page == page);

        if (pageIndex != -1)
            await (page.host as IMultiPageHost).goToAsync(pageIndex);
    }

    /****************************************/

    async loadAsync<TPage extends IPage>(page: TPage, options?: IPageLoadOptions): Promise<TPage> {

        if (this.current == page) {
            await page.refreshAsync();
            return page;
        }

        this.cancelHidePrev();

        let op = Services.operation.begin("Hosting new page");
        try {

            if (this.current && Activity.isActivity(this.current))
                await this.current.deactivateAsync("replace");

            if (options?.loadMode == "clear")
                this._currentIndex = -1;

            if (options?.loadMode == "replace" && this.currentIndex >= 0)
                this._currentIndex--;

            while (this._pageStack.length > this._currentIndex + 1)
                this._pageStack.splice(this._pageStack.length - 1, 1);

            this._pageStack.push({ page: page, options: options });

            if (options && options.transaction)
                this.activeTransaction = options.transaction;
            else
                this.activeTransaction = this.defaultTransaction;

            this._currentIndex++;

            page.host = this;

            page.view.parentView = this;

            this.pageViews[this.backIndex].className = "immediate next";

            await delayAsync(0);

            await page.loadAsync();

            if (this._currentIndex > 0) {

                this.pageViews[this.backIndex].content = page.view;

                await delayAsync(0);

                this.pageViews[this.frontIndex].className = "animate prev";
                this.pageViews[this.backIndex].className = "animate active";
                this._activeViewIndex = this.backIndex;

                this.hidePrev();
            }
            else {

                this.pageViews[this.frontIndex].content = page.view;

                this.pageViews[this.frontIndex].className = "active";
            }

            page.status = "active";

            if (Activity.isActivity(this.current))
                await this.current.activateAsync();
        }
        catch (e) {
            App.handleError(this, e);
        }
        finally {
            op.end();
        }

        return page;
    }

    /****************************************/

    get(index: number) {

        return this._pageStack[index].page;
    }

    /****************************************/

    async goToAsync(index: number) {

        if (index < 0 || index >= this._pageStack.length || index == this._currentIndex)
            return;

        this.cancelHidePrev();

        let op = Services.operation.begin("Going to " + index);
        try {

            if (Activity.isActivity(this.current))
                await this.current.deactivateAsync("replace");

            this.current.status = "hidden";

            let options = this._pageStack[this._currentIndex].options;
            if (options && options.transaction)
                this.activeTransaction = options.transaction;
            else
                this.activeTransaction = this.defaultTransaction;

            this._currentIndex = index;

            if (this.current.view.parentView == this)
                this.current.view.parentView = null;

            this.pageViews[this.backIndex].content = this._pageStack[this._currentIndex].page.view;

            this.pageViews[this.backIndex].className = "immmediate prev";

            await delayAsync(0);

            this.pageViews[this.frontIndex].className = "animate next";
            this.pageViews[this.backIndex].className = "animate active";

            this._activeViewIndex = this.backIndex;

            this.current.status = "active";

            if (Activity.isActivity(this.current))
                await this.current.activateAsync();

            this.hidePrev();
        }
        catch (e) {
            App.handleError(this, e);
        }
        finally {
            op.end();
        }
    }

    /****************************************/

    private cancelHidePrev() {

        if (this._hideTimerId) {
            clearTimeout(this._hideTimerId);
            this._hideTimerId = null;
        }
    }

    /****************************************/

    private hidePrev() {

        this._hideTimerId = window.setTimeout(() => {

            if (!this._hideTimerId)
                return;
            this.pageViews[this.backIndex].className = "prev hide";
        }, 4000);
    }

    /****************************************/

    onActivePageChanged(): void {

    }

    /****************************************/

    onTransactionEnd(): void {

    }

    /****************************************/

    find<TPage extends IPage>(type: Type<TPage>): TPage;

    find<TPage extends IPage>(name: string): TPage;

    find<TPage extends IPage>(nameOrType: string | Type<TPage>): TPage {

        let curPage: IPageStackItem;

        if (typeof nameOrType == "string")
            curPage = linq(this._pageStack).first(a => a.page.name == <string>nameOrType);
        else
            curPage = linq(this._pageStack).first(a => Object.getPrototypeOf(a.page).constructor == nameOrType);

        if (curPage)
            return <TPage>curPage.page;
    }


    /****************************************/

    get current(): IPage {
        return this._currentIndex == -1 ? undefined : this._pageStack[this._currentIndex].page;
    }

    /****************************************/

    get canGoBack(): boolean {
        return this._currentIndex > 0;
    }

    /****************************************/

    get frontIndex(): number {
        return this._activeViewIndex;
    }

    /****************************************/

    get backIndex(): number {
        return (this._activeViewIndex + 1) % 2;
    }

    /****************************************/

    get currentIndex(): number {
        return this._currentIndex;
    }

    set currentIndex(value: number) {
        if (this._currentIndex == value)
            return;
        this.goToAsync(value);
    }

    /****************************************/

    get pageCount(): number {
        return this._pageStack.length;
    }

    /****************************************/

    defaultTransaction: PageTransactionType = "pop-up-down";

    activeTransaction: PageTransactionType = "none";

    pageViews: IPageView[];
}


registerType(SlidePageHost, "SlidePageHost");