import { Format } from "../../Core/Helpers/Format";
import { LocalString } from "../../Core/Types/DynamicString";
import { IAction } from "../Abstraction/IAction";
import { ContentActivity } from "../Activities/ContentActivity";
import { MessageBox } from "../Components/MessageBox";
import { Toast } from "../Components/Toast";
import { MessageContent } from "../Content/MessageContent";
import { Actions } from "./Actions";

export type MessageAction = "yes" | "no" | "cancel" | "retry" | "ok" | "abort";

export interface IMessageOptions {

    message: LocalString,

    title: LocalString;

    icon?: string;

    actions?: MessageAction[];

    customActions?: IAction[];
}

/****************************************/

export namespace Interaction {

    export async function showMessageAsync(options: IMessageOptions): Promise<MessageAction | string> {

        const msgActions = options.actions ?? ["ok"];

        const msgBox = new MessageBox({ message: options.message, icon: options.icon, title: options.title });

        msgActions.forEach(a => {

            msgBox.addAction({
                name: a,
                displayName: Format.action(a),
                executeAsync: () => Promise.resolve()
            })
        })

        if (options.customActions)
            options.customActions.forEach(a => msgBox.addAction(a));

        const result = await msgBox.showAsync();

        return <MessageAction>result;
    }

    /****************************************/

    export async function confirmAsync(message: LocalString): Promise<boolean> {
        const result = await showMessageAsync({
            message: message,
            title: "confirm",
            actions: ["yes", "no"]
        });
        return result == "yes";
    }

    /****************************************/

    export function info(message: LocalString) {

        const toast = new Toast({ message: message });

        toast.showAsync();
    }

    /****************************************/

    export function warning(message: LocalString) {

        const toast = new Toast({ message: message, styles: ["warning"] });

        toast.showAsync();
    }

    /****************************************/

    export function error(message: LocalString) {

        const toast = new Toast({ message: message, styles: ["error"], icon: "fas fa-exclamation" });

        toast.showAsync();
    }


    /****************************************/

    export async function succeed(message: IMessageOptions) {

        const page = await Actions.loadPageAsync(new ContentActivity<string>({
            providers: [new MessageContent({
                icon: "far fa-smile",
                customActions: [{
                    name: "close",
                    displayName: "close",
                    executeAsync: () => Promise.resolve(),
                }],
                ...message
            })],
            name: "message"
        }));

        return await page.result;
    }

    /****************************************/

    export async function fail(message: IMessageOptions) {

        const page = await Actions.loadPageAsync(new ContentActivity<string>({
            providers: [new MessageContent({
                icon: "far fa-sad-tear",
                customActions: [{
                    name: "close",
                    displayName: "close",
                    executeAsync: () => Promise.resolve(),
                }], ...message
            })],
            name: "message"
        }));

        return await page.result;
    }
}