import { IObservableProperty, IPropertyChangedHandler } from "./Abstraction/IObservableProperty";
import { forEachRev } from "./Binder";

export class ComputedProperty<TValue = any> implements IObservableProperty<TValue> {

    private _handlers: IPropertyChangedHandler<TValue>[];

    /****************************************/

    constructor(getter: () => TValue, name: string) {

        this.get = getter;

        this.name = name;
    }

    /****************************************/

    get(): TValue {

        throw "Not implemented";
    }

    /****************************************/

    set(value: TValue) {

        //throw "Not supported";
    }

    /****************************************/

    notifyChanged() {

        const value = this.get();

        forEachRev(this._handlers, handler =>
            handler(value, undefined));
    }

    /****************************************/

    subscribe(handler: IPropertyChangedHandler<TValue>) {
        if (!this._handlers)
            this._handlers = [];
        const index = this._handlers.indexOf(handler);
        if (index == -1)
            this._handlers.push(handler);
        return handler;
    }

    /****************************************/

    unsubscribe(handler: IPropertyChangedHandler<TValue>) {
        if (!this._handlers)
            return;
        const index = this._handlers.indexOf(handler);
        if (index != -1)
            this._handlers.splice(index, 1);
    }

    /****************************************/

    readonly name: string;

    readonly isReadOnly = true;
}