import { LocalString } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { BindableObject } from "../../Templating/BindableObject";
import { Bindable } from "../../Templating/Properties";
import { IAsyncEditor } from "../Abstraction/IAsyncEditor";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { ContentActivity } from "../Activities/ContentActivity";
import { ItemEditContent } from "../Content/ItemEditContent";
import { Actions } from "../Helpers/Actions";

export interface IActivityEditorConfig<TItem, TEdit> {

    editor: IItemEditor<TItem, TEdit>;

    title?: Bindable<LocalString>;

    editToItemAsync?: (edit: TEdit) => Promise<TItem>;
}

/********************************************/

export class ActivityEditor<TItem, TEdit = TItem> extends BindableObject implements IAsyncEditor<TItem, TEdit> {

    constructor(config?: IActivityEditorConfig<TItem, TEdit>) {

        super();

        this.bindConfig("editor", config);

        this.bindConfigString("title", config);

        if (config) {

            if (config.editToItemAsync)
                this.editToItemAsync = config.editToItemAsync;
        }
    }

    /********************************************/

    async editAsync(value: TItem, validate?: (edit: TEdit) => Promise<boolean>): Promise<TEdit> {

        const activity = new ContentActivity<TItem>({
            providers: [new ItemEditContent<TItem, TEdit>({
                editor: this.editor,
                title: this.title,
                value: value,
                saveItemAsync: async edit => {
                    if (validate) {
                        const valResult = await validate(edit);
                        if (valResult == null)
                            return null;
                    }
                    return await this.editToItemAsync(edit);
                }
            })]
        });

        const result = await (await Actions.loadPageAsync(activity, { loadMode: "add" })).result;
        if (result == null)
            return null;

        return this.editor.value;
    }

    /********************************************/

    protected editToItemAsync(edit: TEdit): Promise<TItem> {
        return Promise.resolve(edit as unknown as TItem);
    }

    /********************************************/

    editor: IItemEditor<TItem, TEdit> = null;

    title: string = "Edit";
}


registerType(ActivityEditor, "ActivityEditor");