export interface ITimeFunction {
    (time: number): number;
}

/****************************************/

export interface IAnimateOptions {
    duration: number;
    timeFunction: ITimeFunction;
    step: (time: number) => void;
    stepTime: number;
}

/****************************************/

export namespace Animation {

    export function cubic(p0: number, p1: number, p2: number, p3: number): ITimeFunction {
        return t =>
            Math.pow(1 - t, 3) * p0 +
            3 * Math.pow(1 - t, 2) * t * p1 +
            3 * (1 - t) * Math.pow(t, 2) * p2 +
            Math.pow(t, 3) * p3;
    }

    /****************************************/

    export function linear(): ITimeFunction {
        return t => t;
    }

    /****************************************/

    export function animate(options: IAnimateOptions) {

        let curTime = 0;

        const handler = setInterval(() => {
            if (curTime > options.duration)
                curTime = options.duration;

            options.step(options.timeFunction(curTime / options.duration));

            if (curTime == options.duration)
                clearInterval(handler);

            curTime += options.stepTime;

        }, options.stepTime);
    }
}