import { setEnumerable } from "../../Templating/Functions";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";
import { BaseTextEditor, IBaseTextEditorConfig } from "./BaseTextEditor";
import "Templates/Attach.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

declare var Quill: any;

export interface IRichTextEditorConfig extends IBaseTextEditorConfig<string> {

}

/****************************************/

export class RichTextEditor extends BaseTextEditor<string> implements IHTMLAttach {

    constructor(config?: IRichTextEditorConfig) {

        super({ template: "Attach", ...config });
    }

    /****************************************/

    attach(element: HTMLElement): void {

        this.editor = new Quill(element, {
            theme: "snow",
        });

        this.editor.root.innerHTML = this.value;

        this.editor.on("text-change", () => this.notifyEditValueChangedAsync());
    }

    /****************************************/

    set editValue(value: string) {
        if (this.editor)
            this.editor.root.innerHTML = value;
    }

    get editValue() {
        if (this.editor)
            return this.editor.root.innerHTML;
        return this.value;
    }

    /****************************************/

    editor: any;
}

registerType(RichTextEditor, "RichTextEditor");