import { Strings } from "../StringTable";

export type LocalString = IDynamicString | string | keyof IStringTable;

export enum WordNumber {
    Singular,
    Plural
}

/****************************************/

export enum WordGender {
    Male,
    Female
}

/****************************************/

export enum StringUsage {
    General,
    Label,
    Column,
    Action,
    Message,
    Question,
    Title,
    Tooltip
}

/****************************************/

export interface IDynamicStringConfig {
    useArticle?: boolean;
    useArticleInd?: boolean;
    cardinality?: number;
    number?: WordNumber;
    gender?: WordGender;
    scope?: string;
    language?: string;
    usage?: StringUsage;
    params?: Array<LocalString>;
}

/****************************************/

export interface IDynamicString {
    (config?: IDynamicStringConfig): string;
};


/****************************************/

interface IFullNameConfig {
    detArticle?: string;
    undetArticle?: string;
    name: string;
}

/****************************************/

interface IAdverbConfig {
    male?: string;
    female?: string;
}

/****************************************/

interface INameConfig {
    gender?: WordGender;
    singular: IFullNameConfig;
    plural?: IFullNameConfig;
}

/****************************************/

interface IAdjectiveConfig {
    male?: INameConfig;
    female?: INameConfig;
}

/****************************************/

class DynamicStringManager {

    format(Strings: IDynamicString): IDynamicString {
        return cfg => {
            let curValue = Strings(cfg);

            if (cfg) {

                if (cfg.usage === StringUsage.Column ||
                    cfg.usage === StringUsage.Label ||
                    cfg.usage === StringUsage.Action ||
                    cfg.usage === StringUsage.Title ||
                    cfg.usage === StringUsage.Question ||
                    cfg.usage === StringUsage.Tooltip ||
                    cfg.usage === StringUsage.Message)
                    curValue = curValue.substr(0, 1).toUpperCase() + curValue.substr(1);

                if (cfg.usage === StringUsage.Label)
                    curValue += ":";

                if (cfg.usage === StringUsage.Message)
                    curValue += ".";

                if (cfg.usage === StringUsage.Question)
                    curValue += "?";
            }

            return curValue;
        };
    }

    get(name: string): IDynamicString {
        const value = Strings[name];
        if (typeof value == "string")
            return DynamicString.simple(value);
        return value;
    }

    /****************************************/

    getValue(value: LocalString, config?: IDynamicStringConfig): string;

    getValue(value: LocalString, usage?: StringUsage): string;

    getValue(value: LocalString, usageOrConfig?: any): string {

        if (typeof value == "function") {

            let cfg: IDynamicStringConfig;

            if (usageOrConfig) {
                if (StringUsage[usageOrConfig])
                    cfg = {
                        usage: usageOrConfig
                    };
                else
                    cfg = usageOrConfig;
            }

            return (value as IDynamicString)(cfg);
        }
        else {

            if (value && Strings && value in Strings)
                return this.getValue(this.get(value as string), usageOrConfig);
        }

        return <string>value;
    }

    /****************************************/

    complex(value: string): IDynamicString {
        return DynamicString.format(cfg => {

            let state = 0;
            let result = "";
            let curName = "";
            let curNameParams: string = null;
            let curNameParamsDeep = 0;

            if (!cfg)
                cfg = {};

            if (!cfg.gender && cfg.params) {
                //Forzo la valutazione dei parametri x estrarre il genere
                cfg.params.forEach(a => {
                    if (typeof a === "function")
                        (a as IDynamicString)({ ...cfg, params: [] });
                });
            }

            for (let i = 0; i < value.length; i++) {
                let c = value[i];
                switch (state) {
                    case 0:
                        if (c == "{") {
                            curName = "";
                            curNameParams = null;
                            state = 1;
                        }
                        else
                            result += c;
                        break;
                    case 1:
                        if (c == "}") {
                            state = 3;
                            i--;
                            break;
                        }
                        else if (c == ":") {
                            curNameParams = "";
                            curNameParamsDeep = 0;
                            state = 2;
                        }
                        else
                            curName += c;
                        break;
                    case 2:
                        if (c == "{")
                            curNameParamsDeep++;

                        if (c == "}") {
                            curNameParamsDeep--;
                            if (curNameParamsDeep < 0) {
                                state = 3;
                                i--;
                            }
                            else
                                curNameParams += c;
                        }
                        else
                            curNameParams += c;
                        break;
                    case 3:

                        let paramCfg: IDynamicStringConfig;

                        if (curNameParams)
                            paramCfg = <IDynamicStringConfig>JSON.parse(curNameParams.trim());
                        else
                            paramCfg = {};

                        let flagFound = true;

                        while (flagFound) {

                            flagFound = false;

                            if (curName.startsWith("*")) {
                                paramCfg.number = WordNumber.Plural;
                                curName = curName.substr(1);
                                flagFound = true;
                            }

                            if (curName.startsWith("!")) {
                                paramCfg.useArticle = true;
                                curName = curName.substr(1);
                                flagFound = true;
                            }

                            if (curName.startsWith(".")) {
                                paramCfg.useArticleInd = true;
                                curName = curName.substr(1);
                                flagFound = true;
                            }
                        }

                        if (cfg.gender && !paramCfg.gender)
                            paramCfg.gender = cfg.gender;

                        if (cfg.cardinality && !paramCfg.cardinality)
                            paramCfg.cardinality = cfg.cardinality;

                        if (cfg.number && !paramCfg.number)
                            paramCfg.number = cfg.number;

                        if (cfg.useArticle !== undefined && paramCfg.useArticle === undefined)
                            paramCfg.useArticle = cfg.useArticle;


                        const number = parseInt(curName);

                        if (!isNaN(number)) {
                            if (cfg.params && number < cfg.params.length) {
                                const param = cfg.params[number];
                                result += this.getValue(param, paramCfg);
                            }
                        }
                        else
                            result += this.get(curName)(paramCfg);

                        if (paramCfg.gender)
                            cfg.gender = paramCfg.gender;

                        state = 0;
                        break;
                }
            }

            return result;

        });
    }

    /****************************************/

    adverb(advCfg: IAdverbConfig): IDynamicString {
        return this.format(cfg => {

            return cfg && cfg.gender == WordGender.Female ? advCfg.female : advCfg.male;
        });
    }

    /****************************************/

    private appendArticle(article: string, name: string): string {
        let result = article;
        if (result.charAt(result.length - 1) != "'")
            result += " ";
        return result + name;
    }

    /****************************************/

    adjective(adjCfg: IAdjectiveConfig): IDynamicString {
        return this.format(cfg => {

            const gender = cfg && cfg.gender == WordGender.Female ? adjCfg.female : adjCfg.male;

            const fullName = cfg && (cfg.number == WordNumber.Plural || cfg.cardinality > 1) ? gender.plural : gender.singular;

            if (cfg && cfg.useArticle)
                return this.appendArticle(fullName.detArticle, fullName.name);

            if (cfg && cfg.useArticleInd)
                return this.appendArticle(fullName.undetArticle, fullName.name);

            return fullName.name;
        });
    }

    /****************************************/

    noun(nameCfg: INameConfig): IDynamicString {
        return this.format(cfg => {

            if (!cfg)
                cfg = {};

            if (nameCfg.gender)
                cfg.gender = nameCfg.gender;

            const fullName = (cfg.number == WordNumber.Plural || cfg.cardinality > 1) ? nameCfg.plural : nameCfg.singular;

            if (cfg && cfg.useArticle)
                return this.appendArticle(fullName.detArticle, fullName.name);

            if (cfg && cfg.useArticleInd)
                return this.appendArticle(fullName.undetArticle, fullName.name);

            return fullName.name;
        });
    }

    /****************************************/

    map(selector: (table: IStringTable) => IDynamicString): IDynamicString {
        return cfg => selector(Strings)(cfg);
    }

    /****************************************/

    simple(value: string): IDynamicString {
        return this.format(() => value);
    }
}

/****************************************/

export var DynamicString = new DynamicStringManager();

export function $string(name: string): IDynamicString {
    return DynamicString.get(name);
}
