import { registerType } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { IAsyncLoad } from "../Abstraction/IAsyncLoad";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";import { loadVideoAsync, loadVideoIntoAsync } from "../Helpers/VideoLoader";
;
import { ContentView, IContentViewConfig } from "./ContentView";

export interface IVideoViewConfig extends IContentViewConfig<string> {
    isAutoPlay?: Bindable<boolean>;

    isMuted?: Bindable<boolean>;

    showControls?: Bindable<boolean>;

    isLoop?: Bindable<boolean>;
}

/********************************************/

export class VideoView extends ContentView<string> implements IHTMLAttach, IAsyncLoad {

    protected _video: HTMLVideoElement;

    constructor(config?: IVideoViewConfig) {

        super({ template: "VideoView", ...config });

        this.bindConfig("isAutoPlay", config);

        this.bindConfig("isMuted", config);

        this.bindConfig("showControls", config);

        this.bindConfig("isLoop", config);
    }


    /********************************************/

    protected onContentChanged() {

        this.updateVideo();
    }

    /********************************************/

    attach(element: HTMLElement) {

        this._video = <any>element;

        this.updateVideo();
    }

    /********************************************/

    loadAsync() {

        if (this.content) {
            if (this._video)
                return loadVideoIntoAsync(this.content, this._video);
            else
                return loadVideoAsync(this.content);
        }
        return Promise.resolve();
    }

    /********************************************/

    protected updateVideo() {

        if (!this._video)
            return;

        this._video.muted = this.isMuted;
        this._video.autoplay = this.isAutoPlay;
        this._video.controls = this.showControls;
        this._video.loop = this.isLoop;

        if (this.content)
            this._video.load();
    }

    /********************************************/

    play() {
        if (this._video)
            this._video.play();
    }

    /********************************************/

    pause() {
        if (this._video)
            this._video.pause();
    }

    /********************************************/

    isAutoPlay: boolean = true;

    isMuted?: boolean = true;

    showControls: boolean = false;

    isLoop: boolean = true;
}

registerType(VideoView, "VideoView");