import { parentOfClass } from "../../Core/Utils/DomUtils";
import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { IBehavoir } from "../../Templating/Abstraction/IBehavoir";
import { IScrollListener } from "../Abstraction/IScrollListener";
import { IView } from "../Abstraction/IView";

export class ScrollCheckBehavoir implements IBehavoir<HTMLElement, IView & IScrollListener> {

    async attach(element: HTMLElement, viewModel?: IView & IScrollListener) {

        await delayAsync(0);

        const scrollParent  = element.classList.contains("scroll") ? element : parentOfClass(element, "scroll");

        let isRequested = false;

        function computeScroll() {

            if (!viewModel.isScrollCheckActive || isRequested)
                return;

            isRequested = true;

            requestAnimationFrame(() => {

                let found = false;

                if (scrollParent != document.scrollingElement) {

                    let curItem: Node = element;
                    found = false;
                    while (curItem != null) {
                        if (curItem == scrollParent) {
                            found = true;
                            break;
                        }
                        curItem = curItem.parentNode;
                    }
                }
                else
                    found = true;

                if (!found)
                    scrollParent.removeEventListener("scroll", computeScroll);
                else {

                    viewModel.onScroll({
                        offsetTop: scrollParent.scrollTop,
                        offsetBottom: scrollParent.scrollHeight - (scrollParent.scrollTop + scrollParent.clientHeight),
                        pageBottom: (scrollParent.scrollHeight - (scrollParent.scrollTop + scrollParent.clientHeight)) / scrollParent.clientHeight,
                        pageTop: scrollParent.scrollTop / scrollParent.clientHeight,
                        totPages: scrollParent.scrollHeight / scrollParent.clientHeight
                    });
                }

                isRequested = false;
            });
        }

        if (scrollParent != null) {
            if (scrollParent == document.scrollingElement)
                window.addEventListener("scroll", computeScroll, { passive: true });
            else
                scrollParent.addEventListener("scroll", computeScroll, { passive: true });
        }
    }

    /****************************************/

    detach(element: HTMLElement, viewModel?: IView & IScrollListener) {
    }

    /****************************************/

    static readonly instance = new ScrollCheckBehavoir();
}