import { generateId } from "../../Core/Utils/Identifier";
import { getTypeName, registerType } from "../../Core/Utils/ObjectUtils";
import { setEnumerable } from "../../Templating/Functions";
import { Bindable, IValueConverter } from "../../Templating/Properties";
import { EditorCommitMode, EditorStatus, IEditor } from "../Abstraction/IEditor";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { IViewComponentConfig, ViewComponent } from "../Components/ViewComponent";
import { formatForCss } from "../Helpers/ViewUtils";

export type ValidationMode = "onchanged" | "always" | "manual";

export interface IBaseEditorConfig<TValue, TEdit = any> extends IViewComponentConfig {

    allowNull?: boolean;

    value?: Bindable<TValue>;

    hasFocus?: Bindable<boolean>;

    validationMode?: ValidationMode;

    commitMode?: EditorCommitMode;

    onCommit?: (editor: BaseEditor<TValue, any>) => boolean;

    converter?: IValueConverter<TValue, TEdit>;
}

/********************************************/

export abstract class BaseEditor<TValue, TEdit = TValue> extends ViewComponent implements IEditor<TValue, TEdit>, IItemEditor<TValue, TValue> {

    protected _needValidation: boolean = false;
    protected _isEditing: number = 0;
    protected _onCommit: (editor: this) => boolean;
    protected _editValue: TEdit = null;

    /********************************************/

    constructor(config?: IBaseEditorConfig<TValue, TEdit>) {

        super({ ...config });

        //TODO: hack, props are not enumerable in ts 3.9
        setEnumerable(this, "editValue");

        this._onCommit = config?.onCommit;

        this.bindConfig("allowNull", config);

        this.bindConfig("hasFocus", config);

        this.bindConfig("value", config);

        this.bindConfig("commitMode", config);

        this.bindConfig("validationMode", config);

        this.prop("displayValue");

        this.prop("value").subscribe((value, old) =>
            this.onValueChanged(value, old));

        this.prop("name").subscribe(() => this.updateUid());

        if (config.converter) {
            this.editToValue = config.converter.convertFrom;
            this.valueToEdit = config.converter.convertTo;
        }

        this.updateUid();
    }

    /********************************************/

    protected updateUid() {
        this.uid = generateId(formatForCss(this.name ?? getTypeName(this)));
    }

    /********************************************/

    protected createEditValueProp() {

        this.prop("editValue").subscribe(() => this.notifyEditValueChangedAsync());
    }

    /********************************************/

    protected async notifyEditValueChangedAsync() {

        console.debug("value changed: ", this.debugName);

        this._needValidation = true;

        this.isDirty = true;

        if (!this._isEditing) {

            if (this.commitMode == "onchange")
                await this.commitAsync();
            else
                await this.validateAsync();
        }
    }

    /********************************************/

    protected onValueChanged(value: TValue, oldValue?: TValue) {

        if (this.status == "commiting")
            return;

        this.beginEditAsync(value);
    }

    /********************************************/

    clear() {
        this.value = null;
    }

    /********************************************/

    async loadAsync(): Promise<void> {

        console.group("begin load: ", this.debugName, " - status: ", this.status);

        try {

            if (this.status != "none")
                return;

            this.status = "loading";

            try {

                await this.loadAsyncWork();
            }
            finally {

                this.status = "loaded";
            }
        }
        finally {
            console.groupEnd();
        }
    }

    /********************************************/

    clearError() {
        this.error = null;
    }

    /********************************************/

    protected loadAsyncWork(): Promise<void> {

        return Promise.resolve();
    }

    /********************************************/

    async beginEditAsync(value: TValue) {

        if (this._isEditing)
            return;

        console.group("begin edit: ", this.debugName);

        this._isEditing++;

        try {

            if (this.status == "none")
                await this.loadAsync();

            this.value = value;

            this.editValue = this.valueToEdit(value);

            await this.beginEditWorkAsync(value);

            this._needValidation = true;
        }
        finally {
            this._isEditing--;
            console.groupEnd();
        }
    }

    /********************************************/

    protected valueToEdit(value: TValue): TEdit {
        return <any>value;
    }

    /********************************************/

    protected editToValue(value: TEdit): TValue {
        return <any>value;
    }

    /********************************************/

    protected beginEditWorkAsync(value: TValue) {

        return Promise.resolve();
    }

    /********************************************/

    async validateAsync(force?: boolean): Promise<boolean> {


        console.group("begin validation: " + this.debugName, " - needValidation: ", this._needValidation);

        try {

            if (this._isEditing)
                return undefined;

            if (!this._needValidation && this.validationMode == "onchanged" && !force)
                return this.isValid;

            this.clearError();

            const validationResult = await this.validateAsyncWork(force);

            if (validationResult !== undefined) {

                this.isValid = validationResult;
                this._needValidation = false;
            }

            console.debug("validation: ", this.debugName, " = ", this.isValid);

            return this.isValid;
        }
        finally {
            console.groupEnd();
        }

    }

    /********************************************/

    protected validateAsyncWork(force?: boolean): Promise<boolean> {

        return Promise.resolve(true);
    }

    /********************************************/

    async commitAsync(force?: boolean): Promise<boolean> {

        console.group("begin commit: ", this.debugName, " isDirty: ", this.isDirty, " - status: ", this.status);
        try {

            if (this._isEditing)
                return false;

            if (!force && this.commitMode != "manual" && !this.isDirty)
                return true;

            if (this.status != "loaded")
                return this.status == "commiting";

            this.status = "commiting";

            try {
                if (!await this.validateAsync(force))
                    return false;

                if (!await this.commitAsyncWork(force))
                    return false;

                const newValue = this.editToValue(this.editValue);
                if (newValue !== undefined)
                    this.value = newValue;

                if (this._onCommit)
                    this._onCommit(this);

                this.isDirty = false;

                return true;
            }
            finally {
                this.status = "loaded";
            }
        }
        finally {
            console.groupEnd();
        }
    }

    /********************************************/

    activateAsync() {
        return Promise.resolve();
    }

    /********************************************/

    protected commitAsyncWork(force?: boolean): Promise<boolean> {

        return Promise.resolve(true);
    }

    /********************************************/

    rollback(): void {

        this.editValue = this.valueToEdit(this.value);
    }

    /********************************************/

    get view() {
        return this;
    }

    /********************************************/

    get editValue() {
        return this._editValue;
    }

    set editValue(value: TEdit) {
        this._editValue = value;
    }

    /********************************************/

    isValid: boolean = true

    status: EditorStatus = "none";

    commitMode: EditorCommitMode = "onchange";

    validationMode: ValidationMode = "onchanged";

    allowNull: boolean = true;

    displayValue: string = null;

    value: TValue = null;

    hasFocus: boolean = false;

    isDirty: boolean = false;

    uid: string = null;

    error: string = null;
}

registerType(BaseEditor, "BaseEditor");