import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { BindableObject } from "../../Templating/BindableObject";
import { observableListOf } from "../../Templating/ObservableList";
import { TemplateBuilder } from "../../Templating/TemplateBuilder";
import { IAction } from "../Abstraction/IAction";
import { ActionView } from "./ActionView";
import "Templates/ContextMenu.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface IContextMenuConfig {

    actions?: IAction[];
}

/****************************************/

export class ContextMenu extends BindableObject {

    private _menuContainer: HTMLElement;
    private _clickHandler;

    /****************************************/

    constructor(config?: IContextMenuConfig) {
        super();

        if (config) {
            if (config.actions)
                config.actions.forEach(a => this.addAction(a));
        }

        this._menuContainer = document.createElement("DIV");
        this._menuContainer.className = "popup-container";

        this._clickHandler = this.onClick.bind(this);
    }

    /****************************************/

    addAction(action: IAction) {

        this.actions.add(ActionView.fromAction(action));
    }

    /****************************************/

    async showAsync(element?: HTMLElement, event?: MouseEvent) {

        let curOfs = { x: 0, y: 0 };

        if (!element && event) {
            element = <HTMLElement>event.srcElement;
            curOfs.x = event.offsetX;
            curOfs.y = event.offsetY;
        }

        const builder = new TemplateBuilder(this, this._menuContainer);

        builder.template("ContextMenu", a => a);

        document.body.appendChild(this._menuContainer);

        await delayAsync(0);

        window.addEventListener("mouseup", this._clickHandler);

        let curEl = element;
        let offsetEl = element;

        while (curEl) {
            if (curEl == offsetEl) {
                curOfs.y += curEl.offsetTop;
                curOfs.x += curEl.offsetLeft;
                offsetEl = <HTMLElement>curEl.offsetParent;
            }
            curOfs.y -= curEl.scrollTop;
            curOfs.x -= curEl.scrollLeft;
            curEl = curEl.parentElement;
        }

        let xTrans = "";
        let yTrans = "";

        if (curOfs.x + this._menuContainer.clientWidth > document.body.clientWidth) {
            curOfs.x -= this._menuContainer.clientWidth;
            xTrans = "right";
        }
        else
            xTrans = "left";

        if (curOfs.y + this._menuContainer.clientHeight > document.body.clientHeight) {
            curOfs.y -= this._menuContainer.clientHeight;
            yTrans = "bottom";
        }
        else
            yTrans = "top";

        this._menuContainer.style.top = curOfs.y + "px";
        this._menuContainer.style.left = curOfs.x + "px";
        this._menuContainer.style.transformOrigin = xTrans + " " + yTrans;

        await delayAsync(0);

        this._menuContainer.classList.add("visible");
    }

    /****************************************/

    hide() {

        this._menuContainer.classList.remove("visible");

        window.removeEventListener("mouseup", this._clickHandler);

        setTimeout(() => document.body.removeChild(this._menuContainer), 500);
    }

    /****************************************/

    protected onClick(e: MouseEvent) {
        setTimeout(() => this.hide(), 0);
    }

    /****************************************/

    readonly actions: IObservableList<ActionView> = observableListOf();

    className: string = "context-menu";
}

registerType(ContextMenu, "ContextMenu");