import { Format } from "../../Core/Helpers/Format";
import { IValidationContext, IValidationResult, IValidator } from "../Abstraction/IValidator";

export namespace Validators {

    const EMAIL_REGEXP = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    const PHONE_REGEXP = /^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\s\./0-9]*$/;

    export var required: IValidator<any, any> = {
        validateAsync: ctx => Promise.resolve({
            isValid: ctx.value != null && ctx.value != "" && (!Array.isArray(ctx.value) || ctx.value.length > 0),
            error: Format.message("msg-specify-value", ctx.fieldName)
        } as IValidationResult)
    };

    /****************************************/

    export function minValue(value: number): IValidator<number, any> {
        return {
            validateAsync: ctx => Promise.resolve(({
                isValid: ctx.value >= value,
                error: Format.message("msg-insert-greater-than", value.toString())
            }))
        }
    };

    /****************************************/

    export function maxValue(value: number): IValidator<number, any> {
        return {
            validateAsync: ctx => Promise.resolve(({
                isValid: ctx.value <= value,
                error: Format.message("msg-insert-less-than", value.toString()),
            }))
        }
    };

/****************************************/

   

    export var integer: IValidator<number, any> = {
        validateAsync: ctx => Promise.resolve(({
            isValid: Math.round(ctx.value) == ctx.value,
            error: Format.message("msg-insert-int-number"),
        }))
    };


    /****************************************/

    export var phone: IValidator<string, any> = {

        validateAsync: ctx => Promise.resolve(({
            isValid: ctx.value && ctx.value.length >= 9 && PHONE_REGEXP.test(ctx.value.toLowerCase()),
            error: Format.message("msg-invalid-phone", ctx.value)
        }))

    };



    /****************************************/

    export var email: IValidator<string, any> = {

        validateAsync: ctx => Promise.resolve(({
            isValid: ctx.value && EMAIL_REGEXP.test(ctx.value.toLowerCase()),
            error: Format.message("msg-invalid-email", ctx.value)
        }))

    };

    /****************************************/

    export function custom<TValue, TTarget>(validator: (ctx: IValidationContext<TValue, TTarget>) => Promise<IValidationResult>) {
        return {
            validateAsync: async ctx => await validator(ctx)
        }
    };

}