import { IErrorHandler } from "../../Core/Abstraction/IErrorHandler";
import { App, Services } from "../../Core/Global";
import { LocalString } from "../../Core/Types/DynamicString";
import { getTypeName, registerType } from "../../Core/Utils/ObjectUtils";
import { BindableObject } from "../../Templating/BindableObject";
import { Bindable } from "../../Templating/Properties";
import { IPage, PageStatus } from "../Abstraction/IPage";
import { IMultiPageHost, IPageHost } from "../Abstraction/IPageHost";
import { IView } from "../Abstraction/IView";

export interface IPageConfig<TView extends IView> {

    name?: string;

    url?: Bindable<string>;

    view?: Bindable<TView>;

    title?: Bindable<LocalString>;

    subTitle?: Bindable<LocalString>;
}

/****************************************/

export class Page<TView extends IView = IView> extends BindableObject implements IPage<TView>, IErrorHandler<Page> {

    constructor(config?: IPageConfig<TView>) {

        super();

        this.bindConfig("url", config);

        this.bindConfigString("title", config);

        this.bindConfigString("subTitle", config);

        this.bindConfig("view", config);

        if (config) {
            if (config.name)
                this.name = config.name;
        }
    }

    /****************************************/

    getState(): object {
        return {};
    }

    /****************************************/

    setState(state: object) {

    }

    /****************************************/

    handleError(source: this, error: any) {
        App.handleError(source, error);
    }

    /****************************************/

    async loadAsync(): Promise<void> {
        if (this.status == "notloaded" || this.status == "error") {

            let op = Services.operation.begin({ message: "Loading page: " + (this.title ? this.title : (this.url ? this.url : getTypeName(this))) });
            this.status = "loading";
            try {

                await this.loadWorkAsync();
            }
            catch (e) {
                this.status = "error";
                App.handleError(this, e);
            }
            finally {
                op.end();
                this.status = "loaded";
            }
        }
    }

    /****************************************/

    protected loadWorkAsync(): Promise<void> {
        return Promise.resolve();
    }

    /****************************************/

    refreshAsync(): Promise<void> {
        return Promise.resolve();
    }

    /****************************************/

    async closeAsync(result?: any) {

        if ("goBackAsync" in this.host)
            await (this.host as IMultiPageHost).goBackAsync();

        this.status = "closed";

        return true;
    }

    /****************************************/

    url: string = null;

    host: IPageHost = null;

    view: TView = null;

    title: string = null;

    subTitle: string = null;

    name: string = null;

    status: PageStatus = "notloaded";

    args: object = null;
}

registerType(Page, "Page");