import { Type } from "../../Core/Types/Types";
import { IPage } from "./IPage";

declare global {

    export interface IServices {

        pageHost: IPageHost;
    }
}


export type PageTransactionType = "none" | "dissolve" | "h-slide" | "pop-up-down" | "up-down";

export type PageLoadMode = "add" | "replace" | "clear";

export enum PageHostMode {
    Default,
    WebView
}

/****************************************/

export interface IPageLoadOptions {

    transaction?: PageTransactionType;

    hostMode?: PageHostMode;

    loadMode?: PageLoadMode;
}

/****************************************/

export interface IPageHost {

    clearAsync(): void;

    loadAsync<TPage extends IPage>(page: TPage, options?: IPageLoadOptions): Promise<TPage>;

    readonly current: IPage;
}

/****************************************/

export interface IMultiPageHost extends IPageHost {

    find<TPage extends IPage>(type: Type<TPage>): TPage;

    find<TPage extends IPage>(name: string): TPage;

    bringFrontAsync(page: IPage): Promise<void>;

    goBackAsync(): Promise<void>;

    goForwardAsync(): Promise<void>;

    goToAsync(index: number): Promise<void>;

    currentIndex: number;

    get(index: number): IPage;

    readonly pageCount: number;

    readonly canGoBack: boolean;
}