import { DynamicString, IDynamicStringConfig, LocalString, StringUsage } from "../Core/Types/DynamicString";
import { IBindable } from "./Abstraction/IBindable";
import { IObservableProperty, IPropertyChangedHandler } from "./Abstraction/IObservableProperty";
import { Binder } from "./Binder";
import { ComputedProperty } from "./ComputedProperty";
import { Bindable, IValueConverter, Properties } from "./Properties";

export class BindableObject implements IBindable {

    private _computedBinder: Binder<this>;

    prop<TKey extends keyof this & string>(propName: TKey): IObservableProperty<this[TKey]> {
        return Properties.getOrCreate(this, propName);
    }

    /****************************************/

    static bindValue<T>(value: Bindable<T>): T {
        if (Properties.isProperty(value))
            return value.get();
        return <any>value;
    }

    /****************************************/

    bindConfig<TConfig, TKey extends keyof this & keyof TConfig & string, TSrc extends TConfig[TKey], TDest extends this[TKey]>(propName: TKey, config: TConfig, converter?: IValueConverter<TSrc, TDest>, onValueChanged?: IPropertyChangedHandler<TDest>) {

        this.bind(propName, config && propName in config ? <Bindable<TSrc>>config[propName] : undefined, converter, onValueChanged);
    }

    /****************************************/


    bindConfigString<TConfig, TKey extends keyof this & keyof TConfig & string>(propName: TKey, config: TConfig, configOrUsage?: IDynamicStringConfig | StringUsage, onValueChanged?: IPropertyChangedHandler<string>) {

        this.bindString(propName, config && propName in config ? <any>config[propName] : undefined, configOrUsage, onValueChanged);
    }

    /****************************************/

    computed<TKey extends keyof this & string, TValue extends this[TKey]>(propName: TKey, compute: (model: this) => TValue) {

        return Properties.create(this, propName, this.createComputed(compute, <string>propName));
    }

    /****************************************/

    createComputed<TValue>(compute: (model: this) => TValue, name?: string): IObservableProperty<TValue> {

        if (!this._computedBinder)
            this._computedBinder = new Binder(this);

        const prop = new ComputedProperty(() => compute(this), name);

        this._computedBinder.bind(compute, () => prop.notifyChanged());

        return prop;
    }

    /****************************************/

    bind<TKey extends keyof this & string, TSrc, TDest extends this[TKey]>(propName: TKey, value: Bindable<TSrc>, converter?: IValueConverter<TSrc, TDest>, onValueChanged?: IPropertyChangedHandler<TDest>) {

        const prop = Properties.getOrCreate(this, propName);

        if (onValueChanged)
            prop.subscribe(onValueChanged);

        if (value !== undefined)
            Properties.bind(prop, value, converter);
    }

    /****************************************/

    bindString<TKey extends keyof this & string>(propName: TKey, value: Bindable<LocalString>, configOrUsage?: IDynamicStringConfig | StringUsage, onValueChanged?: IPropertyChangedHandler<string>) {

        return this.bind(propName, value, {
            convertTo: a => <any>DynamicString.getValue(a, <any>configOrUsage),
            convertFrom: a => <any>a
        }, <any>onValueChanged);
    }

    /****************************************/

    apply(block: (obj: this) => any): this {
        block(this);
        return this;
    }
}