
import { Services } from "../../Core/Global";
import { Format } from "../../Core/Helpers/Format";
import { linq } from "../../Core/Linq";
import { DynamicString, StringUsage } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { ActionPriority, IAction } from "../Abstraction/IAction";
import { IMultiPageHost } from "../Abstraction/IPageHost";
import { IPopUpMenu } from "../Abstraction/IPopUpMenu";
import { ISelectable } from "../Abstraction/ISelectable";
import { ViewStyle } from "../Abstraction/IView";
import { IViewContentHost } from "../Abstraction/IViewContentHost";
import { DeactivateContentReason, IViewContentProvider } from "../Abstraction/IViewContentProvider";
import { ActionBar } from "../Components/ActionBar";
import { ActionView, IActionViewConfig } from "../Components/ActionView";
import { NavBar } from "../Components/NavBar";
import { Panel } from "../Components/Panel";
import { SearchView } from "../Components/SearchView";
import { SelectionManager } from "../SelectionManager";
import { Activity, IActivityConfig } from "./Activity";

export enum ContentSelector {
    Manual = 1,
    Tabs = 2,
    BottomNav = 3
}


export enum PrimaryActionMode {
    Floating = 1,
    Button = 2,
    FloatingFull = 3,
}

/****************************************/

export interface IContentActivitConfig<TCtx extends ContentActivity> extends IActivityConfig {

    providers: IViewContentProvider<TCtx>[];

    selector?: ContentSelector;

    menu?: IPopUpMenu;

    actions?: ActionView[];

    activeContent?: Bindable<string>;
}

/****************************************/

export class ContentActivity<TResult = any> extends Activity<TResult> implements IViewContentHost {

    private _actionBar: ActionBar;
    private _contentView: Panel;
    private _floatingView: Panel;
    private _isContentLoaded: boolean = false;
    private _navBar: NavBar<IViewContentProvider<this>>;
    private _selector: ContentSelector = ContentSelector.Tabs;
    private _isDeactivating = false;

    /****************************************/

    constructor(config?: IContentActivitConfig<ContentActivity>) {
        super({
            ...config
        });

        this.bindConfig("activeContent", config);

        if (config.providers)
            this.providers = config.providers;

        if (config.menu)
            this.menu = config.menu;

        if (config.selector)
            this._selector = config.selector;

        if (config.actions)
            this.actions = config.actions;

        this.prop("host").subscribe(() => {

            if (this._actionBar?.mainAction == "none" && (this.host as IMultiPageHost)?.canGoBack)
                this._actionBar.mainAction = "back";
        });
    }

    /****************************************/

    async closeAsync(result?: TResult) {

        await this.deactivateAsync("close");

        return await super.closeAsync(result);
    }

    /****************************************/

    activateAsync() {

        if (!this._isContentLoaded)
            return this.refreshAsync();

        if (this.activeContentProvider?.activateAsync)
            return this.activeContentProvider?.activateAsync("refresh");

        return Promise.resolve();
    }

    /****************************************/

    async deactivateAsync(reason: DeactivateContentReason) {

        if (this._isDeactivating)
            return;

        try {
            this._isDeactivating = true;
            if (this.activeContentProvider?.deactivateAsync)
                await this.activeContentProvider.deactivateAsync(reason);
        }
        finally {
            this._isDeactivating = false;
        }
    }

    /****************************************/

    refreshAsync(): Promise<void> {

        if (this.activeContentProvider)
            return this.loadContentAsync(this.activeContentProvider, true);
        return this.loadActiveContentAsync(true);
    }

    /****************************************/

    notifyContentChanged(provider: IViewContentProvider<this>) {

        this.loadContentAsync(provider, true);
    }

    /****************************************/

    startSelection?<TItem extends ISelectable>(): SelectionManager<TItem> {

        return this._actionBar.startSelection<TItem>();
    }

    /****************************************/

    protected loadActiveContentAsync(force: boolean) {

        const provider = linq(this.providers).first(a => a.info.name == this.activeContent);

        return this.loadContentAsync(provider, force);
    }

    /****************************************/

    protected async loadWorkAsync(): Promise<void> {

        await this.createAsync();

        if (this.activeContentProvider)
            this.activeContent = this.activeContentProvider.info.name;
    }

    /****************************************/

    async loadContentAsync(provider: IViewContentProvider<this>, force: boolean): Promise<void> {

        const isContentChanged = this.activeContentProvider != provider;

        if (!isContentChanged && !force)
            return;

        if (isContentChanged && this.activeContentProvider?.deactivateAsync)
            this.activeContentProvider?.deactivateAsync("replace");

        this.activeContentProvider = provider;

        if (!force && (this.status == "notloaded" || this.status == "loading" || this.status == "closed")) {
            console.debug("Skip loading content: " + provider.info.name)
            return;
        }

        const op = Services.operation.begin({ message: "Loading content: " + provider.info.name });
        try {
            const content = await provider.getContentAsync(this);

            this.title = this.formatTitle(Format.title(content.title));

            if (content.url)
                this.url = content.url;

            this._contentView.clear();
            if (content.styles)
                this._contentView.styles = content.styles;
            else
                this._contentView.styles = this.contentStyle;

            if (content.views)
                content.views.forEach(a => this._contentView.addView(a));

            this._contentView.name = provider.info.name;
            this._contentView.buildStyles();

            this._actionBar.actions.clear();

            if (this._floatingView)
                this._floatingView.clear();

            let actionPanel: Panel;

            if (content.actions) {

                content.actions.forEach(action => {

                    if (action.priority == null || action.priority == ActionPriority.Secondary)
                        this._actionBar.actions.add(this.createActionView(action));

                    else if (action.priority == ActionPriority.Primary) {
                        if (content.actionMode === undefined || content.actionMode != PrimaryActionMode.Button) {
                            this._floatingView.visible = true;
                            this._floatingView.addView(this.createActionView(action, { styles: ["floating", ...action.styles ?? []] }));
                            if (content.actionMode == PrimaryActionMode.FloatingFull)
                                this._floatingView.addStyle("full");
                            else
                                this._floatingView.removeStyle("full");
                        }
                        else {

                            this._floatingView.visible = false;

                            const actionView = ActionView.fromAction(action, { template: "ActionButton" });
                            if (!actionPanel) {
                                actionPanel = new Panel({
                                    name: "actions",
                                    styles: ["vertical", "margin-items-v"],
                                });

                                this._contentView.addView(actionPanel);
                            }

                            actionPanel.addView(actionView);
                        }
                    }
                });
            }

            if (content.searchAsync) {

                const searchView = new SearchView({
                    name: "search-expanded",
                    isExpanded: true,
                    searchAsync: content.searchAsync
                });

                this._actionBar.actions.add(searchView);
            }

            this._floatingView.visible = this._floatingView.content.count > 0;

            this.actions.forEach(a => this._actionBar.actions.add(a));

            await this._contentView.loadAsync();

            if (provider?.activateAsync)
                await provider.activateAsync("loading");

            this.onContentChanged(provider);

            this._isContentLoaded = true;
        }
        catch (e) {

            this.handleError(this, e);
        }
        finally {
            op.end();
        }
    }


    /****************************************/

    protected formatTitle(value: string) {
        return value;
    }

    /****************************************/

    protected createAsync() {

        if (this.menu) {
            this._actionBar = this.view.addView(new ActionBar({
                title: this.prop("title"),
                mainAction: "menu",
                navigationMenu: this.menu
            }));
        }
        else {
            this._actionBar = this.view.addView(new ActionBar({
                onBack: () => this.closeAsync(),
                title: this.prop("title"),
                mainAction: (this.host as IMultiPageHost).canGoBack ? "back" : "none"
            }));
        }

        if (this.providers.length > 1) {

            if (this._selector == ContentSelector.Manual) {
                this.prop("activeContent").subscribe(() => this.loadActiveContentAsync(false));
            }
            else {
                let navStyle: string;
                let itemTemplate: string;
                let itemBehavoirs: string[];

                switch (this._selector) {
                    case ContentSelector.Tabs:
                        navStyle = "tab-view";
                        itemTemplate = "TextView";
                        itemBehavoirs = ["ripple"];
                        break;
                    case ContentSelector.BottomNav:
                        navStyle = "bottom-nav";
                        itemTemplate = "IconTextView";
                        itemBehavoirs = ["ripple"];
                        break;
                }

                this._navBar = new NavBar({
                    styles: [<any>navStyle],
                    itemTemplate: itemTemplate,
                    onItemSelected: a => this.loadContentAsync(a.content, false),
                    selectedItem: this.prop("activeContent"),
                    itemBehavoirs: itemBehavoirs,
                    items: linq(this.providers).select(a => ({
                        name: a.info.name,
                        icon: a.info.icon,
                        text: a.info.displayName,
                        content: a
                    })).toArray()
                });
            }
        }

        if (this._navBar && this._selector == ContentSelector.Tabs)
            this.view.addView(this._navBar);

        const contentWrapper = this.view.addView(new Panel({ name: "content" }));

        this._contentView = contentWrapper.addView(new Panel({ name: "scroll-container", styles: this.contentStyle }));

        this._floatingView = contentWrapper.addView(new Panel({ name: "floating-container", template: "ItemsViewWrapped", visible: false, styles: this.floatingStyle }));

        if (this._navBar && this._selector == ContentSelector.BottomNav)
            this.view.addView(this._navBar);

        if (this.providers.length == 1)
            this.activeContentProvider = this.providers[0];

        this.status = "created";

        return Promise.resolve();
    }

    /****************************************/

    protected createActionView(action: IAction, config?: IActionViewConfig): ActionView {
        return ActionView.fromAction(action, config);
    }

    /****************************************/

    protected onContentChanged(newContent: IViewContentProvider<this>) {

    }

    /****************************************/

    get actionBar() { return this._actionBar; }

    activeContent: string = null;

    activeContentProvider: IViewContentProvider<this> = null;

    providers: IViewContentProvider<this>[] = [];

    menu: IPopUpMenu = null;

    actions: ActionView[] = [];

    floatingStyle: ViewStyle[] = ["vertical", "item-absolute", "bottom", "fill-h", "center-items-h"];

    contentStyle: ViewStyle[] = ["vertical", "scroll"];
}

registerType(ContentActivity, "ContentActivity");