import { LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { IAction } from "../Abstraction/IAction";
import { IPopUpMenu } from "../Abstraction/IPopUpMenu";
import { ISelectable } from "../Abstraction/ISelectable";
import { SelectionManager } from "../SelectionManager";
import { ActionView } from "./ActionView";
import { IViewComponentConfig, ViewComponent } from "./ViewComponent";
import "Templates/ActionBar.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

type ActionBarMainActionType = "none" | "menu" | "back";

/****************************************/

export interface IActionBarConfig extends IViewComponentConfig {

    actions?: IAction[];

    mainAction?: Bindable<ActionBarMainActionType>;

    navigationMenu?: Bindable<IPopUpMenu>;

    title?: Bindable<LocalString>;

    icon?: Bindable<string>;

    onBack?: () => void;
}

/****************************************/

export class ActionBar extends ViewComponent {

    constructor(config?: IActionBarConfig) {

        super(config);

        this.bindConfig("mainAction", config);

        this.bindConfig("navigationMenu", config);

        this.bindConfigString("title", config, StringUsage.Title);

        this.bindConfig("icon", config);

        this.actions.subscribe({
            onItemAdded: this.onActionAdded.bind(this),
            onItemRemoved: this.onActionRemoved.bind(this)
        });


        if (config) {

            if (config.actions)
                config.actions.forEach(a => this.addAction(a));

            if (config.onBack)
                this.back = config.onBack;
        }
    }

    /****************************************/

    protected onActionRemoved(action: ActionView) {
        if (action && action.parentView == this)
            action.parentView = null;
    }

    /****************************************/

    protected onActionAdded(action: ActionView) {

        if (action)
            action.parentView = this;
    }

    /****************************************/

    async showNavigationMenu() {

        if (this.navigationMenu)
            this.navigationMenu.showAsync();
    }

    /****************************************/

    addAction(action: IAction) {
        this.actions.add(ActionView.fromActionIcon(action));
    }

    /****************************************/

    back() {

    }

    /****************************************/

    startSelection<TItem extends ISelectable>(): SelectionManager<TItem> {

        if (!this.selectionManager)
            this.selectionManager = new SelectionManager<any>();
        this.selectionManager.isActive = true;
        return this.selectionManager;
    }

    /****************************************/

    readonly actions: IObservableList<ActionView> = observableListOf();

    mainAction: ActionBarMainActionType = "none";

    navigationMenu: IPopUpMenu = null;

    title: string = null;

    icon: string = null;

    selectionManager: SelectionManager<any> = null;
}

registerType(ActionBar, "ActionBar");