import { IOperation } from "../../Core/Abstraction/IOperation";
import { App, Services } from "../../Core/Global";
import { Format } from "../../Core/Helpers/Format";
import { Http } from "../../Core/Helpers/Http";
import { linq } from "../../Core/Linq";
import { IPage } from "../Abstraction/IPage";
import { IPageInfo, IPageManager } from "../Abstraction/IPageManager";
import { ILoader, ScriptLoader, StyleLoader } from "../Helpers/Loaders";

interface IClientPage {

    scripts?: string[];

    styles?: string[];

    action?: string;

    isLoded?: boolean;

    url?: string;
}

/****************************************/

interface IAppInclude {

    scripts: string[];

    styles: string[];

    components: string[];
}

/****************************************/

interface IAppComponent {
    include: IAppInclude;
}

/****************************************/

interface IAppPage extends IAppComponent {

    url: string;

    action: string;
}

/****************************************/

interface IAppStructure {

    pages: { [name: string]: IAppPage };

    components: { [name: string]: IAppComponent };

    templates?: { [name: string]: string };

    include?: [];
}

/****************************************/

export class DynamicPageManager implements IPageManager {

    private _struture: IAppStructure;

    /****************************************/

    async createPageAsync(pageInfo: IPageInfo): Promise<IPage> {

        const curOperation = Services.operation.begin({ message: "Loading page '" + pageInfo.name + "'..." });

        const clientPage = await this.getClientPageAsync(pageInfo.name, curOperation);

        if (!clientPage.isLoded)
            await this.loadClientPageAsync(clientPage, curOperation);

        const result = eval(clientPage.action);


        let page: IPage;

        if (result instanceof Promise)
            await <Promise<IPage>>result;

        if (page != null)
            page.url = Format.replaceArgs(App.baseUrl + clientPage.url, pageInfo.args);

        page.args = pageInfo.args;

        curOperation.end();

        return page;
    }

    /****************************************/

    async loadComponentsAsync(pageName: string) {

        const curOperation = Services.operation.begin({ message: "Loading page '" + pageName + "'..." });

        const clientPage = await this.getClientPageAsync(pageName, curOperation);

        if (clientPage && !clientPage.isLoded)
            await this.loadClientPageAsync(clientPage, curOperation);

        curOperation.end();
    }


    /****************************************/

    private async include(src: string) {
        const curStruct = await Http.getJsonAsync<IAppStructure>(src);

        if (curStruct) {

            if (curStruct.include) {
                for (let file of curStruct.include)
                    await this.include(file);
            }

            if (curStruct.components) {
                for (const key in curStruct.components)
                    this._struture.components[key] = curStruct.components[key];
            }

            if (curStruct.pages) {
                for (const key in curStruct.pages)
                    this._struture.pages[key] = curStruct.pages[key];
            }
        }
    }

    private async loadAppStructureAsync(operation?: IOperation): Promise<void> {

        if (!this._struture) {
            this._struture = {
                components: {},
                pages: {},
            };
            await this.include("~/app.json");
        }
    }

    /****************************************/

    private async loadClientPageAsync(clientPage: IClientPage, operation?: IOperation): Promise<void> {

        const loaders: ILoader[] = [];

        clientPage.isLoded = true;

        if (clientPage.scripts)
            clientPage.scripts.forEach(a => loaders.push(new ScriptLoader(a)));

        if (clientPage.styles)
            clientPage.styles.forEach(a => loaders.push(new StyleLoader(a)));

        await Promise.all(linq(loaders).select(a => a.loadAsync(operation)).toArray());
    }

    /****************************************/

    private async getClientPageAsync(name: string, operation?: IOperation): Promise<IClientPage> {

        await this.loadAppStructureAsync(operation);

        const result: IClientPage = {};

        const pageInfo = this._struture.pages[name];

        result.styles = [];
        result.scripts = [];
        result.action = pageInfo.action;
        result.url = pageInfo.url;

        this.includeClientPage(result, pageInfo.include);

        return result;
    }

    /****************************************/

    private includeClientPage(clientPage: IClientPage, include: IAppInclude) {

        if (!include)
            return;

        if (include.components)
            include.components.forEach(a => this.includeClientPage(clientPage, this._struture.components[a].include));

        if (include.styles)
            include.styles.forEach(a => {
                if (clientPage.styles.indexOf(a) == -1)
                    clientPage.styles.push(a);
            });

        if (include.scripts)
            include.scripts.forEach(a => {
                if (clientPage.scripts.indexOf(a) == -1)
                    clientPage.scripts.push(a);
            });
    }
}