import { registerType } from "../../Core/Utils/ObjectUtils";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";
import { ContentView, IContentViewConfig } from "./ContentView";

export interface IWebViewConfig extends IContentViewConfig<string> {

}

/********************************************/

export class WebView extends ContentView<string> implements IHTMLAttach {

    constructor(config?: IWebViewConfig) {

        super({ template: "WebView", ...config });
    }

    /********************************************/

    attach(element: HTMLFrameElement): void {
        this.window = element.contentWindow;
    }

    /********************************************/

    window: Window;
}


registerType(WebView, "WebView");