import { registerType } from "../../Core/Utils/ObjectUtils";
import { BindableObject } from "../../Templating/BindableObject";
import { IActivable } from "../Abstraction/IActivable";
import { IEditor } from "../Abstraction/IEditor";
import { IItemEditor } from "../Abstraction/IItemEditor";
import { isActivable } from "../Helpers/TypeCheck";

export interface IItemEditorConverterConfig<TItem, TEdit> {

    editor?: IEditor<TEdit, any>;

    itemToEdit?: (item: TItem) => TEdit;

    editToItem?: (item: TItem, editValue: TEdit) => void
}

/****************************************/

export class ItemEditorConverter<TItem, TEdit> extends BindableObject implements IItemEditor<TItem, TEdit>, IActivable {

    private _item: TItem;

    constructor(config?: IItemEditorConverterConfig<TItem, TEdit>) {

        super();

        this.bindConfig("editor", config);

        if (config) {

            if (config.itemToEdit)
                this.itemToEdit = config.itemToEdit;

            if (config.editToItem)
                this.editToItem = config.editToItem;
        }
    }

    /****************************************/

    activateAsync() {

        if (isActivable(this.editor))
            return this.editor.activateAsync();

        return Promise.resolve();
    }

    /****************************************/

    beginEditAsync(item: TItem) {

        this._item = item;
        return this.editor.beginEditAsync(this.itemToEdit(item));
    }

    /****************************************/

    async commitAsync(force?: boolean) {

        if (await this.editor.commitAsync(force)) {
            this.editToItem(this._item, this.editor.value);
            return true;
        }
        return false;
    }

    /****************************************/

    itemToEdit(item: TItem): TEdit {

        throw "Not supported";
    }

    /****************************************/

    editToItem(item: TItem, editValue: TEdit): void {

    }

    /****************************************/

    get value() {
        return this.editor.value;
    }

    get view() {
        return this.editor;
    }

    get isValid() {
        return this.editor.isValid;
    }

    editor: IEditor<TEdit, any> = null;
}

registerType(ItemEditorConverter, "ItemEditorConverter");