import { registerType } from "../../Core/Utils/ObjectUtils";
import { CatalogTemplate } from "../../Templating/Abstraction/ITemplateProvider";
import { Bindable } from "../../Templating/Properties";
import { ContentView, IContentViewConfig } from "./ContentView";
import "Templates/HeadedContentView.html";

export interface IHeadedContentViewConfig<TContent, THeader> extends IContentViewConfig<TContent> {

    header?: Bindable<THeader>;
}

/****************************************/

export class HeadedContentView<TContent, THeader> extends ContentView<TContent> {

    constructor(config?: IHeadedContentViewConfig<TContent, THeader>) {

        super(config);

        this.bindConfig("header", config);
    }

    /****************************************/

    header: THeader = null;

    headerTemplate: CatalogTemplate<THeader> = null;
}

registerType(HeadedContentView, "HeadedContentView");