import { IList } from "../../Core/Abstraction/IList";
import { linq } from "../../Core/Linq";
import { isUpperCase } from "../../Core/Utils/StringUtils";
import { IAsyncLoad } from "../Abstraction/IAsyncLoad";
import { isAsyncLoad } from "./TypeCheck";

export async function loadAllAsync<T>(items: T[] | IList<T> | Iterator<T>) {

    const loaders = linq(items as any).ofType<IAsyncLoad>(a => isAsyncLoad(a)).select(a => a.loadAsync()).toArray();

    return await Promise.all(loaders);
}

/****************************************/

export function formatForCss(name: string): string {
    let s = 0;
    let result = "";
    for (let i = 0; i < name.length; i++) {
        const c = name.charAt(i);
        switch (s) {
            //upper mode or begin 
            case 0:
                result += c.toLowerCase();
                if (!isUpperCase(c) || c == "-")
                    s = 1;
                break;
            //first-mode
            case 1:
                if (isUpperCase(c) && c != "-") {
                    result += "-";
                    s = 0;
                }
                result += c.toLowerCase();
                break;
        }
    }
    return result;
}