import { Format } from "../../Core/Helpers/Format";
import { DynamicString, LocalString } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { IView, IViewFactory, ViewStyle } from "../Abstraction/IView";
import { ActionBar } from "../Components/ActionBar";
import { IconTextView } from "../Components/IconTextView";
import { IListViewConfig, ListView } from "../Components/ListView";
import { SearchView } from "../Components/SearchView";
import { ItemsSource } from "../Items/ItemsSource";
import { PagedItemsLoader } from "../Items/PagedItemsLoader";
import { Activity, IActivityConfig } from "./Activity";

export type ItemSearchMode = "client" | "default";

export interface ISearchActivityConfig<TItem, TItemView extends IView> extends IActivityConfig {

    createItemView?: IViewFactory<TItem, TItemView>;

    searchAsync?: (query: string, offset?: number, pageSize?: number) => Promise<TItem[]>;

    searchMode?: ItemSearchMode;

    tooltip?: LocalString;

    query?: string;

    pageSize?: number;

    listStyles?: ViewStyle[];

    noMatchingText?: LocalString;
}

/****************************************/

export class SearchActivity<TItem, TItemView extends IView, TResult = any> extends Activity<TResult> {

    protected _actionBar: ActionBar;
    protected _listView: ListView<TItem, TItemView>;
    protected _searchView: SearchView;
    protected _emptyView: IconTextView;
    protected _lastItems: TItem[];

    constructor(config?: ISearchActivityConfig<TItem, TItemView>) {
        super(config);

        this.bindConfigString("tooltip", config);

        this.bindConfig("pageSize", config);

        this.bindConfig("noMatchingText", config);

        if (config) {

            if (config.createItemView)
                this.createItemView = config.createItemView;

            if (config.searchAsync)
                this.searchWorkAsync = config.searchAsync;


            if (config.query)
                this.searchText = config.query;

            if (config.listStyles)
                config.listStyles.forEach(a => this.listStyles.push(a));
        }

    }

    /****************************************/

    refreshAsync() {

        return this._listView.refreshAsync();
    }

    /****************************************/

    protected async createAsync() {
        this._actionBar = this.view.addView(new ActionBar({
            onBack: () => this.closeAsync(),
            title: this.prop("title"),
            mainAction: "back"
        }));

        this._searchView = new SearchView({
            name: "search-expanded",
            tooltip: this.prop("tooltip"),
            isExpanded: true,
            searchText: this.prop("searchText"),
            searchAsync: text => this._listView.refreshAsync()
        });

        this._actionBar.actions.add(this._searchView);

        this._emptyView = new IconTextView({
            styles: ["empty-view"],
            visible: false
        });

        this._listView = this.view.addView(new ListView({
            name: "content",
            styles: this.listStyles,
            template: "ListViewInline",
            header: [this._emptyView],
            itemsSource: new ItemsSource({
                getItemsAsync: async () => {
                    const searchText = this.searchText;
                    const items = await this.searchWorkAsync(searchText);
                    if (!searchText)
                        this._lastItems = items;
                    this.updateView(items?.length > 0, searchText);
                    return items;
                }
            }),
            itemsLoader: this.pageSize ? new PagedItemsLoader({
                getFilter: (offset, pageSize) => this.getFilter(this.searchText, offset, pageSize),
                pageSize: this.pageSize
            }) : undefined,
            createItemView: item => this.createItemView(item),
            ...this.configureListView()
        }));



        setTimeout(() =>
            this._searchView.hasFocus = true, 500);

        await this.refreshAsync();
    }

    /****************************************/

    protected updateView(hasItems: boolean, searchText: string) {

        if (!hasItems) {

            this._emptyView.visible = true;

            if (!this.searchText) {
                this._emptyView.icon = "fas fa-microscope";
                this._emptyView.text = "";
            }
            else {

                this._emptyView.icon = "far fa-sad-cry";
                if (this.noMatchingText)
                    this._emptyView.text = Format.text(this.noMatchingText, searchText);
            }
        }
        else
            this._emptyView.visible = false;
    }

    /****************************************/

    protected configureListView() {

        return <IListViewConfig<TItem, TItemView, any>>{};
    }

    /****************************************/

    protected createItemView(item: TItem): TItemView {
        throw "Not Implemented";
    }

    /****************************************/

    protected searchWorkAsync(query: string, offset?: number, pageSize?: number): Promise<TItem[]> {
        throw "Not Implemented";
    }

    /****************************************/

    protected getFilter(query: string, offset?: number, pageSize?: number) {
        return {}
    }

    /****************************************/

    searchText: string = null;

    tooltip: string = null;

    pageSize: number = 0;

    listStyles: ViewStyle[] = ["vertical", "scroll", "compact"];

    emptyView: IconTextView;

    noMatchingText: LocalString = null;
}

registerType(SearchActivity, "SearchActivity");