import { registerType } from "../../Core/Utils/ObjectUtils";
import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { Bindable } from "../../Templating/Properties";
import { ActionView, IActionViewConfig } from "./ActionView";

export interface ISearchViewConfig extends IActionViewConfig {

    searchText?: Bindable<string>;

    isExpanded?: Bindable<boolean>;

    poolInterval?: number;

    searchAsync?: (text: string) => Promise<void>;
}

/****************************************/

export class SearchView extends ActionView {

    protected _lastSearchText: string = null;

    /****************************************/

    constructor(config?: ISearchViewConfig) {
        super({
            name: "search",
            template: "SearchView",
            content: "fas fa-search",
            tooltip: "Ricerca",
            ...config
        });

        this.bindConfig("isExpanded", config);

        this.bindConfig("searchText", config);

        this.prop("hasFocus").subscribe(value => this.onFocusChanged(value));

        if (config) {

            if (config.poolInterval)
                this.poolInterval = config.poolInterval;

            if (config.searchAsync)
                this.doSearchAsync = config.searchAsync;
        }
    }


    /****************************************/

    protected onFocusChanged(value: boolean) {

        if (value) {
            this._lastSearchText = this.searchText;
            this.poolSearchTextAsync();
        }
    }

    /****************************************/

    protected async poolSearchTextAsync(): Promise<void> {

        while (this.hasFocus) {
            if (this.searchText != this._lastSearchText) {
                this._lastSearchText = this.searchText;
                await this.searchAsync(this._lastSearchText);
            }
            await delayAsync(this.poolInterval);
        }
    }

    /****************************************/

    protected executeWorkAsync(): Promise<void> {

        if (!this.isExpanded) {
            this.isExpanded = true;
            this.hasFocus = true;
        }
        return Promise.resolve();
    }

    /****************************************/

    async searchAsync(text: string) {

        this.status = "searching";

        await delayAsync(0);

        try {
            await this.doSearchAsync(text);
        }
        finally {
            this.status = "loaded";
        }
    }

    /****************************************/

    protected doSearchAsync(text: string) {

        return Promise.resolve();
    }

    /****************************************/

    clear() {
        this.searchText = "";
    }

    /****************************************/

    close() {
        this.isExpanded = false;
    }

    /****************************************/

    searchText: string = null;

    isExpanded: boolean = false;

    hasFocus: boolean = false;

    poolInterval: number = 200;
}

registerType(SearchView, "SearchView");