import { OperationType } from "../../Core/Abstraction/IOperation";
import { Services } from "../../Core/Global";
import { linq } from "../../Core/Linq";
import { Signal } from "../../Core/Types/Signal";
import { get, registerType, set } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";
import { ICreateMapItemOptions, IMapItem, IMapManager } from "../Abstraction/IMapManager";
import { ISingleItemSelector } from "../Abstraction/ISingleItemSelector";
import { ItemsSource } from "../Items/ItemsSource";
import { IItemsViewConfig, ItemsView } from "./ItemsView";

export interface IMapItemsViewConfig<TItem extends object, TFilter> extends IItemsViewConfig<TItem> {

    manager?: IMapManager;

    onItemSelected?: (item: TItem) => void;

    createMapItem?: (item: TItem) => ICreateMapItemOptions;

    itemsSource?: ItemsSource<TItem, TItem, TFilter>

    showCurrentPosition?: Bindable<boolean>;
}

/********************************************/

type ItemsMapViewStatus = "" | "loading";

/********************************************/

export class MapItemsView<TItem extends object, TFilter = any> extends ItemsView<TItem> implements IHTMLAttach, ISingleItemSelector<TItem> {

    private _manager: IMapManager;

    /********************************************/

    constructor(config?: IMapItemsViewConfig<TItem, TFilter>) {
        super({ template: "Attach", ...config });

        this.bindConfig("itemsSource", config);

        this.bindConfig("showCurrentPosition", config);

        if (config) {

            if (config.manager)
                this._manager = config.manager;

            if (config.createMapItem)
                this.createMapItem = config.createMapItem;
        }

        if (!this._manager)
            this._manager = Services.mapManager();

        this.prop("selectedItem").subscribe(value => {
            if (!value)
                this._manager.selectedItem = null;
            else
                this._manager.selectedItem = value["@mapItem"];

            if (config && config.onItemSelected)
                config.onItemSelected(value as TItem);
        });

        this._manager.onSelectionChanged = () => {
            if (this._manager.selectedItem)
                this.selectedItem = this._manager.selectedItem["@item"];
            else
                this.selectedItem = null;
        };


        this.prop("showCurrentPosition").subscribe(value => {
            this.manager.showCurrentPositionAsync(value);
        })
    }

    /****************************************/

    async loadItemsAsync(filter?: TFilter): Promise<void> {

        if (!this.itemsSource)
            return;

        this.beginUpdate();

        this.content.clear();

        this.status = "loading";
        let operation = Services.operation.begin({ message: "Loading items", type: OperationType.Local });
        try {

            let items = await this.itemsSource.getItemsAsync(filter);

            if (items)
                items.forEach(a => this.content.add(a));

            if (this.mapReady.isSet)
                this.initItems();
        }
        finally {
            operation.end();
            this.status = "";
            this.endUpdate();
        }
    }

    /********************************************/

    attach(element: HTMLElement): void {

        Services.operation.progress("Map attacched");

        if (this._manager)
            this._manager.attach(element);

        this.mapReady.set();

        if (this.status != "loading")
            this.initItems();

        this._manager.showCurrentPositionAsync(this.showCurrentPosition);
    }

    /********************************************/

    protected initItems() {

        let mapOptions = linq(this.content).select(a => this.createMapItem(a)).toArray();
        let mapItems = this._manager.addItems(mapOptions);

        for (let i = 0; i < mapItems.length; i++) {
            set(this.content.get(i), "@mapItem", mapItems[i]);
            set(mapItems[i], "@item", this.content.get(i));
        }
    }

    /********************************************/

    protected createMapItem(item: TItem): ICreateMapItemOptions {
        return undefined;
    }

    /********************************************/

    protected onItemAdded(item: TItem) {

        if (!this.mapReady.isSet)
            return;

        let options = this.createMapItem(item);

        if (!options.location || isNaN(options.location.latitude) || isNaN(options.location.longitude)) {
            console.warn("Invalid location for " + options.title);
            return;
        }

        let mapItem = this._manager.addItems([options]);

        set(item, "@mapItem", mapItem);
        set(mapItem, "@item", item);
    }

    /********************************************/

    protected onItemRemoved(item: TItem) {

        if (!this.mapReady.isSet)
            return;
        let mapItem = get<IMapItem>(item, "@mapItem");
        if (mapItem)
            this._manager.removeItem(mapItem);
    }

    /********************************************/

    itemsSource: ItemsSource<TItem, TItem, TFilter> = null;

    status: ItemsMapViewStatus = "";

    selectedItem: TItem = null;

    showCurrentPosition: boolean = false;

    mapReady = new Signal();

    get manager() { return this._manager; }
}

registerType(MapItemsView, "MapItemsView");