import { Bindable } from "../../Templating/Properties";
import { ContentView, IContentViewConfig } from "./ContentView";
import { Animation } from "../Helpers/Animation";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface ICounterViewConfig extends IContentViewConfig<number> {

    value?: Bindable<number>;

    title: Bindable<string>;
}

/********************************************/

export class CounterView extends ContentView<number> {

    constructor(config?: ICounterViewConfig) {
        super({ template: "CounterView", ...config });

        this.bindConfig("value", config);
        this.bindConfig("title", config);

        this.prop("value").subscribe(() => this.animate());

        if (this.value != null)
            this.animate();
    }

    /********************************************/

    protected animate() {

        this.content = 0;

        let duration = this.value / 200;

        return Animation.animate({
            timeFunction: Animation.linear(),// Animation.cubic(0.000, 0.610, 0.610, 0.795),
            duration: isNaN(duration) || duration < 1 ? 1 : duration,
            stepTime: 1 / 20,
            step: t =>
                this.content = Math.round(this.value * t)
        });
    }

    /********************************************/

    title: string = null;

    value: number = 0;
}

registerType(CounterView, "CounterView");