import { IAsyncLoad } from "../Abstraction/IAsyncLoad";
import { ContentView, IContentViewConfig } from "./ContentView";

import "Templates/ImageView.html";
import { loadImageAsync } from "../Helpers/ImageLoade";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface IImageViewConfig extends IContentViewConfig<string> {

}

/********************************************/

export class ImageView extends ContentView<string> implements IAsyncLoad {

    constructor(config?: IImageViewConfig) {

        super({ template: "Image", ...config });
    }


    /********************************************/

    loadAsync() {

        if (this.content)
            return loadImageAsync(this.content);
        return Promise.resolve();
    }
}

registerType(ImageView, "ImageView");