import { formatDate, parseDate } from "../../Core/Utils/DateUtils";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { BaseTextEditor, IBaseTextEditorConfig } from "./BaseTextEditor";

export interface IDateEditorConfig extends IBaseTextEditorConfig<Date> {

    placeholder?: Bindable<string>;
}

/********************************************/

export class DateEditor extends BaseTextEditor<Date> {

    constructor(config?: IDateEditorConfig) {

        super({ template: "DateEditor", ...config });

        this.bindConfig("placeholder", config);

        this.createEditValueProp();
    }


    /********************************************/

    protected valueToEdit(value: Date): string {

        return value === null || value === undefined ? "" : formatDate(value, "{YYYY}-{MM}-{DD}");
    }

    /********************************************/

    protected editToValue(value: string): Date {

        if (value === undefined || value === null)
            return null;
        return parseDate(value + " 00:00");
    }


    /********************************************/

    placeholder: string = null;
}

registerType(DateEditor, "DateEditor");