import { isString } from "../../Core/Helpers/TypeCheck";
import { Type } from "../../Core/Types/Types";
import { getType, getTypeName, registerType } from "../../Core/Utils/ObjectUtils";
import { IPage } from "../Abstraction/IPage";
import { IMultiPageHost, IPageLoadOptions } from "../Abstraction/IPageHost";
import { IView } from "../Abstraction/IView";
import { IContentViewConfig } from "./ContentView";
import { View } from "./View";

interface ISelfHostedPageHost extends IContentViewConfig<IView> {

}

/****************************************/

export class SelfHostedPageHost extends View implements IMultiPageHost {

    private _parentHost: IMultiPageHost;

    /****************************************/

    constructor(config?: ISelfHostedPageHost) {

        super({ template: "SinglePageHost", ...config });

        this._parentHost = window.parent["WebApp"]["app"]["pageHost"];
    }

    /****************************************/

    get(index: number) {
        return this._parentHost.get(index);
    }

    /****************************************/

    clearAsync() {
        this.current = null;
        return Promise.resolve();
    }

    /****************************************/

    goBackAsync() {
        return this._parentHost.goBackAsync();
    }

    /****************************************/

    goForwardAsync() {
        return this._parentHost.goForwardAsync();
    }

    /****************************************/

    bringFrontAsync(page: IPage) {

        if (page == this.current)
            return Promise.resolve();
        return this._parentHost.bringFrontAsync(page);
    }

    /****************************************/

    loadAsync<TPage extends IPage>(page: TPage, options?: IPageLoadOptions): Promise<TPage> {

        if (!this.current) {
            page.host = this;
            this.current = page;
            page.loadAsync();
            page.status = "active";
            return Promise.resolve(page);
        }
        return this._parentHost.loadAsync(page, options);
    }

    /****************************************/

    find<TPage extends IPage>(nameOrType: string | Type<TPage>): TPage {

        if (this.current) {
            if (isString(nameOrType) && nameOrType == getTypeName(this.current))
                return <TPage>this.current;

            if (getType(this.current) == nameOrType)
                return <TPage>this.current;
        }
        return this._parentHost.find(nameOrType as Type<TPage>);
    }


    /****************************************/

    async goToAsync(index: number) {

        this._parentHost.currentIndex = index;
        return Promise.resolve();
    }

    /****************************************/

    get canGoBack(): boolean {
        return this._parentHost.canGoBack;
    }

    /****************************************/

    get currentIndex(): number {
        return this._parentHost.currentIndex;
    }

    set currentIndex(value: number) {
        this._parentHost.currentIndex = value;
    }

    /****************************************/

    get pageCount(): number {
        return this._parentHost.pageCount;
    }

    /****************************************/

    current: IPage;
}



registerType(SelfHostedPageHost, "SelfHostedPageHost");