import { OperationType } from "../../Core/Abstraction/IOperation";
import { Services } from "../../Core/Global";

 export function loadVideoAsync(src: string): Promise < HTMLVideoElement > {

    const op = Services.operation.begin({ message: "Loading image: " + src, type: OperationType.Local });

    return new Promise((res, rej) => {

        const video = document.createElement("video");

        video.addEventListener("playing", () => {
            video.pause();
            op.end()
            res(video);
        });

        video.addEventListener("error", ev => {
            op.end();
            rej(ev.error)
        });

        video.src = src;
        video.play();
        if (video.readyState >= 2)
            res(video);
    });
}

export function loadVideoIntoAsync(src: string, video: HTMLVideoElement) {

    if (!video.paused || video.played)
        return Promise.resolve();

    const op = Services.operation.begin({ message: "Loading image: " + src, type: OperationType.Local });

    return new Promise((res, rej) => {

        function removeListeners() {
            video.removeEventListener("playing", loadListener);
            video.removeEventListener("error", errorListener);
        }

        const loadListener = () => {
            op.end();
            removeListeners();
            res(undefined);
        };

        const errorListener = ev => {
            op.end();
            removeListeners();
            rej(ev.error);
        };

        video.addEventListener("playing", loadListener);
        video.addEventListener("error", errorListener);
        video.src = src;
        video.play();
    });
}