import { getBaseType, getType } from "../Core/Utils/ObjectUtils";
import { CatalogTemplate } from "./Abstraction/ITemplateProvider";
import { Properties } from "./Properties";
import { TemplateBuilder } from "./TemplateBuilder";

export function model<T extends Object>(obj: T): T {
    Properties.createAll(obj);
    return obj;
}

/****************************************/

export function template<TModel>(root: HTMLElement, template: CatalogTemplate<TModel>, model: TModel) {

    root.innerHTML = "";

    const builder = new TemplateBuilder(model, root);

    builder.begin();

    builder.loadTemplate(template)(builder);

    builder.end();
}

/****************************************/

export function setEnumerable<T>(obj: T, propName: keyof T) {
    let curType = getType(obj); 
    while (true) {
        const desc = Object.getOwnPropertyDescriptor(curType.prototype, propName);
        if (desc) {
            if (!desc.enumerable) {
                desc.enumerable = true;
                Object.defineProperty(obj, propName, desc);
            }
            return;
        } 
        curType = getBaseType(curType);
    }
}