import { TimeSpan } from "../../Core/Types/TimeSpan";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { Bindable } from "../../Templating/Properties";
import { BaseTextEditor, IBaseTextEditorConfig } from "./BaseTextEditor";

export interface ITimeEditorConfig extends IBaseTextEditorConfig<TimeSpan> {

    placeholder?: Bindable<string>;
}

/********************************************/

export class TimeEditor extends BaseTextEditor<TimeSpan> {

    constructor(config?: ITimeEditorConfig) {

        super({ template: "TimeEditor", ...config });

        this.bindConfig("placeholder", config);

        this.createEditValueProp();
    }

    /********************************************/

    protected valueToEdit(value: TimeSpan): string {

        return value === null || value === undefined ? "" : value.format("{hh}:{mm}");
    }

    /********************************************/

    protected editToValue(value: string): TimeSpan {

        if (value === undefined || value === null)
            return null;
        var parts = value.split(':');
        if (parts.length != 2)
            return null;
        return TimeSpan.fromMinutes(parseInt(parts[0]) * 60 + parseInt(parts[1]));
    }

    /********************************************/

    placeholder: string = null;
}

registerType(TimeEditor, "TimeEditor");