import { LocalString, StringUsage } from "../../Core/Types/DynamicString";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { ActionPriority, IAction } from "../Abstraction/IAction";
import { ViewStyle } from "../Abstraction/IView";
import { ActionView } from "./ActionView";
import { BasePopUpMessage, IBasePopUpMessageConfig } from "./BasePopUpMessage";

export interface IMessageBoxConfig extends IBasePopUpMessageConfig {

    actions?: IAction[];

    message?: Bindable<LocalString>;

    title?: Bindable<LocalString>;

    icon: Bindable<string>;
}

/****************************************/

export class MessageBox extends BasePopUpMessage {


    constructor(config?: IMessageBoxConfig) {

        super({ template: "MessageBox", className: "message-box", ...config });

        this.bindConfigString("title", config, StringUsage.Title);

        this.bindConfigString("message", config, StringUsage.Message);

        this.bindConfig("icon", config);

        if (config) {
            if (config.actions)
                config.actions.forEach(a => this.addAction(a));
        }
    }

    /****************************************/

    addAction(action: IAction) {

        const styles: ViewStyle[] = action.styles ?? [];

        if (action.priority == ActionPriority.Evidence)
            styles.push("primary");

        this.actions.add(ActionView.fromAction(action,
            {
                template: "ActionButton",
                styles: styles,
                executeAsync: async () => {
                    await action.executeAsync();
                    this.hide(action.name);
                }
            }));
    }

    /****************************************/

    readonly actions: IObservableList<ActionView> = observableListOf();

    message: string = null;

    title: string = null;

    icon: string = null;
}

registerType(MessageBox, "MessageBox");