import { linq } from "../../Core/Linq";
import { LocalString } from "../../Core/Types/DynamicString";
import { IObservableList } from "../../Templating/Abstraction/IObservableList";
import { observableListOf } from "../../Templating/ObservableList";
import { Bindable } from "../../Templating/Properties";
import { IEditor } from "../Abstraction/IEditor";
import { NavBar } from "../Components/NavBar";
import { IViewComponentConfig, ViewComponent } from "../Components/ViewComponent";
import { BaseEditor, IBaseEditorConfig } from "./BaseEditor";
import "Templates/SectionEditor.html";
import { registerType } from "../../Core/Utils/ObjectUtils";

export interface ISectionEditorViewConfig<TValue> extends IViewComponentConfig {

    editor?: IEditor<TValue, any>;

    title?: Bindable<LocalString>;
}

/********************************************/

export interface ISectionEditorConfig<TValue> extends IBaseEditorConfig<TValue> {

    sections?: ISectionEditorViewConfig<TValue>[];

}

/********************************************/

class SectionEditorView<TValue> extends ViewComponent {

    constructor(config?: ISectionEditorViewConfig<TValue>) {

        super(config);

        this.bindConfig("editor", config);

        this.bindConfigString("title", config);
    }

    /********************************************/

    title: string = null;

    editor?: IEditor<TValue, any>;
}

/********************************************/

export class SectionEditor<TValue, TEdit = TValue> extends BaseEditor<TValue, TEdit> {

    constructor(config?: ISectionEditorConfig<TValue>) {

        super({ template: "SectionEditor", ...config });

        this.navBar = new NavBar<SectionEditorView<TValue>>({
            styles: ["tab-view", "surface"],
            itemTemplate: "TextView",
            itemBehavoirs: ["ripple"],
            selectedItem: this.prop("activeSectionName")
        });

        if (config) {
            if (config.sections)
                config.sections.forEach(a => this.addSection(a));
        }

        if (this.sections.count > 0)
            this.activeSectionName = this.sections.get(0).name;

        this.createEditValueProp();
    }

    /********************************************/

    protected async loadAsyncWork() {

        for (let section of this.sections) {
            if (section.editor)
                await section.editor.loadAsync();
        }
    }

    /********************************************/

    protected async beginEditWorkAsync(value: TValue) {

        for (let section of this.sections) {
            if (section.editor)
                await section.editor.beginEditAsync(value);
        }
    }

    /********************************************/

    protected async validateAsyncWork(force?) {

        let isValid = true;

        for (let section of this.sections) {
            if (section.editor)
                if (!await section.editor.validateAsync(force)) {
                    isValid = false;
                    this.activeSectionName = section.name;
                }
        }

        return isValid;
    }

    /********************************************/

    protected async commitAsyncWork(force?) {

        let isValid = true;

        for (let section of this.sections) {
            if (section.editor)
                if (!await section.editor.commitAsync(force))
                    isValid = false;
        }

        return isValid;
    }


    /********************************************/

    getSection(name: string) {

        return linq(this.sections).first(a => a.name == name);
    }

    /********************************************/

    addSection(config: ISectionEditorViewConfig<TValue>) {
        const item = new SectionEditorView(config);

        this.sections.add(item);

        this.navBar.addItem({
            text: item.prop("title"),
            name: item.name,
            content: item
        });

        return item;
    }

    /********************************************/

    readonly sections: IObservableList<SectionEditorView<TValue>> = observableListOf();

    readonly navBar: NavBar<SectionEditorView<TValue>>;

    activeSectionName: string = null;
}


registerType(SectionEditorView, "SectionEditorView");

registerType(SectionEditor, "SectionEditor");